import type { v4 } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getActionsPathForOP } from '../../application';
import { addPatternForBindingChangeOfEnumOPV4 } from '../../../common/decoration/control';
import type { CustomActionPositionOP } from './ObjectPageToolBarAction';

export class CustomFormActionOP implements v4.CustomFormActionOP {
    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    text: string;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    position: CustomActionPositionOP;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    press: string;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        },
        generate: addPatternForBindingChangeOfEnumOPV4
    })
    @validity({
        since: '1.84.0'
    })
    visible?: boolean;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        },
        generate: addPatternForBindingChangeOfEnumOPV4
    })
    @validity({
        since: '1.84.0'
    })
    enabled?: boolean;
}
