export interface MacrosXMLPathInfo {
    name: string;
    index: number;
}
export type MacrosXMLPath = Array<MacrosXMLPathInfo>;

export enum MacrosPropertyType {
    Control = 'Control',
    Property = 'Property',
    Aggregation = 'Aggregation',
    Event = 'Event'
}

export interface SelectionPositionRange {
    startOffset: number;
    endOffset: number;
    startLine: number;
    endLine: number;
    startColumn: number;
    endColumn: number;
}

export interface MacrosSchemaMetadata {
    // Absolute path in XML to target element
    path?: MacrosXMLPath;
    // Control full name
    controlName?: string;
    // Type of property - SAPUI5 control and it's subtypes.
    type?: MacrosPropertyType;
    // Position in file.
    position?: SelectionPositionRange;
    // File path
    filePath?: string;
    // Macros id
    internalId?: string;
    // Default aggregation
    defaultAggregation?: string;
    // Namespaces
    namespaces?: string[];
}

export interface SchemaDefinitionKey {
    name: SchemaKeyName;
    value: string;
}

export interface SchemaDefinition extends Definition {
    isViewNode?: boolean;
    propertyIndex?: number;
    metadata?: MacrosSchemaMetadata;
    hidden?: boolean;
    annotationPath?: string;
    keys?: SchemaDefinitionKey[];
}
