import { parseAndMergeAndConvert } from '../../common/utils';
import { generateObjectPageSchemaV2 } from './objectPage';
import { generateListReportSchemaV2 } from './listReport';
import { generateAnalyticalListReportSchemaV2 } from './analyticalListReport';
import { generateOverviewPageSchemaV2 } from './overviewPage';
import type { ExtensionLogger, GenerateAppSchemaParameters, FileData, Manifest } from '@sap/ux-specification-types';
import { LogSeverity, FioriElementsVersion, TemplateType } from '@sap/ux-specification-types';
import { getEntitySetByEntitySetName } from '../../common/importProject';
import i18next from 'i18next';
import { log } from '../../../extensionLogger';
import { initI18n } from '../../../i18n/i18n';

/**
 * Common logic for LR and OP to prepare the generation
 * @param {FileData[]} annotations - list of all annotation files (file content plus URI as identifier)
 * @param {ExtensionLogger} logger - Logger class for logging messages
 * @param {object} manifest - manifest.json of the app
 * @param {FileData[]} [fragments] - Array with XML fragments.
 * @param {string} entitySet - the base entity set name of the given page
 *
 * @returns the filled intrface structure with all information for the schema generation
 */
function prepareGenerationV2(
    annotations: FileData[],
    logger: ExtensionLogger,
    manifest: Manifest,
    fragments: FileData[],
    entitySet: string,
    templateType: TemplateType
): GenerateAppSchemaParameters {
    initI18n();

    const serviceAVT = parseAndMergeAndConvert(annotations, logger);
    const generateParameters: GenerateAppSchemaParameters = {
        templateType,
        fioriElementsVersion: FioriElementsVersion.v2,
        manifest,
        fragments,
        serviceAVT,
        logger
    };
    generateParameters.entitySet = getEntitySetByEntitySetName(entitySet, generateParameters.serviceAVT);
    if (!generateParameters.entitySet) {
        log(generateParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('ENTITYSETNOTFOUND', { entitySetName: entitySet })
        });
    }
    return generateParameters;
}

/**
 * Generates an app specific schema out of the generic schema.
 * Generic types are replaced by information from the app specific annotations.
 *
 * @param {object} genericSchema - generic JSON schema of an object page
 * @param {string} entitySet - the base entity set name of the given page
 * @param {FileData[]} annotations - list of all annotation files (file content plus URI as identifier)
 * @param {object} manifest - manifest.json of the app
 * @param {FileData[]} [fragments] - Array with XML fragments.
 * @param {ExtensionLogger} logger - Logger class for logging messages
 *
 * @returns the app specific JSON schema
 */
export function generateObjectPageInterfaceV2(
    genericSchema: object,
    entitySet: string,
    annotations: FileData[],
    manifest: Manifest,
    fragments?: FileData[],
    logger?: ExtensionLogger
): object {
    const generateParameters = prepareGenerationV2(
        annotations,
        logger,
        manifest,
        fragments,
        entitySet,
        TemplateType.ListReportObjectPageV2
    );
    return generateObjectPageSchemaV2(generateParameters, genericSchema);
}

