import type { ConvertedMetadata, EntitySet, EntityType } from '@sap-ux/vocabularies-types';
import type { DataFieldAbstractTypes, DataFieldForAnnotation } from '@sap-ux/vocabularies-types/vocabularies/UI';
import { UIAnnotationTerms, UIAnnotationTypes } from '@sap-ux/vocabularies-types/vocabularies/UI';
import { getStableIdPartFromDataField } from '../../v4/utils/StableIdHelper';
import type { FacetConfig, GenerateAppSchemaParameters, SchemaDefinition } from '@sap/ux-specification-types';
import {
    DefinitionName,
    FacetBase,
    FioriElementsVersion,
    PropertyName,
    SchemaKeyName,
    SchemaTag,
    SectionType,
    UIVOCABULARY,
    v2
} from '@sap/ux-specification-types';
import {
    DEFINITION_LINK_PREFIX,
    getDataFieldDescription,
    createAnnotationPath,
    findAlias,
    prepareRef,
    addDataTypeToDefinition,
    replaceNamespaces
} from '..';
import type { Definition } from 'typescript-json-schema';
import { addStandardFooterActionsOP, addStandardHeaderActions, isDraftEnabled } from '../../v2/generate/utils';

type DefinitionsProperties = { [key: string]: SchemaDefinition };

interface FieldsContainerSchema {
    fields?: DefinitionsProperties;
    actions?: DefinitionsProperties;
}

/**
 * Copies the field properties from the generic definition to the specific field definition
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @param {Definition} appSchema - App schema in general
 * @param {DataFieldAbstractTypes} field - field definition in parser output (AVT)
 * @param {string} fieldDefinition - the unique ID for the given field definition
 */
function copyFieldProperties(
    version: FioriElementsVersion,
    appSchema: Definition,
    field: DataFieldAbstractTypes,
    fieldDefinition: string
) {
    if (version === FioriElementsVersion.v4 && appSchema['definitions']['Field']) {
        switch (field.$Type) {
            case UIAnnotationTypes.DataField:
            case UIAnnotationTypes.DataFieldForAnnotation:
            case UIAnnotationTypes.DataFieldWithNavigationPath:
            case UIAnnotationTypes.DataFieldWithUrl: {
                (appSchema.definitions[fieldDefinition] as Definition).properties =
                    appSchema.definitions.Field['properties'];
                break;
            }
        }
    } else if (version === FioriElementsVersion.v2 && appSchema['definitions']['Field']) {
        switch (field.$Type) {
            case UIAnnotationTypes.DataField:
            case UIAnnotationTypes.DataFieldForAnnotation:
            case UIAnnotationTypes.DataFieldWithUrl: {
                (appSchema.definitions[fieldDefinition] as Definition).properties =
                    appSchema.definitions.Field['properties'];
                break;
            }
        }
    }
}

/**
 * Determines the name for a field definition in app schema
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @param fieldReferenceId - the stable ID representation of the field
 * @param {string} targetID - unique ID, identifies the parent object (form)
 * @param fieldReference - the reference to the field definition in schema
 * @returns the field definition name in schema
 */
function determineFieldDefinitionName(
    version: FioriElementsVersion,
    fieldReferenceId: string,
    targetID: string,
    fieldReference: string
): string {
    const fieldReferenceType =
        version === FioriElementsVersion.v2 && fieldReferenceId && fieldReferenceId.includes('::')
            ? fieldReferenceId.split('::')[0].replace('DataField', 'Field')
            : undefined;
    if (version === FioriElementsVersion.v2) {
        return `${fieldReferenceType}<${targetID}::${fieldReference}>`;
    } else {
        return `${targetID}::${fieldReference}`;
    }
}

/**
 * Add the keys and target tag for Datafield
 * @param field - the given field definition
 * @param appSchema - the app schema to be updated
 * @param schemaKeyOfField - key for the given field in the app schema
 */
function addKeysTagForDataField(field, appSchema: Definition, schemaKeyOfField: string) {
    if (typeof field.Value !== 'string' && field.Value.type === 'Path') {
        appSchema.definitions[schemaKeyOfField][SchemaTag.keys] = [
            {
                name: 'Value',
                value: field.Value.path
            }
        ];
        appSchema.definitions[schemaKeyOfField][SchemaTag.target] = field.Value.$target
            ? field.Value.$target.fullyQualifiedName.split('/')[0]
            : field.Value.path;
    } else if (typeof field.Value === 'string' && field.Value) {
        appSchema.definitions[schemaKeyOfField][SchemaTag.keys] = [
            {
                name: 'Value',
                value: field.Value
            }
        ];
        appSchema.definitions[schemaKeyOfField][SchemaTag.target] = field.fullyQualifiedName.split('@')[0];
    }
}

/**
 * Adds the "keys" tag to a field definition in schema
 * @param field - the given field definition
 * @param appSchema - the app schema to be updated
 * @param schemaKeyOfField - key for the given field in the app schema
 */
function addKeysTagToField(field: DataFieldAbstractTypes, appSchema: Definition, schemaKeyOfField: string) {
    if (
        field.$Type === UIAnnotationTypes.DataField ||
        field.$Type === UIAnnotationTypes.DataFieldWithUrl ||
        field.$Type === UIAnnotationTypes.DataFieldWithNavigationPath
    ) {
        addKeysTagForDataField(field, appSchema, schemaKeyOfField);
    } else if (field.$Type === UIAnnotationTypes.DataFieldForAnnotation) {
        appSchema.definitions[schemaKeyOfField][SchemaTag.keys] = [
            {
                name: 'Target',
                value: replaceNamespaces(field.Target.value)
            }
        ];
        appSchema.definitions[schemaKeyOfField][SchemaTag.target] =
            field.Target.$target.fullyQualifiedName.split('@')[0];
    } else if (field.$Type === UIAnnotationTypes.DataFieldForAction) {
        const separator = field.Action.lastIndexOf('.');
        if (separator === -1) {
            appSchema.definitions[schemaKeyOfField][SchemaTag.keys] = [
                {
                    name: 'Action',
                    value: field.Action
                }
            ];
        } else {
            appSchema.definitions[schemaKeyOfField][SchemaTag.keys] = [
                {
                    name: 'Action',
                    value: field.Action.substring(separator + 1, field.Action.length)
                }
            ];
            appSchema.definitions[schemaKeyOfField][SchemaTag.target] = field.Action.substring(0, separator);
        }
    } else if (field.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation) {
        appSchema.definitions[schemaKeyOfField][SchemaTag.keys] = [
            {
                name: SchemaKeyName.semanticObject,
                value: field.SemanticObject
            }
        ];
        if (field.Action) {
            appSchema.definitions[schemaKeyOfField][SchemaTag.keys].push({
                name: 'Action',
                value: field.Action
            });
        }
        appSchema.definitions[schemaKeyOfField][SchemaTag.target] = field.fullyQualifiedName.split('@')[0];
    }
}

/**
 * Method checks if passed field is connected field.
 * @param field Field to check.
 * @returns True if passed field's target is ConnectedFieldsType.
 */
function isConnectedField(field: DataFieldAbstractTypes): field is DataFieldForAnnotation {
    return (
        field.$Type === UIAnnotationTypes.DataFieldForAnnotation &&
        field.Target?.$target?.$Type === UIAnnotationTypes.ConnectedFieldsType
    );
}

/**
 * Method stores connected fields into schema definitions.
 * @param {SectionType} sectionType - prefix to distinguish Section and HeaderSection
 * @param {Definition} appSchema - App schema in general
 * @param {DataFieldAbstractTypes} field - field definition in parser output (AVT)
 * @param {FacetConfig} section - current section of the object page
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @returns Resolved connected fields with reference to stored schema definitions.
 */
function addConnectedFields(
    sectionType: SectionType,
    appSchema: Definition,
    field: DataFieldForAnnotation,
    section: FacetConfig,
    version: FioriElementsVersion
): DefinitionsProperties {
    const target = field.Target.$target;
    const properties: DefinitionsProperties = {};
    if (target.$Type === UIAnnotationTypes.ConnectedFieldsType) {
        const template = target.Template || '';
        const templateParts = template.match(/(?<=\{)[^}{]*(?=\})/g) || [];
        const targetData = target.Data;
        // Maintain fallback index which should be used for fields which are not listed in 'Template'
        let fallbackIndex = templateParts.length;
        for (const innerKey in targetData) {
            const innerField = targetData[innerKey];
            if (typeof innerField === 'object' && '$Type' in innerField) {
                let index = templateParts.findIndex((templateKey) => templateKey === innerKey);
                if (index === -1) {
                    index = fallbackIndex;
                    fallbackIndex++;
                }
                // eslint-disable-next-line @typescript-eslint/no-use-before-define
                addField(
                    sectionType,
                    appSchema,
                    innerField,
                    `ConnectedFields::${target.qualifier}`,
                    index.toString(),
                    section,
                    version,
                    undefined,
                    {
                        actions: properties,
                        fields: properties
                    },
                    target.fullyQualifiedName.split('@')[0]
                );
            }
        }
    }
    return properties;
}

/**
 * Method returns schema container properties where to store fields.
 * @param {Definition} appSchema - App schema in general
 * @param {string} ref - Container reference id.
 * @param {DefinitionsProperties} [properties] - Preferable container properties to use.
 * @returns Schema container properties where to store fields.
 */
function getFieldsSchemaContainer(
    appSchema: Definition,
    ref: string,
    properties?: DefinitionsProperties
): DefinitionsProperties {
    if (properties) {
        return properties;
    }
    const fieldsDefinition = prepareRef(ref);
    return appSchema.definitions[fieldsDefinition]['properties'];
}

/**
 * Adds a field definition to the app specific schema
 * @param {SectionType} sectionType - prefix to distinguish Section and HeaderSection
 * @param {Definition} appSchema - App schema in general
 * @param {DataFieldAbstractTypes} field - field definition in parser output (AVT)
 * @param {string} targetID - unique ID, identifies the parent object (form)
 * @param {string} key - current field key
 * @param {FacetConfig} section - current section of the object page
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @param {boolean} [withActions=true] - Fields with actions
 * @param {FieldsContainerSchema} containersSchema - Schema definitions to store fields and actions.
 * @param {string} [entityTypeName=section.entityType.fullyQualifiedNam] - Entity type name for annotation path resoluton.
 */
function addField(
    sectionType: SectionType,
    appSchema: Definition,
    field: DataFieldAbstractTypes,
    targetID: string,
    key: string,
    section: FacetConfig,
    version: FioriElementsVersion,
    withActions = true,
    containersSchema: FieldsContainerSchema = {},
    entityTypeName = section.entityType.fullyQualifiedName
): void {
    // Calculate field reference
    const fieldReferenceId = getStableIdPartFromDataField(field);
    const fieldReference =
        version === FioriElementsVersion.v2 && fieldReferenceId && fieldReferenceId.includes('::')
            ? fieldReferenceId.split('::').splice(1).join('::')
            : fieldReferenceId;
    // Determine field definition name
    const schemaKeyOfField = determineFieldDefinitionName(version, fieldReferenceId, targetID, fieldReference);
    // Field properties
    let properties: DefinitionsProperties = {};
    // Evaluate field type, set field reference of properties of fields' or actions' definition
    if (!field || !field.$Type) {
        return;
    } else if (
        field.$Type === UIAnnotationTypes.DataFieldForAction ||
        field.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation
    ) {
        if (withActions && sectionType === SectionType.Section) {
            const actionsProperties = getFieldsSchemaContainer(
                appSchema,
                `${sectionType}${DefinitionName.Actions}<${targetID}>`,
                containersSchema.actions
            );
            const fieldInSchema = (actionsProperties[fieldReference] = {
                $ref: `${DEFINITION_LINK_PREFIX}${schemaKeyOfField}`
            });
            fieldInSchema[SchemaTag.propertyIndex] = parseInt(key);
        }
    } else {
        const fieldsProperties = getFieldsSchemaContainer(
            appSchema,
            `${sectionType}${DefinitionName.Fields}<${targetID}>`,
            containersSchema.fields
        );
        fieldsProperties[fieldReference] = {
            $ref: `${DEFINITION_LINK_PREFIX}${schemaKeyOfField}`,
            propertyIndex: parseInt(key)
        };
        if (isConnectedField(field)) {
            // Populate connected fields
            properties = addConnectedFields(sectionType, appSchema, field, section, version);
        }
    }
    // Now set up the field definition
    appSchema.definitions[schemaKeyOfField] = {
        type: 'object',
        properties,
        description: getDataFieldDescription(field, section.entityType),
        additionalProperties: false
    };
    //Add tags
    addKeysTagToField(field, appSchema, schemaKeyOfField);
    appSchema.definitions[schemaKeyOfField][SchemaTag.annotationPath] = `/${entityTypeName}/${
        field.fullyQualifiedName.split(entityTypeName)[1]
    }`;
    appSchema.definitions[schemaKeyOfField][SchemaTag.annotationType] = field.$Type;
    appSchema.definitions[schemaKeyOfField][SchemaTag.isViewNode] = true;

    addDataTypeToDefinition(appSchema.definitions[schemaKeyOfField] as Definition, field);

    // Copy the generic definition of field properties to the specific one
    copyFieldProperties(version, appSchema, field, schemaKeyOfField);
    if (field.$Type === UIAnnotationTypes.DataFieldForAction && appSchema.definitions?.['FormAction']) {
        (appSchema.definitions[schemaKeyOfField] as Definition).$ref =
            `${DEFINITION_LINK_PREFIX}${DefinitionName.FormAction}`;
        delete (appSchema.definitions[schemaKeyOfField] as Definition).properties;
    }
}

/**
 * The function determines the target ID that shall be used in the app schema for a form
 * @param section - object describing the section or facet
 * @returns the target ID (string)
 */
function determineTargetIdOfForm(section: FacetConfig) {
    let targetID: string;
    if (section.target && section.target['qualifier'] && section.entityType) {
        targetID = `${section.entityType.name}::${section.base}::${section.target['qualifier']}`;
    } else if (section.entityType) {
        targetID = `${section.entityType.name}::${section.base}`;
    } else {
        targetID = section.ID;
    }
    return targetID;
}

/**
 * Adds the specific definitions of the form fields to a schema
 * @param {FacetConfig} section - section definition, as prepared from the annotations
 * @param {SectionType} sectionType - prefix to distinguish Section and HeaderSection
 * @param {object} appSchema - app specific schema that gets enhanced
 * @param {string } targetID - target id of form
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @param {boolean} withActions - indicates that actions shall be considered
 */
function addFieldsToFormDefinition(
    section: FacetConfig,
    sectionType: SectionType,
    appSchema: Definition,
    targetID: string,
    version: FioriElementsVersion,
    withActions: boolean
) {
    if (section.base === FacetBase.Identification) {
        for (const key in section.target) {
            const field = section.target[key];
            if (field) {
                addField(sectionType, appSchema, field, targetID, key, section, version, withActions);
            }
        }
    } else {
        for (const key in section.target['Data']) {
            const field = section.target['Data'][key];
            addField(sectionType, appSchema, field, targetID, key, section, version);
        }
    }
}

/**
 * Adds the schema tags for a form definition
 * @param {FacetConfig} section - section definition, as prepared from the annotations
 * @param {object} appSchema - app specific schema that gets enhanced
 * @param {string} formDefinitionKey - key of the form definition in schema
 */
function addTagsToFormDefinition(section: FacetConfig, appSchema: Definition, formDefinitionKey: string) {
    if (section.target) {
        const targetValue = section.target['qualifier']
            ? `${section.target['term']}#${section.target['qualifier']}`
            : `${section.target['term']}`;
        appSchema.definitions[formDefinitionKey][SchemaTag.keys] = [
            {
                name: SchemaKeyName.target,
                value: targetValue.replace(/com.sap.vocabularies.UI.v1./g, '')
            }
        ];
        appSchema.definitions[formDefinitionKey][SchemaTag.target] =
            section.target['fullyQualifiedName']?.split('@')[0];
    }
    appSchema.definitions[formDefinitionKey][SchemaTag.isViewNode] = true;

    if (section.entityType?.name) {
        appSchema.definitions[formDefinitionKey][SchemaTag.annotationPath] = createAnnotationPath(
            section.entityType.fullyQualifiedName,
            section.target['term'],
            section.target['qualifier']
        );
    }
}

/**
 * Adds definitions for forms in object page sections to the app schema
 * @param {SectionType} sectionType - prefix to distinguish Section and HeaderSection
 * @param {FacetConfig} section - section definition, as prepared from the annotations
 * @param {object} appSchemaSection - current definition of facet or section in the JSON schema
 * @param {object} appSchema - app specific schema that gets enhanced
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @param {string} facetKey - facet key
 * @returns {string | undefined} target id of form
 */
export function handleForm(
    sectionType: SectionType,
    section: FacetConfig,
    appSchemaSection: Definition,
    appSchema: Definition,
    version: FioriElementsVersion,
    facetKey: string
): string | undefined {
    if (!section.target) {
        return;
    }
    const targetID: string = determineTargetIdOfForm(section);
    const formDefinitionKey = prepareRef(`${sectionType}${DefinitionName.Form}<${targetID}>`);
    const fieldsDefinitionKey = prepareRef(`${sectionType}${DefinitionName.Fields}<${targetID}>`);
    const actionsDefinitionKey = prepareRef(`${sectionType}${DefinitionName.Actions}<${targetID}>`);
    const withActions = section.base === FacetBase.Identification && facetKey.indexOf('#') < 0 ? false : true;

    // Form definition
    appSchemaSection.properties.form = { $ref: `${DEFINITION_LINK_PREFIX}${formDefinitionKey}` };

    if (sectionType === SectionType.Section && withActions) {
        appSchema.definitions[formDefinitionKey] = {
            type: 'object',
            properties: {
                actions: {
                    $ref: `${DEFINITION_LINK_PREFIX}${actionsDefinitionKey}`
                },
                fields: { $ref: `${DEFINITION_LINK_PREFIX}${fieldsDefinitionKey}` }
            },
            description: 'Form',
            additionalProperties: false
        };
        appSchema.definitions[actionsDefinitionKey] = {
            type: 'object',
            properties: {},
            description: 'Actions',
            additionalProperties: false
        };
        appSchema.definitions[actionsDefinitionKey][SchemaTag.isViewNode] = true;
    } else {
        appSchema.definitions[formDefinitionKey] = {
            type: 'object',
            properties: {
                fields: { $ref: `${DEFINITION_LINK_PREFIX}${fieldsDefinitionKey}` }
            },
            description: 'Form',
            additionalProperties: false
        };
    }
    //Add schema tags
    addTagsToFormDefinition(section, appSchema, formDefinitionKey);

    // Fields definition
    appSchema.definitions[fieldsDefinitionKey] = {
        type: 'object',
        properties: {},
        description: 'Fields',
        additionalProperties: false
    };
    appSchema.definitions[fieldsDefinitionKey][SchemaTag.isViewNode] = true;
    // Add fields
    addFieldsToFormDefinition(section, sectionType, appSchema, targetID, version, withActions);
    return targetID;
}

/**
 * Replaces an alias in an annotation ID or reference, based on the AVT references' list
 * @param annotationIdentifier - annotation ID or reference
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @returns the converted string
 */
export function replaceAlias(annotationIdentifier: string, oDataServiceAVT: ConvertedMetadata): string {
    const potentialAlias = annotationIdentifier.indexOf('.') > -1 ? annotationIdentifier.split('.')[0] : undefined;
    let result = annotationIdentifier;
    if (potentialAlias && oDataServiceAVT) {
        const searchTerm = potentialAlias.replace('SAP__', '');
        const reference = oDataServiceAVT.references.find((ref) => {
            return ref.alias === searchTerm;
        });
        if (reference) {
            result = annotationIdentifier.replace(potentialAlias, reference.namespace);
        }
    }
    return result;
}

/**
 * Common routine to determine the name and the ID of a header action
 * @param action - action as given by AVT
 * @param entityType - entity type by AVT
 * @returns an object comprising action name and action id
 */

export function getActionNameAndId(
    item: any,
    oDataServiceAVT: ConvertedMetadata
): { actionName: string; actionId: string; namespace: string } {
    const action = replaceAlias(item.Action, oDataServiceAVT);

    let relevantAction = action;
    let separatorIndex = action.indexOf('(');

    if (separatorIndex > 0) {
        relevantAction = action.substring(0, separatorIndex);
    }
    separatorIndex = relevantAction.lastIndexOf('/');
    if (separatorIndex === -1) {
        separatorIndex = relevantAction.lastIndexOf('.');
    }
    let actionName: string, actionId: string, namespace: string;
    if (separatorIndex > -1) {
        actionName = relevantAction.substring(separatorIndex + 1);
        namespace = relevantAction.substring(0, separatorIndex);
        actionId = `${relevantAction.substring(0, separatorIndex)}::${actionName}`;
    } else {
        actionName = relevantAction;
        namespace = item.fullyQualifiedName.split('@')[0];
        if (actionName && actionName !== '') {
            actionId = `${namespace.substring(0, namespace.length - 1)}::${actionName}`;
        }
    }
    return { actionName, actionId, namespace };
}

/**
 * Checks if requested Entity Set is same as first Object Page
 * @param {SapUiAppPagesV2} pages - pages found in manifest
 * @param {GenerateAppSchemaParameters} generateParameters - Schema parameters
 * @returns {boolean} If found Object Page is first Object Page
 */
function isFirstObjectPage(pages: v2.SapUiAppPagesV2, generateParameters: GenerateAppSchemaParameters): boolean {
    for (const key in pages) {
        const element = pages[key];
        if (element.component?.name === v2.FE_TEMPLATE_V2_OBJECT_PAGE) {
            if (generateParameters.entitySet?.name === element.entitySet) {
                return true;
            } else {
                return false;
            }
        }
        return isFirstObjectPage(element.pages, generateParameters);
    }
}

/**
 * Add Object Page Footer Action Buttons to app-specific schema
 *
 * @param {object} appSchema Schema of the app
 * @param {GenerateAppSchemaParameters} generateParameters - Schema parameters
 * @param {FioriElementsVersion} version - Fiori elements versions
 * @param {function} addFooterActionCallBack - Callback function, either V2 or V4
 * @param {SapUiAppPagesV2} pages - pages found in manifest
 */
export function addFooterActions(
    appSchema: Definition,
    generateParameters: GenerateAppSchemaParameters,
    version: FioriElementsVersion,
    addFooterActionCallBack,
    pages?: v2.SapUiAppPagesV2
): void {
    appSchema.definitions[DefinitionName.ObjectPageFooter] = JSON.parse(
        JSON.stringify(appSchema.definitions[`${DefinitionName.ObjectPageFooter}<ObjectPageFooterActions>`])
    );
    // Change reference of footer
    appSchema.properties['footer'] = {
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.ObjectPageFooter}`
    };
    // Add action to configuration
    (appSchema.definitions[DefinitionName.ObjectPageFooter] as Definition).properties[PropertyName.actions] = {
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.FooterActions}`
    };
    const footerActions = (appSchema.definitions[DefinitionName.FooterActions] = {
        type: 'object',
        properties: {},
        description: 'Actions',
        additionalProperties: false
    });
    footerActions[SchemaTag.isViewNode] = true;

    const alias = findAlias(UIVOCABULARY, generateParameters.serviceAVT);
    const entitySet = generateParameters.entitySet;
    const entityType = generateParameters.entityType;
    const actionAnnotation = alias && entityType?.annotations?.[alias]?.Identification;
    if (actionAnnotation) {
        const convertedIdentification = actionAnnotation.fullyQualifiedName.replace(
            `@${UIAnnotationTerms.Identification}`,
            `/@${UIAnnotationTerms.Identification}`
        );
        footerActions[SchemaTag.annotationPath] = `/${convertedIdentification}`;
        //V2 or V4 vcallback
        addFooterActionCallBack(entityType, appSchema, footerActions, actionAnnotation);
    } else if (entityType) {
        footerActions[SchemaTag.annotationPath] =
            `/${entityType.fullyQualifiedName}/@${UIAnnotationTerms.Identification}`;
    }
    if (
        version === FioriElementsVersion.v2 &&
        isFirstObjectPage(pages, generateParameters) &&
        !isDraftEnabled(entitySet)
    ) {
        addStandardFooterActionsOP(appSchema, footerActions);
    }
    delete appSchema.definitions[DefinitionName.ObjectPageFooterActions];
    delete appSchema.definitions[`${DefinitionName.ObjectPageFooter}<${DefinitionName.ObjectPageFooterActions}>`];
}

/**
 * Add Object Page Header Action Buttons to app-specific schema
 * @param {object} appSchema Schema of the app
 * @param {EntityType} entityType - current entity type
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @param {function} addHeaderActionCallBack - Callback function, either V2 or V4
 */
export function addHeaderActions(
    appSchema: Definition,
    entityType: EntityType,
    entitySet: EntitySet,
    oDataServiceAVT: ConvertedMetadata,
    addHeaderActionCallBack,
    isV2 = false
): void {
    const actionsDefinition = DefinitionName.ObjectPageHeaderActions;
    const actions = appSchema.definitions[actionsDefinition] as Definition;
    (appSchema.definitions[DefinitionName.ObjectPageHeader] as Definition).properties[PropertyName.actions] = {
        $ref: `${DEFINITION_LINK_PREFIX}${actionsDefinition}`
    };
    const headerActions = (appSchema.definitions[actionsDefinition] = {
        type: 'object',
        properties: {},
        description: 'Actions',
        additionalProperties: false
    });
    headerActions[SchemaTag.isViewNode] = true;

    const alias = findAlias(UIVOCABULARY, oDataServiceAVT);
    const identification = alias && entityType?.annotations?.[alias]?.Identification;

    if (identification) {
        headerActions[SchemaTag.annotationPath] =
            '/' +
            identification.fullyQualifiedName.replace(
                `@${UIAnnotationTerms.Identification}`,
                `/@${UIAnnotationTerms.Identification}`
            );
    } else if (entityType) {
        headerActions[SchemaTag.annotationPath] =
            `/${entityType.fullyQualifiedName}/@${UIAnnotationTerms.Identification}`;
    }
    addHeaderActionCallBack(entityType, appSchema, identification, headerActions, oDataServiceAVT, entitySet);
    if (isV2) {
        // handle header actions
        addStandardHeaderActions(appSchema, true, actions);
    }
}

/**
 * Adds common Object Page Header definitions to the app schema
 * @param appSchema - app-specific JSOn schema
 * @param entityType - current entity Type
 */
export function addCommonHeaderSchema(appSchema: Definition, entityType: EntityType): void {
    appSchema.properties['header'] = {
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.ObjectPageHeader}`
    };
    appSchema.definitions[DefinitionName.ObjectPageHeader] = JSON.parse(
        JSON.stringify(appSchema.definitions[`${DefinitionName.ObjectPageHeader}`])
    );
    if (entityType) {
        appSchema.definitions[DefinitionName.ObjectPageHeader][SchemaTag.annotationPath] =
            `/${entityType.fullyQualifiedName}/@${UIAnnotationTerms.HeaderInfo}`;
    }
}

/**
 * Common logic to determine the EntitySet of a given EntityType
 * @param {EntityType} - entity type
 * @param {ConvertedMetadata} serviceAVT - AVT converter output, comprising all annotation information
 * @returns the entity set as defined in AVT, if found
 */
export function determineEntitySetOfEntityType(
    entityType: EntityType,
    serviceAVT: ConvertedMetadata
): EntitySet | undefined {
    return entityType
        ? serviceAVT.entitySets.find((es) => es.entityType.fullyQualifiedName === entityType.fullyQualifiedName)
        : undefined;
}
