import type { PageConfig } from '../../common';
import type {
    ObjectPageHeader,
    ObjectPageLayout,
    GenericSections,
    CustomSections,
    ObjectPageFooter
} from '../controls';

export enum VariantManagementTypeObjectPage {
    None = 'None',
    Control = 'Control'
}

export interface ObjectPageConfigV4 extends PageConfig {
    header?: ObjectPageHeader;
    layout?: ObjectPageLayout;
    /**
     * Allows you to enable and disable variant management for tables.
     *
     * - None (default): No variant management is possible by default.
     * - Control: Individual personalization for each control is possible.
     *
     * @descriptionSrcURL https://ui5.sap.com/sdk/#/topic/f26d42bea11c4deda82c7a2e00c9bc05.html
     */
    variantManagement?: VariantManagementTypeObjectPage;

    sections?: GenericSections | CustomSections;

    footer?: ObjectPageFooter;
}
