import type { Manifest, v4 } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { addPatternForBindingChangeOfEnumLR } from '../../../common/decoration/control';
import { getHeaderActionsPath } from '../../application';

/**
 * Sync class for Position
 */
export class CustomHeaderActionPosition implements v4.CustomHeaderActionPosition {
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey: string): string =>
                getHeaderActionsPath(pageName, manifest, sectionId, columnKey) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    anchor?: string;
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey: string): string =>
                getHeaderActionsPath(pageName, manifest, sectionId, columnKey) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    placement: v4.ActionPlacement;
}

export class CustomHeaderAction implements v4.CustomHeaderAction {
    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        }
    })
    @validity({
        since: '1.84.0'
    })
    text: string;

    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        }
    })
    @validity({
        since: '1.84.0'
    })
    position: CustomHeaderActionPosition;

    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        }
    })
    @validity({
        since: '1.84.0'
    })
    press: string;

    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        },
        generate: addPatternForBindingChangeOfEnumLR
    })
    @validity({
        since: '1.84.0'
    })
    visible?: boolean;

    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        },
        generate: addPatternForBindingChangeOfEnumLR
    })
    @validity({
        since: '1.84.0'
    })
    enabled?: boolean;
}
