import type { v2, ControlTypeFunction } from '@sap/ux-specification-types';
import { ControlType, FacetTitlePrefix } from '@sap/ux-specification-types';
import { syncRule, exportToFlexChange, addPatternForBindingChangeOfEnumOP } from '../../../common';

const PrefixHeaderEditable = 'header::headerEditable::';

/**
 * Create stable ID for Object Page DataPoint Header Section
 *
 * @param {string} baseId Base Id of the control
 * @param {string[]} _idBreadcrumbs
 * @param {string[]} _breadcrumbs
 * @param {ControlTypeFunction} _controlType
 * @returns {string} stable ID of Object Page Header Action button
 */
const getDataPointHeaderSectionId = (
    baseId: string,
    _idBreadcrumbs: string[],
    _breadcrumbs: string[],
    _controlType: ControlTypeFunction,
    title: string
): string => {
    return `${baseId}${PrefixHeaderEditable}${title}`;
};

/**
 * Create stable ID for Object Page Address Header Section
 *
 * @param {string} baseId Base Id of the control
 * @param {string[]} _idBreadcrumbs
 * @param {string[]} _breadcrumbs
 * @param {ControlTypeFunction} _controlType
 * @returns {string} stable ID of Object Page Header Action button
 */
const getAddressHeaderSectionId = (
    baseId: string,
    _idBreadcrumbs: string[],
    _breadcrumbs: string[],
    _controlType: ControlTypeFunction,
    title: string
): string => {
    return `${baseId}${PrefixHeaderEditable}${title}::ContactAddress`;
};

/**
 * Create stable ID for Object Page Chart Header Section
 *
 * @param {string} baseId Base Id of the control
 * @param {string[]} _idBreadcrumbs
 * @param {string[]} _breadcrumbs
 * @param {ControlTypeFunction} _controlType
 * @returns {string} stable ID of Object Page Header Action button
 */
const getChartHeaderSectionId = (
    baseId: string,
    _idBreadcrumbs: string[],
    _breadcrumbs: string[],
    _controlType: ControlTypeFunction,
    title: string
): string => {
    return `${baseId}${PrefixHeaderEditable}${title}::SmartMicroChartVBox`;
};

/**
 * Create stable ID for Object Page Form Header Section
 *
 * @param {string} baseId Base Id of the control
 * @param {string[]} _idBreadcrumbs
 * @param {string[]} breadcrumbs
 * @param {ControlTypeFunction} _controlType
 * @returns {string} stable ID of Object Page Header Action button
 */
const getFormHeaderSectionId = (
    baseId: string,
    _idBreadcrumbs: string[],
    breadcrumbs: string[],
    _controlType: ControlTypeFunction,
    title: string
): string => {
    const sectionName = breadcrumbs[breadcrumbs.length - 1].replace(/@com.sap/g, 'com.sap');
    const facetId = title?.includes(FacetTitlePrefix) ? title.split(FacetTitlePrefix)[1] : sectionName;
    return `${baseId}${PrefixHeaderEditable}${facetId}::Form`;
};

const flexRuleForHeaderSection = {
    controlType: (): ControlType => ControlType.ObjectPageHeaderSection,
    createFlexChange: exportToFlexChange
};

export class CommonHeaderFacetSettings implements v2.CommonHeaderFacetSettings {
    @syncRule({
        flex: {
            ...flexRuleForHeaderSection,
            // Placeholder for stable ID, will be replaced by the specific section
            controlId: getFormHeaderSectionId
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    visible?: boolean;
}

export class ObjectPageHeaderSectionForm extends CommonHeaderFacetSettings implements v2.ObjectPageHeaderSectionForm {
    @syncRule({
        flex: {
            ...flexRuleForHeaderSection,
            controlId: getFormHeaderSectionId
        }
    })
    visible?: boolean;
}

export class ObjectPageHeaderSectionChart extends CommonHeaderFacetSettings implements v2.ObjectPageHeaderSectionChart {
    @syncRule({
        flex: {
            ...flexRuleForHeaderSection,
            controlId: getChartHeaderSectionId
        }
    })
    visible?: boolean;
}

export class ObjectPageHeaderSectionDataPoint
    extends CommonHeaderFacetSettings
    implements v2.ObjectPageHeaderSectionDataPoint
{
    @syncRule({
        flex: {
            ...flexRuleForHeaderSection,
            controlId: getDataPointHeaderSectionId
        }
    })
    visible?: boolean;
}

export class ObjectPageHeaderSectionAddress
    extends CommonHeaderFacetSettings
    implements v2.ObjectPageHeaderSectionAddress
{
    @syncRule({
        flex: {
            ...flexRuleForHeaderSection,
            controlId: getAddressHeaderSectionId
        }
    })
    visible?: boolean;
}

export class HeaderSections implements v2.HeaderSections {
    [key: string]:
        | ObjectPageHeaderSectionAddress
        | ObjectPageHeaderSectionChart
        | ObjectPageHeaderSectionDataPoint
        | ObjectPageHeaderSectionForm;
}
