const ROOT_PROPERTIES_ORDER: Array<keyof v4.ObjectPageConfigV4> = ['header', 'layout', 'sections', 'footer'];
const CRITICAL_ACTION_TYPES = [
    'UI.CriticalityType/Positive',
    'UI.CriticalityType/VeryPositive',
    'UI.CriticalityType/Negative',
    'UI.CriticalityType/VeryNegative'
];
const CUSTOM_ACTION_DEFINITION_DESCRIPTION = 'Custom Action';

type ActionListType = {
    index: string;
    item: object;
};

/**
 * V4 callback - adds a single header action to schema
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @param {ActionListType} action - item of the Ui.Identification collection and its original index
 * @param {ActionType} actionType - action classification for grouping
 * @param {Definition} appSchema Schema of the app
 * @param {EntityType} entityType - current entity type
 * @param {Definition} headerActions - list of header actions, to be enhanced
 */
function addSingleHeaderActionV4(
    oDataServiceAVT: ConvertedMetadata,
    action: ActionListType,
    actionType: ActionType,
    appSchema: Definition,
    entityType: any,
    headerActions: Definition,
    index: number
): void {
    const { actionName, namespace } = getActionNameAndId(action.item, oDataServiceAVT);
    const stableId = getStableIdPartFromDataField(action.item);
    //stableId is used as key in V4, as the DataFieldForAction:: prefix shall be present in manifest settings
    const convertedIdentification = action.item['fullyQualifiedName'].replace(
        `@${UIAnnotationTerms.Identification}`,
        `/@${UIAnnotationTerms.Identification}`
    );
    const actionDefinitionKey = `ObjectPageHeaderAction<${prepareRef(stableId)}>`;
    const headerAction = (appSchema.definitions[actionDefinitionKey] = {
        type: 'object',
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.ObjectPageHeaderAction}`,
        description: getDataFieldDescription(action.item as DataFieldAbstractTypes, entityType)
    });
    headerAction[SchemaTag.isViewNode] = true;
    headerAction[SchemaTag.dataType] = determineDataType(action.item);
    headerAction[SchemaTag.propertyIndex] = index;

    //keys
    headerAction[SchemaTag.keys] = [];
    if (action.item[SchemaKeyName.semanticObject]) {
        headerAction[SchemaTag.keys].push({
            name: SchemaKeyName.semanticObject,
            value: action.item[SchemaKeyName.semanticObject]
        });
    }
    headerAction[SchemaTag.keys].push({ name: SchemaKeyName.action, value: actionName });

    if (namespace) {
        appSchema.definitions[actionDefinitionKey][SchemaTag.target] = namespace;
    }
    headerAction[SchemaTag.actionType] = actionType;
    headerAction[SchemaTag.annotationPath] = `/${convertedIdentification}`;
    headerActions.properties[stableId] = {
        $ref: `${DEFINITION_LINK_PREFIX}${actionDefinitionKey}`
    };

    // add propertyIndexes
    headerActions.properties[stableId][SchemaTag.propertyIndex] = index;
}

/**
 * Adds a given standard action to the app schema of the object page
 * @param {string} actionName  - name of the action
 * @param {Definition} appSchema - schema of the object page
 * @param {Definition} headerActions - list of header actions, to be enhanced
 * @param {number} index - index for propertyIndex
 *
 * @returns the new header action definition
 */
function addStandardHeaderAction(
    actionName: string,
    appSchema: Definition,
    headerActions: Definition,
    index: number
): Definition {
    const referenceKey = `${actionName}Action`;
    const actionDefinitionKey = `ObjectPageHeaderAction<${referenceKey}>`;
    const headerAction = (appSchema.definitions[actionDefinitionKey] = {
        type: 'object',
        description: `${actionName}`,
        additionalProperties: true
    });
    headerAction[SchemaTag.isViewNode] = true;
    headerAction[SchemaTag.propertyIndex] = index;

    //keys
    headerAction[SchemaTag.keys] = [];
    headerAction[SchemaTag.keys].push({ name: SchemaKeyName.action, value: actionName });

    headerAction[SchemaTag.actionType] = ActionType.Standard;
    headerActions.properties[referenceKey] = {
        $ref: `${DEFINITION_LINK_PREFIX}${actionDefinitionKey}`
    };
    // add propertyIndexes
    headerActions.properties[referenceKey][SchemaTag.propertyIndex] = index;
    return headerAction;
}

/**
 * V4 callback - adds a single footer action to schema
 * @param {ActionListType} action - item of the Ui.Identification collection and its original index
 * @param {ActionType} actionType - action classification for grouping
 * @param {EntityType} entityType - current entity type
 * @param {Definition} appSchema - schema of the object page
 * @param {Definition} footerActions - list of footer actions, to be enhanced
 */
function addSingleFooterAction(
    action: ActionListType,
    actionType: ActionType,
    entityType: any,
    appSchema: Definition,
    footerActions: Definition,
    index: number
) {
    const actionId = getStableIdPartFromDataField(action.item);
    const description = getDataFieldDescription(action.item as DataFieldAbstractTypes, entityType);
    const actionDefinitionKey = prepareRef(`${DefinitionName.ObjectPageFooterAction}<${actionId}>`);
    const convertedIdentification = action.item['fullyQualifiedName'].replace(
        `@${UIAnnotationTerms.Identification}`,
        `/@${UIAnnotationTerms.Identification}`
    );
    const footerAction = (appSchema.definitions[actionDefinitionKey] = {
        type: 'object',
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.FooterActionV4}`,
        description: description
    });
    footerAction[SchemaTag.isViewNode] = true;
    footerAction[SchemaTag.actionType] = actionType;
    footerAction[SchemaTag.propertyIndex] = index;
    footerAction[SchemaTag.annotationPath] = `/${convertedIdentification}`;

    footerActions.properties[actionId] = {
        $ref: `${DEFINITION_LINK_PREFIX}${actionDefinitionKey}`
    };
}

/**
 * Distiguishes critical actions and others and returns two separate groups
 * @param {any} identification - UI.Identification annotation as present in AVT ConvertedMetadata
 * @returns two separate groups of items
 */
function groupByCriticality(identification: any) {
    const criticalActions: ActionListType[] = [];
    const unCriticalActions: ActionListType[] = [];

    identification.forEach((item, index) => {
        if (
            (!item['Determining'] && item.$Type === UIAnnotationTypes.DataFieldForAction) ||
            item.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation
        ) {
            if (CRITICAL_ACTION_TYPES.includes(item['Criticality'])) {
                criticalActions.push({ index, item });
            } else {
                unCriticalActions.push({ index, item });
            }
        }
    });
    return { criticalActions, unCriticalActions };
}

/**
 * Adds the critical header actions to schema
 * @param {ActionListType[]} criticalActions - list of critical actions
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @param {Definition} appSchema - schema of the object page
 * @param {EntityType} entityType - current entity type
 * @param {Definition} headerActions - list of all header actions
 */
function addCriticalActions(
    criticalActions: ActionListType[],
    oDataServiceAVT: ConvertedMetadata,
    appSchema: Definition,
    entityType: EntityType,
    headerActions: Definition
) {
    for (let index = 0; index < criticalActions.length; index++) {
        addSingleHeaderActionV4(
            oDataServiceAVT,
            criticalActions[index],
            ActionType.Criticality,
            appSchema,
            entityType,
            headerActions,
            index
        );
    }
}

/**
 * Checks if one of the uncritical actions is marked by isCopyAction.
 * If so, adds this first to the schema and deletes it from the list.
 * @param {ActionListType[]} unCriticalActions - list of uncritical actions
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @param {Definition} appSchema - schema of the object page
 * @param {EntityType} entityType - current entity type
 * @param {Definition} headerActions - list of all header actions
 * @param {number} nextIndex - index for propertyIndex
 */
function addCopyAction(
    unCriticalActions: ActionListType[],
    oDataServiceAVT: ConvertedMetadata,
    appSchema: Definition,
    entityType: EntityType,
    headerActions: Definition,
    nextIndex: number
) {
    const copyActionIndex = unCriticalActions.findIndex((action) => {
        return action.item['annotations']?.UI?.IsCopyAction !== undefined;
    });
    if (copyActionIndex > -1) {
        addSingleHeaderActionV4(
            oDataServiceAVT,
            unCriticalActions[copyActionIndex],
            ActionType.Copy,
            appSchema,
            entityType,
            headerActions,
            nextIndex
        );
        nextIndex++;
        unCriticalActions.splice(copyActionIndex, 1);
    }
}

/**
 * Adds the critical header actions to schema
 * @param {ActionListType[]} unCriticalActions - list of uncritical actions
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @param {Definition} appSchema - schema of the object page
 * @param {EntityType} entityType - current entity type
 * @param {Definition} headerActions - list of all header actions
 * @param {number} nextIndex - index for propertyIndex
 */
function addUncriticalActions(
    unCriticalActions: ActionListType[],
    oDataServiceAVT: ConvertedMetadata,
    appSchema: Definition,
    entityType: EntityType,
    headerActions: Definition,
    nextIndex: number
) {
    for (const action of unCriticalActions) {
        addSingleHeaderActionV4(
            oDataServiceAVT,
            action,
            ActionType.Annotation,
            appSchema,
            entityType,
            headerActions,
            nextIndex
        );
        nextIndex++;
    }
}

/**
 * Fills the annotationPath of the given schema definition, by checking first the EntitySet annotation, then the EntityType annotation.
 * @param {Definition} schemaDefinition - current definition in the schema
 * @param {EntitySet} entitySet - current entity set
 * @param {EntityType} entityType - current entity type
 * @param {string} annotationName - name of the annotation term to be checked
 */
function setAnnotationPathFromEntitySetOrEntityType(
    schemaDefinition: Definition,
    entitySet: EntitySet,
    entityType: EntityType,
    annotationName: string
) {
    if (entitySet?.annotations?.UI?.[annotationName] || !entityType?.annotations?.UI?.[annotationName]) {
        schemaDefinition[SchemaTag.annotationPath] =
            `/${entitySet.fullyQualifiedName}/${UIVOCABULARYALPHADOT}${annotationName}`;
    } else {
        schemaDefinition[SchemaTag.annotationPath] =
            `/${entityType.fullyQualifiedName}/${UIVOCABULARYALPHADOT}${annotationName}`;
    }
}

/**
 * Callback for adding a single action to headerActions of V4
 * @param {EntityType} entityType - current entity type
 * @param {Definition} appSchema - schema of the object page
 * @param {any} identification - UI.Identification annotation as present in AVT ConvertedMetadata
 * @param {Definition} headerActions - list of header actions, to be enhanced
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @param {EntitySet} entitySet - current entity set
 */
function addHeaderActionCallBackV4(
    entityType: EntityType,
    appSchema: Definition,
    identification: EntityTypeAnnotationsBase_UI['Identification'] | undefined,
    headerActions: Definition,
    oDataServiceAVT: ConvertedMetadata,
    entitySet: EntitySet
): any {
    //Build groups of action types
    const { criticalActions = [], unCriticalActions = [] } = identification ? groupByCriticality(identification) : {};

    addCriticalActions(criticalActions, oDataServiceAVT, appSchema, entityType, headerActions);

    if (entitySet) {
        const isDeletable = isEntityDeletable(entitySet);
        const isUpdatable = isEntityUpdatable(entitySet);
        // Add standard header actions

        // If ReadOnly, and all capabilities restrictions are set to true or When nothing exists i.e. no capabilities annotation and no draft root, show only Delete button
        // If DraftRoot + DraftNode and dynamic value for restriction - show Edit and Delete, if bool == false then hide the button

        if (isDraftEnabled(entitySet) && isUpdatable) {
            const editAction = addStandardHeaderAction('Edit', appSchema, headerActions, criticalActions.length);
            setAnnotationPathFromEntitySetOrEntityType(editAction, entitySet, entityType, 'UpdateHidden');
        }
        if (isDeletable) {
            const deleteAction = addStandardHeaderAction(
                'Delete',
                appSchema,
                headerActions,
                criticalActions.length + 1
            );
            setAnnotationPathFromEntitySetOrEntityType(deleteAction, entitySet, entityType, 'DeleteHidden');
        }
    }

    const nextIndex = criticalActions.length + 2;

    //Check if isCopyAction is defined, this gets rendered first after delete
    addCopyAction(unCriticalActions, oDataServiceAVT, appSchema, entityType, headerActions, nextIndex);

    addUncriticalActions(unCriticalActions, oDataServiceAVT, appSchema, entityType, headerActions, nextIndex);
}

/**
 * Callback for adding a single action to footerActions of V4
 * @param {EntityType} entityType - current entity type
 * @param {Definition} appSchema - schema of the object page
 * @param {Definition} footerActions - list of footer actions, to be enhanced
 * @param {any} identification - UI.Identification annotation as present in AVT ConvertedMetadata
 */
function addFooterActionCallBackV4(
    entityType,
    appSchema: Definition,
    footerActions: Definition,
    identification: any
): any {
    const criticalActions: ActionListType[] = [];
    const unCriticalActions: ActionListType[] = [];

    identification.forEach((item, index) => {
        if (item['Determining'] && item.$Type === UIAnnotationTypes.DataFieldForAction) {
            if (item['Criticality']) {
                criticalActions.push({ index, item });
            } else {
                unCriticalActions.push({ index, item });
            }
        }
    });
    for (let index = 0; index < criticalActions.length; index++) {
        const action = criticalActions[index];
        addSingleFooterAction(action, ActionType.Criticality, entityType, appSchema, footerActions, index);
    }

    for (let index = 0; index < unCriticalActions.length; index++) {
        const action = unCriticalActions[index];
        addSingleFooterAction(
            action,
            ActionType.Annotation,
            entityType,
            appSchema,
            footerActions,
            criticalActions.length + index
        );
    }
}

/**
 * Object Page specific enhancements to addCustomColumnDefinition
 * @param {object} appSchema - app schema of the object page
 * @param {SapUiAppPageV4} v4Page Current page in manifest
 * @param {string} lineItemId - identifier of the current table in schema
 * @param {string} facetIdInManifest - identifier of the current object page section in manifest
 * @param {ExtensionLogger} logger - logger for exceptions
 */
function addCustomColumnsOP(
    appSchema: Definition,
    v4Page: v4.SapUiAppPageV4,
    lineItemId: string,
    facetIdInManifest: string,
    logger: ExtensionLogger
) {
    // Create type specific copy of 'TableCustomColumnOP'
    const useDefinitionAsKey =
        lineItemId !== DefinitionName.LineItems &&
        lineItemId !== 'LineItemsOfSPV::LineItem' &&
        lineItemId !== `@${UIAnnotationTerms.LineItem}`;
    const customColumDefinitionKey = useDefinitionAsKey
        ? `${DefinitionName.CustomColumnOP}<${lineItemId}>`
        : DefinitionName.CustomColumnOP;
    const customColumnDefinition = appSchema.definitions[customColumDefinitionKey] as Definition;
    customColumnDefinition[SchemaTag.isViewNode] = true;
    customColumnDefinition.description = 'Custom Column';

    // Update definitions for type specific custom columns
    const schemaId = `${DefinitionName.CustomColumnOP}<${lineItemId}>`;
    addCustomColumnDefinition(appSchema, v4Page, logger, schemaId, lineItemId, facetIdInManifest);
    const facetKey = useDefinitionAsKey ? lineItemId : DefinitionName.PositionOP;
    parseSchemaDefinition(DefinitionName.PositionOP, facetKey, appSchema, useDefinitionAsKey);
    const positionId = useDefinitionAsKey ? `${DefinitionName.PositionOP}<${lineItemId}>` : DefinitionName.PositionOP;
    (customColumnDefinition.properties.position as Definition).$ref = `${DEFINITION_LINK_PREFIX}${positionId}`;
    addFragmentEnumForAnchor(appSchema, lineItemId, v4Page, positionId, facetIdInManifest.replace(/::/g, '/'));
}

/**
 * Object Page specific enhancements to addCustomColumnDefinition
 * @param {object} appSchema - app schema of the object page
 * @param {SapUiAppPageV4} v4Page Current page in manifest
 * @param {string} lineItemId - identifier of the current table in schema
 * @param {string} facetIdInManifest - identifier of the current object page section in manifest
 * @param {ExtensionLogger} logger - logger for exceptions
 */
function addTableCustomActionsOP(
    appSchema: Definition,
    v4Page: v4.SapUiAppPageV4,
    lineItemId: string,
    facetIdInManifest: string,
    logger: ExtensionLogger
) {
    // Create type specific copy of 'TableCustomActionOP'
    const useDefinitionAsKey =
        lineItemId !== DefinitionName.LineItems &&
        lineItemId !== 'LineItemsOfSPV::LineItem' &&
        lineItemId !== `@${UIAnnotationTerms.LineItem}`;
    const customActionDefinitionKey = useDefinitionAsKey
        ? `${DefinitionName.CustomTableActionOP}<${lineItemId}>`
        : DefinitionName.CustomTableActionOP;
    const customActionDefinition = appSchema.definitions[customActionDefinitionKey] as Definition;
    customActionDefinition[SchemaTag.isViewNode] = true;
    customActionDefinition.description = CUSTOM_ACTION_DEFINITION_DESCRIPTION;
    // Update definitions for type specific custom columns
    const schemaId = `${DefinitionName.CustomTableActionOP}<${lineItemId}>`;
    const schemaIdForActions = `${DefinitionName.Actions}<${prepareRef(lineItemId)}>`;
    addCustomActionDefinition(appSchema, v4Page, logger, schemaId, schemaIdForActions, facetIdInManifest);
    const facetKey = useDefinitionAsKey ? lineItemId : DefinitionName.CustomActionPositionOP;
    parseSchemaDefinition(DefinitionName.CustomActionPositionOP, facetKey, appSchema, useDefinitionAsKey);
    // Anchor selection
    const positionId = useDefinitionAsKey
        ? `${DefinitionName.CustomActionPositionOP}<${lineItemId}>`
        : DefinitionName.CustomActionPositionOP;
    (customActionDefinition.properties.position as Definition).$ref = `${DEFINITION_LINK_PREFIX}${positionId}`;
    addEnumForActionAnchor(appSchema, schemaIdForActions, positionId);
}

/**
 * Object Page specific custom action enhancements to header and footer
 * @param {object} appSchema - app schema of the object page
 * @param {SapUiAppPageV4} v4Page  - current page in manifest
 * @param {DefinitionName} customActionDefinitionKey - custom action key used in definitions
 * @param {ExtensionLogger} logger - logger for exceptions
 */
function addHeaderFooterCustomActionsOP(
    appSchema: Definition,
    v4Page: v4.SapUiAppPageV4,
    customActionDefinitionKey: DefinitionName.CustomHeaderActionOP | DefinitionName.CustomFooterActionOP,
    logger: ExtensionLogger
) {
    const customActionDefinition = appSchema.definitions[customActionDefinitionKey] as Definition;
    customActionDefinition[SchemaTag.isViewNode] = true;
    customActionDefinition.description = CUSTOM_ACTION_DEFINITION_DESCRIPTION;
    const schemaIdForActions =
        customActionDefinitionKey === DefinitionName.CustomHeaderActionOP
            ? DefinitionName.ObjectPageHeaderActions
            : DefinitionName.FooterActions;
    addHeaderFooterCustomActionDefinition(appSchema, v4Page, logger, customActionDefinitionKey, schemaIdForActions);
    const facetKey =
        customActionDefinitionKey === DefinitionName.CustomHeaderActionOP
            ? DefinitionName.CustomHeaderActionPositionOP
            : DefinitionName.CustomFooterActionPositionOP;
    parseSchemaDefinition(facetKey, facetKey, appSchema, false);
    // Anchor selection
    (customActionDefinition.properties.position as Definition).$ref = `${DEFINITION_LINK_PREFIX}${facetKey}`;
    addEnumForActionAnchor(appSchema, schemaIdForActions, facetKey);
    if (appSchema.definitions[schemaIdForActions]) {
        (appSchema.definitions[schemaIdForActions] as Definition).additionalProperties = {
            $ref: `${DEFINITION_LINK_PREFIX}${customActionDefinitionKey}`
        };
    }
}

/**
 * Object Page specific enhancements to handleFormIdentification
 * @param {object} appSchema - app schema of the object page
 * @param {SapUiAppPageV4} v4Page - current page in manifest
 * @param {string} formItemId - identifier of the current form in schema
 * @param {string} facetIdInManifest - identifier of the current object page section in manifest
 * @param {ExtensionLogger} logger - logger for exceptions
 */
function addFormCustomActionsOP(
    appSchema: Definition,
    v4Page: v4.SapUiAppPageV4,
    formItemId: string,
    facetIdInManifest: string,
    logger: ExtensionLogger
) {
    const customActionDefinitionKey = `${DefinitionName.CustomFormActionOP}<${formItemId}>`;
    const customActionDefinition = appSchema.definitions[customActionDefinitionKey] as Definition;
    customActionDefinition[SchemaTag.isViewNode] = true;
    customActionDefinition.description = CUSTOM_ACTION_DEFINITION_DESCRIPTION;
    const schemaId = `${DefinitionName.CustomFormActionOP}<${formItemId}>`;
    const schemaIdForActions = `${DefinitionName.SectionActions}<${prepareRef(formItemId)}>`;
    addCustomActionDefinition(appSchema, v4Page, logger, schemaId, schemaIdForActions, facetIdInManifest);
    parseSchemaDefinition(DefinitionName.CustomActionPositionOP, formItemId, appSchema, true);
    // Anchor selection
    const positionId = `${DefinitionName.CustomActionPositionOP}<${formItemId}>`;
    (customActionDefinition.properties.position as Definition).$ref = `${DEFINITION_LINK_PREFIX}${positionId}`;
    addEnumForActionAnchor(appSchema, schemaIdForActions, positionId);
    if (appSchema.definitions[schemaIdForActions]) {
        (appSchema.definitions[schemaIdForActions] as Definition).additionalProperties = {
            $ref: `${DEFINITION_LINK_PREFIX}${customActionDefinitionKey}`
        };
    }
}

/**
 * Adds definitions for line items in object page sections to the app schema
 * @param {FacetConfig} facet - the given facet from the UI annotations
 * @param {string} facetKey - key of the facet, as used in annotation
 * @param {Definition} appSchema - app schema of the object page
 * @param {object} lineItemFacet - current lineItem facet
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @param {SapUiAppPageV4} v4Page Current page in manifest
 * @param {ExtensionLogger} logger - logger for exceptions
 */
function handleLineItem(
    facet: FacetConfig,
    facetKey: string,
    appSchema: Definition,
    lineItemFacet: object,
    oDataServiceAVT: ConvertedMetadata,
    v4Page: v4.SapUiAppPageV4,
    logger: ExtensionLogger
): Definition {
    const lineItemId = getFacetKeyInSchema(facetKey);
    const index = facetKey.lastIndexOf('::');
    const annotation = index > -1 ? facetKey.substring(index + 2) : facetKey;
    const tableDefinition = parseSchemaDefinition(DefinitionName.ObjectPageTable, facetKey, appSchema);
    if (facet.entityType && annotation) {
        tableDefinition[SchemaTag.annotationPath] = `/${facet.entityType.fullyQualifiedName}/${annotation}`;
    }
    tableDefinition.properties.columns = {
        $ref: `${DEFINITION_LINK_PREFIX}${lineItemId}`
    };
    (tableDefinition.properties.toolBar as Definition).$ref = getFacetDefinitionLink(
        DefinitionName.ObjectPageToolBar,
        facetKey
    );
    //adjust quickVariantSelection
    (tableDefinition.properties.quickVariantSelection as Definition).$ref = getFacetDefinitionLink(
        DefinitionName.QuickVariantSelectionV4OP,
        facetKey
    );
    const tableQVS = parseSchemaDefinition(DefinitionName.QuickVariantSelectionV4OP, facetKey, appSchema);
    tableQVS.properties.paths['items'].$ref = getFacetDefinitionLink(DefinitionName.AnnotationPathAsObject, facetKey);
    parseSchemaDefinition(DefinitionName.AnnotationPathAsObject, facetKey, appSchema);

    addEnumForSingleTabVariant(facet.entityType, appSchema, `${DefinitionName.AnnotationPathAsObject}<${lineItemId}>`);
    const dataForLineItem: LineItemDataForSchema = {
        appSchema,
        lineItemAnnotation: lineItemFacet['target'] as AnnotationTerm<LineItem>,
        entityType: lineItemFacet['entityType'],
        oDataServiceAVT,
        lineItemId
    };
    addLineItemsTypeToSchema(
        dataForLineItem,
        DefinitionName.ObjectPageTableColumn,
        DefinitionName.CustomColumnOP,
        DefinitionName.CustomTableActionOP
    );
    addCustomColumnsOP(appSchema, v4Page, lineItemId, facetKey, logger);
    addTableCustomActionsOP(appSchema, v4Page, lineItemId, facetKey, logger);
    return tableDefinition;
}

/**
 * Adds definitions for table section in object page sections to the app schema
 * @param {FacetConfig} facet - current facet
 * @param {string} facetKey - facet key
 * @param {Definition} appSchema - app specific schema of the object page that gets enhanced
 * @param {object} lineItemFacet - current lineItem facet
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {SapUiAppPageV4} v4Page Current page in manifest
 * @param {Definition} sections - schema of current sections definition.
 * @returns {object} sectionTable - section of app schema containing added table section
 */
function handleSectionTable(
    facet: FacetConfig,
    facetKey: string,
    appSchema: Definition,
    lineItemFacet: FacetConfig,
    generateParameters: GenerateAppSchemaParameters,
    v4Page,
    sections?: Definition
): { sectionTable: Definition; table: Definition } {
    if (sections) {
        sections.properties[facetKey] = {
            $ref: getFacetDefinitionLink(DefinitionName.ObjectPageSectionTableV4, facetKey)
        };
    }
    const sectionTable = parseSchemaDefinition(DefinitionName.ObjectPageSectionTableV4, facetKey, appSchema);
    addSectionTitleAndDescription(facet, sectionTable, getFacetKeyInSchema(facetKey));
    sectionTable[SchemaTag.annotationPath] = facet.annotationPath;
    sectionTable[SchemaTag.target] = facet.namespace;
    sectionTable.properties.table['$ref'] = getFacetDefinitionLink(DefinitionName.ObjectPageTable, facetKey);
    const table = handleLineItem(
        facet,
        facetKey,
        appSchema,
        lineItemFacet,
        generateParameters.serviceAVT,
        v4Page,
        generateParameters.logger
    );
    return { sectionTable, table };
}

/**
 * Adds definitions for Identification and Form facet items in object page sections to the app schema
 * @param {FacetConfig} facet - current facet
 * @param {v4.SapUiAppPageV4} v4Page Current page in manifest
 * @param {string} facetKey - facet key
 * @param {object} sections - schema of current sections definition
 * @param {object} appSchema - app specific schema of the object page that gets enhanced
 * @param {SectionType} sectionPrefix - prefix to distinguish Section and HeaderSection
 * @param {ExtensionLogger} logger - logger for exceptions
 */
function handleFormIdentification(
    facet: FacetConfig,
    v4Page: v4.SapUiAppPageV4,
    facetKey: string,
    sections: Definition,
    appSchema: Definition,
    sectionPrefix: SectionType,
    logger: ExtensionLogger
) {
    let definitionType: DefinitionName;
    if (sectionPrefix === SectionType.Section) {
        definitionType = DefinitionName.ObjectPageSectionForm;
    } else if (sectionPrefix === SectionType.HeaderSection) {
        definitionType = DefinitionName.ObjectPageHeaderSectionForm;
    }
    sections['properties'][facetKey] = {
        $ref: getFacetDefinitionLink(definitionType, facetKey)
    };
    const formSection: Definition = parseSchemaDefinition(definitionType, facetKey, appSchema);
    if (!facet.ID) {
        formSection.properties = {};
    }
    addSectionTitleAndDescription(facet, formSection, facetKey);
    formSection[SchemaTag.annotationPath] = facet.annotationPath;
    formSection[SchemaTag.target] = facet.namespace;
    formSection[SchemaTag.isViewNode] = true;
    const targetId = handleForm(sectionPrefix, facet, formSection, appSchema, FioriElementsVersion.v4, facetKey);
    if (facet.base === FacetBase.Form) {
        const formItemId = getFacetKeyInSchema(facetKey);
        parseSchemaDefinition(DefinitionName.CustomFormActionOP, targetId, appSchema);
        addFormCustomActionsOP(appSchema, v4Page, targetId, formItemId, logger);
    }
}

/**
 * Adds definitions for header sections (DataPoint, Chart) in object page sections to the app schema
 * @param {FacetConfig} facet - current facet
 * @param {string} facetKey - facet key
 * @param {object} sections - schema of current sections definition
 * @param {object} appSchema - app specific schema of the object page that gets enhanced
 * @param {SectionType} facetDefinitionKey - key to identify new definition
 */
function handleHeaderFacet(
    facet: FacetConfig,
    facetKey: string,
    sections: Definition,
    appSchema: Definition,
    facetDefinitionKey: string
) {
    sections['properties'][facetKey] = {
        $ref: `${DEFINITION_LINK_PREFIX}${facetDefinitionKey}`
    };
    const sectionFacet = parseSchemaDefinition(
        DefinitionName.CommonHeaderFacetSettings,
        facetDefinitionKey,
        appSchema,
        false
    );
    addTargetTitleAndSectionDescription(facet, sectionFacet, facetKey);
    sectionFacet[SchemaTag.annotationPath] = facet.annotationPath;
    sectionFacet[SchemaTag.target] = facet.namespace;
    sectionFacet[SchemaTag.dataType] = determineDataTypeOfTarget(facet?.target);
    sectionFacet[SchemaTag.isViewNode] = true;
}

/**
 * Adds definitions for Chart items in object page sections to the app schema
 * @param {FacetConfig | Chart | object} facet - current facet.
 * @param {string} facetKey - facet key.
 * @param {object} appSchema - app specific schema of the object page that gets enhanced.
 * @param {EntityType} entityType - current entity type
 */
function handleChartActions(
    facet: FacetConfig | Chart | object,
    facetKey: string,
    appSchema: Definition,
    entityType: EntityType
): Definition {
    const actions = parseSchemaDefinition(DefinitionName.ObjectPageToolBarActions, facetKey, appSchema);
    actions.additionalProperties = false;
    actions.properties = {};
    if ((facet as Chart).Actions) {
        let i = 0;
        (facet as Chart).Actions.forEach((actionRecord) => {
            const description = getDataFieldDescription(actionRecord as DataFieldAbstractTypes, entityType);
            const prefix = actionRecord.$Type.split(UIVOCABULARYDOT)[1];
            const actionId = (actionRecord.Action as string).replace('/', '::');
            const toolBarActionsProperty = (actions.properties[`${prefix}::${actionRecord.Action}`] = {
                $ref: getFacetDefinitionLink(DefinitionName.ObjectPageToolBarAction, actionId),
                description
            });
            toolBarActionsProperty[SchemaTag.propertyIndex] = i;
            toolBarActionsProperty[SchemaTag.annotationType] = actionRecord.$Type;
            const toolBarAction = parseSchemaDefinition(
                DefinitionName.ObjectPageToolBarAction,
                prepareRef(actionId),
                appSchema
            );
            toolBarAction[SchemaTag.annotationPath] = getFacetAnnotationPath(actionRecord);
            toolBarAction[SchemaTag.dataType] = determineDataType(actionRecord);
            toolBarAction.properties = {};
            i++;
        });
    }
    return actions;
}

/**
 * Adds definitions for Chart items in object page sections to the app schema
 * @param {FacetConfig | Chart | object} facet - current facet.
 * @param {string} facetKey - facet key.
 * @param {object} appSchema - app specific schema of the object page that gets enhanced.
 * @param {EntityType} entityType - current entity type
 */
function handleChart(
    facet: FacetConfig | Chart | object,
    facetKey: string,
    appSchema: Definition,
    entityType?: EntityType
): { chart: Definition; chartActions: Definition } {
    const chart = parseSchemaDefinition(DefinitionName.ObjectPageChart, facetKey, appSchema);
    if (facet['Title']) {
        chart.description = facet['Title'];
    }
    chart[SchemaTag.annotationPath] = getFacetAnnotationPath(facet as PresentationVariantV4);
    chart[SchemaTag.dataType] = determineDataTypeOfTarget((facet as FacetConfig).target || facet);
    chart[SchemaTag.isViewNode] = true;
    chart.properties.toolBar['$ref'] = getFacetDefinitionLink(DefinitionName.ObjectPageToolBar, facetKey);
    const chartToolBar = parseSchemaDefinition(DefinitionName.ObjectPageToolBar, facetKey, appSchema);

    chartToolBar.properties.actions['$ref'] = getFacetDefinitionLink(DefinitionName.ObjectPageToolBarActions, facetKey);
    const chartActions = handleChartActions(facet, facetKey, appSchema, entityType);
    return { chart, chartActions };
}

/**
 * Adds definitions for Chart section in object page sections to the app schema
 * @param {FacetConfig} facet - current facet.
 * @param {string} facetKey - facet key.
 * @param {object} appSchema - app specific schema of the object page that gets enhanced.
 * @param {object} sections - schema of current sections definition.
 * @param {EntityType} entityType - current entity type
 */
function handleSectionChart(
    facet: FacetConfig | Chart,
    facetKey: string,
    appSchema: Definition,
    sections?: Definition,
    entityType: EntityType = (facet as FacetConfig).entityType
): { sectionChart: Definition; chart: Definition; chartActions?: Definition } {
    if (sections) {
        sections.properties[facetKey] = {
            $ref: getFacetDefinitionLink(DefinitionName.ObjectPageSectionChart, facetKey)
        };
    }
    const sectionChart = parseSchemaDefinition(DefinitionName.ObjectPageSectionChart, facetKey, appSchema);
    addTargetTitleAndSectionDescription(facet as FacetConfig, sectionChart, facetKey);

    sectionChart[SchemaTag.annotationPath] = getFacetAnnotationPath(facet);
    sectionChart.properties.chart['$ref'] = getFacetDefinitionLink(DefinitionName.ObjectPageChart, facetKey);
    const { chart, chartActions } = handleChart(
        (facet as FacetConfig).target || facet,
        facetKey,
        appSchema,
        entityType
    );
    return { sectionChart, chart, chartActions };
}

/**
 * Adds definitions for presentation variants in object page sections to the app schema
 * @param {FacetConfig} facet - the given facet from the UI annotations
 * @param {string} facetKey - key of the facet, as used in annotation
 * @param {Definition} appSchema - app schema of the object page
 * @param {object} sections - schema of current sections definition.
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {SapUiAppPageV4} v4Page Current page in manifest
 */
function handleSectionPresentationVariant(
    facet: FacetConfig,
    facetKey: string,
    appSchema: Definition,
    sections: Definition,
    generateParameters: GenerateAppSchemaParameters,
    v4Page: v4.SapUiAppPageV4
) {
    let facetTarget, visualizationKey: string, target: object;
    if (!facet?.target) {
        log(generateParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOPRESENTATIONVARIANT', { key: facetKey })
        });
        return;
    }
    const visualizations = (facet?.target as PresentationVariant)?.Visualizations;
    const navPath = facetKey.lastIndexOf('::') > 0 ? facetKey.slice(0, facetKey.lastIndexOf('::')) : undefined;
    if (visualizations?.[0]) {
        if (visualizations[0].$target) {
            facetTarget = visualizations[0].$target;
            visualizationKey = `@${facetTarget.fullyQualifiedName.split('@')[1].split('/')[0]}`;
            target = facetTarget;
        } else {
            facetTarget = visualizations[0];
            visualizationKey =
                '@' +
                replaceAlias(`${facetTarget.targetString.split('@')[1].split('/')[0]}`, generateParameters.serviceAVT);
            const navigationProp = generateParameters.entityType.navigationProperties.find((prop) => {
                return prop.name === navPath;
            });
            const targetType = navigationProp.targetType;
            const visualization = visualizationKey?.split(UIVOCABULARYDOT)[1];
            target = visualization && targetType?.annotations?.UI[visualization];
        }
    }
    if (facetTarget) {
        const visualizationType = visualizationKey.split(UIVOCABULARYDOT)[1];
        const namespace = facet.target[SchemaTag.fullyQualifiedName].split('@')[0];
        const fullVisualizationKey = navPath ? `${navPath}::${visualizationKey}` : visualizationKey;

        if (visualizationType.startsWith(Visualization.Chart)) {
            sections.properties[`${facetKey}<${visualizationKey}>`] = {
                $ref: getFacetDefinitionLink(DefinitionName.ObjectPageSectionChart, fullVisualizationKey)
            };
            const { sectionChart, chart } = handleSectionChart(
                facetTarget as Chart,
                fullVisualizationKey,
                appSchema,
                undefined,
                facet.entityType
            );
            sectionChart[SchemaTag.annotationPath] = facet.annotationPath;
            addTargetTitleAndSectionDescription(facet, sectionChart, facetKey);
            chart[SchemaTag.annotationPath] = `/${namespace}/${visualizationKey}`;
            chart[SchemaTag.target] = namespace;
        } else if (visualizationType.startsWith(Visualization.LineItem)) {
            sections.properties[`${facetKey}<${visualizationKey}>`] = {
                $ref: getFacetDefinitionLink(DefinitionName.ObjectPageSectionTableV4, fullVisualizationKey)
            };
            const { sectionTable, table } = handleSectionTable(
                facet,
                fullVisualizationKey,
                appSchema,
                { target, entityType: facet.entityType } as FacetConfig,
                generateParameters,
                v4Page
            );
            sectionTable[SchemaTag.annotationPath] = facet.annotationPath;
            addTargetTitleAndSectionDescription(facet, sectionTable, facetKey);
            delete sectionTable[SchemaTag.isViewNode];
            table[SchemaTag.annotationPath] = `/${namespace}/${visualizationKey}`;
            table[SchemaTag.target] = namespace;
        }
    }
}

/**
 * Method checks if passed facet contains any collection facet.
 * @param {FacetConfig} facet - facet to check
 * @returns {boolean} Passed facet contains at least one collection facet
 */
function containsCollectionFacet(facet: FacetConfig): boolean {
    const childFacets = facet.facets || {};
    for (const id in childFacets) {
        const childFacet = childFacets[id];
        if (childFacet.base === FacetBase.CollectionFacet) {
            return true;
        }
    }
    return false;
}

/**
 * Object Page specific enhancements to addCustomColumnDefinition
 * @param {object} appSchema - app schema of the object page
 * @param {string} id - identifier of the current section facet
 * @param {string[]} featureToggles - supported features
 * @returns {string} Custom sub section reference id
 */
function createCustomSubSectionReference(appSchema: Definition, id: string, featureToggles?: string[]): string {
    const referenceId = `${DefinitionName.ObjectPageCustomSubSectionFragment}<${id}>`;
    const sourceDefinition = appSchema.definitions[DefinitionName.ObjectPageCustomSubSectionFragment];
    if (typeof sourceDefinition === 'object') {
        const relatedFacetSchema =
            typeof sourceDefinition.properties?.relatedFacet === 'object'
                ? sourceDefinition.properties.relatedFacet
                : {};
        const newRef: Definition = {
            ...sourceDefinition,
            properties: {
                ...sourceDefinition.properties,
                relatedFacet: {
                    ...relatedFacetSchema,
                    type: 'string'
                }
            }
        };
        // Building blocks
        if (isFeatureSupported(featureToggles, Features.BuildingBlocks)) {
            newRef['properties']['controls'] = {
                type: 'object'
            };
        }
        appSchema.definitions[referenceId] = newRef;
    }

    return referenceId;
}

/**
 * Common method updates schema for custom header sections or custom subsections.
 * @param {Definition} sections - sections definition
 * @param {Definition} appSchema - app schema of the object page
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {SapUiAppPageV4} v4Page Current page in manifest
 * @param {FacetConfig} facet - the given facet from the UI annotations
 * @param {string} facetKey - key of the facet, as used in annotation
 */
function applyCustomSections(
    sections: Definition,
    appSchema: Definition,
    generateParameters: GenerateAppSchemaParameters,
    v4Page: v4.SapUiAppPageV4,
    facet?: FacetConfig,
    facetKey?: string
): void {
    let isMergedSections = false;
    let customSectionRef: string;
    // facet and facetkey are used for custom subsection definition
    if (facet && facetKey) {
        isMergedSections = !containsCollectionFacet(facet);
        customSectionRef = createCustomSubSectionReference(appSchema, facetKey, generateParameters.featureToggles);
    } else {
        // else use custom header section definition
        customSectionRef = DefinitionName.ObjectPageCustomHeaderSectionFragment;
    }
    sections.additionalProperties = {
        $ref: `${DEFINITION_LINK_PREFIX}${customSectionRef}`
    };
    addCustomSectionDefinition(
        appSchema,
        sections,
        v4Page,
        customSectionRef,
        isMergedSections,
        facetKey,
        generateParameters.logger
    );
    const sectionDefinition = appSchema.definitions[customSectionRef] as Definition;
    if (typeof sectionDefinition === 'object') {
        addDescriptiveEnumDefinition(
            appSchema,
            sections,
            {
                schema: sectionDefinition,
                property: 'relatedFacet'
            },
            {
                resolveDefinition: true,
                keyProperties: [SchemaKeyName.id, SchemaKeyName.key],
                descriptionAsFallback: true
            }
        );
    }
    const relatedFacet = sectionDefinition.properties.relatedFacet;
    if (
        customSectionRef?.includes(DefinitionName.ObjectPageCustomSubSectionFragment) &&
        isMergedSections &&
        typeof relatedFacet === 'object'
    ) {
        // Exclude annotation nodes
        const section = v4Page.options?.settings?.content?.body?.sections?.[facetKey];
        const subSections = section && 'subSections' in section ? section.subSections : {};
        for (let i = relatedFacet.oneOf.length - 1; i >= 0; i--) {
            const entry = relatedFacet.oneOf[i] as { const: string; hidden?: boolean };
            if (!subSections[entry.const]) {
                entry.hidden = true;
            }
        }
    }
}

/**
 * Adds definitions for collections in object page sections to the app schema
 * @param {FacetConfig} facet - the given facet from the UI annotations
 * @param {string} facetKey - key of the facet, as used in annotation
 * @param {Definition} appSchema - app schema of the object page
 * @param {Definition} sections - schema of current sections definition.
 * @param {SectionType} sectionPrefix - prefix to distinguish Section and HeaderSection
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {SapUiAppPageV4} v4Page Current page in manifest
 * @param {number} level Collection level in tree
 */
function handleCollection(
    facet: FacetConfig,
    facetKey: string,
    appSchema: Definition,
    sections: Definition,
    sectionPrefix: SectionType,
    generateParameters: GenerateAppSchemaParameters,
    v4Page: v4.SapUiAppPageV4,
    level: number
) {
    const isCustomSubSectionsSupported = level === 0;
    const collectionFacet = (sections.properties[facetKey] = createDefaultSection(appSchema, facet, facetKey));
    const subsections: Definition = {
        // Custom subsections are allowed on first level of subsections
        additionalProperties: false,
        type: 'object',
        properties: {},
        description: 'Subsections'
    };
    collectionFacet.properties = {
        subsections
    };
    subsections[SchemaTag.annotationPath] = facet.annotationPath;
    subsections[SchemaTag.target] = facet.namespace;
    subsections[SchemaTag.keys] = [
        {
            name: SchemaKeyName.id,
            value: facet.ID
        }
    ];
    subsections[SchemaTag.isViewNode] = true;
    const nextLevel = level + 1;
    for (const key in facet.facets) {
        addSection(generateParameters, facet.facets, key, subsections, appSchema, sectionPrefix, v4Page, nextLevel);
    }
    if (isCustomSubSectionsSupported) {
        applyCustomSections(subsections, appSchema, generateParameters, v4Page, facet, facetKey);
    }
}

/**
 * Creates a section definition in app schema.
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {FacetConfigs} facets - list of all facets
 * @param {string} facetKey - facet key
 * @param {object} sections - schema of current sections definition
 * @param {object} appSchema - app specific schema of the object page that gets enhanced.
 * @param {SectionType} sectionPrefix - prefix to distinguish Section and HeaderSection
 * @param {SapUiAppPageV4} v4Page Current page in manifest
 * @param {number} [level=0] Section level in tree
 */
function addSection(
    generateParameters: GenerateAppSchemaParameters,
    facets: FacetConfigs,
    facetKey: string,
    sections: Definition,
    appSchema: Definition,
    sectionPrefix: SectionType,
    v4Page: v4.SapUiAppPageV4,
    level = 0
): void {
    const facet: FacetConfig = facets[facetKey];
    const facetDefinitionKey = `ObjectPage${sectionPrefix}${facet.base}<${getFacetKeyInSchema(facetKey)}>`;
    switch (facet.base) {
        case FacetBase.PresentationVariant: {
            handleSectionPresentationVariant(facet, facetKey, appSchema, sections, generateParameters, v4Page);
            break;
        }
        case FacetBase.CollectionFacet: {
            handleCollection(facet, facetKey, appSchema, sections, sectionPrefix, generateParameters, v4Page, level);
            break;
        }
        case FacetBase.LineItem: {
            if (sectionPrefix === SectionType.Section) {
                const lineItemFacet = facets[facetKey];
                handleSectionTable(facet, facetKey, appSchema, lineItemFacet, generateParameters, v4Page, sections);
            }
            break;
        }
        case FacetBase.Form:
        case FacetBase.Identification: {
            handleFormIdentification(
                facet,
                v4Page,
                facetKey,
                sections,
                appSchema,
                sectionPrefix,
                generateParameters.logger
            );
            break;
        }
        case FacetBase.DataPoint: {
            handleHeaderFacet(facet, facetKey, sections, appSchema, facetDefinitionKey);
            break;
        }
        case FacetBase.Chart: {
            if (sectionPrefix === SectionType.HeaderSection) {
                handleHeaderFacet(facet, facetKey, sections, appSchema, facetDefinitionKey);
            } else if (sectionPrefix === SectionType.Section) {
                handleSectionChart(facet, facetKey, appSchema, sections);
            }
            break;
        }
        case FacetBase.Address:
        case FacetBase.Contact: {
            handleAddressContact(facet, facetKey, sections, appSchema, sectionPrefix);
            break;
        }
        default: {
            sections['properties'][facetKey] = createDefaultSection(appSchema, facet, facetKey);
        }
    }
}

/**
 * Derives the sections from the UI.Facets annotation and adds them to the schema
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {object} appSchema - application specific JSON schema of the object page
 * @param {SapUiAppPageV4} v4Page - current page
 */
function addSections(
    generateParameters: GenerateAppSchemaParameters,
    appSchema: Definition,
    v4Page: v4.SapUiAppPageV4
): void {
    // Get facet annotations
    const facets: FacetConfigs = getObjectPageFacets(
        generateParameters.entityType,
        generateParameters.serviceAVT,
        FioriElementsVersion.v4,
        generateParameters.logger
    );
    if (!facets) {
        delete appSchema.definitions[DefinitionName.ObjectPageSubSections];
        return;
    }

    const sections = appSchema.definitions[DefinitionName.Sections] as Definition;

    for (const facetKey in facets) {
        addSection(generateParameters, facets, facetKey, sections, appSchema, SectionType.Section, v4Page);
    }
    // Custom sections as part of sections
    if (appSchema.definitions[DefinitionName.CustomSections]) {
        sections.properties = {
            ...sections.properties,
            ...(appSchema.definitions[DefinitionName.CustomSections] as Definition).properties
        };
    }
}

/**
 * Derives the header sections from the UI.HeaderFacets annotation and adds them to the schema
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {object} appSchema - application specific JSON schema of the object page
 * @param {EntityType} entityType - current entity type
 * @param {SapUiAppPageV4} v4Page - current page in manifest
 */
function addHeaderSections(
    generateParameters: GenerateAppSchemaParameters,
    appSchema: Definition,
    entityType: EntityType,
    v4Page: v4.SapUiAppPageV4
): void {
    // Get facet annotations
    const facets: FacetConfigs = getObjectPageHeaderFacets(
        entityType,
        generateParameters.serviceAVT,
        FioriElementsVersion.v4,
        generateParameters.logger
    );
    const sections = appSchema.definitions[DefinitionName.HeaderSections] as Definition;
    sections.properties = {};
    sections.additionalProperties = false;

    if (!facets) {
        return;
    }
    // add annotation header sections
    for (const facetKey in facets) {
        addSection(generateParameters, facets, facetKey, sections, appSchema, SectionType.HeaderSection, v4Page);
    }
    // add custom header sections
    applyCustomSections(sections, appSchema, generateParameters, v4Page);
    //delete generic definitions
    delete appSchema.definitions[DefinitionName.ObjectPageHeaderSectionForm];
    delete appSchema.definitions[DefinitionName.ObjectPageHeaderSectionChart];
    delete appSchema.definitions[DefinitionName.ObjectPageHeaderSectionDataPoint];
    delete appSchema.definitions[DefinitionName.ObjectPageHeaderSectionContact];
    delete appSchema.definitions[DefinitionName.ObjectPageHeaderSectionAddress];
}

/**
 * Adds the related facets keys 'enum' to the app schema
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {object} schema - app specific schema of the object page that gets enhanced
 * @param {SapUiAppPageV4[]} pages - list of all pages
 * @param {string} contextPath - contextPath of the actual (object) page
 */
function addRelatedFacetKeysType(
    generateParameters: GenerateAppSchemaParameters,
    schema: object,
    pages: v4.SapUiAppPageV4[],
    contextPath: string
): void {
    Object.keys(pages).forEach((element) => {
        if (
            pages[element].options &&
            pages[element].options.settings &&
            (pages[element].options.settings.entitySet === generateParameters?.entitySet?.name ||
                (contextPath && pages[element].options.settings.contextPath === contextPath)) &&
            pages[element].name === v4.FE_TEMPLATE_V4_OBJECT_PAGE
        ) {
            // Find sections from annotation
            const facetSections: FacetSection[] = getObjectPageFacetSection(
                generateParameters.entityType,
                generateParameters.serviceAVT,
                generateParameters.logger
            );
            // Find custom sections ids from manifest
            const page: v4.SapUiAppPageV4 = pages[element];
            const customSections = page.options?.settings?.content?.body?.sections;
            if (customSections) {
                for (const key in customSections) {
                    const customSection = customSections[key];
                    // Avoid duplications
                    if (!facetSections.some((section: FacetSection) => section.key === key)) {
                        facetSections.push({
                            key,
                            title: customSection['title'],
                            custom: true
                        });
                    }
                }
            }
            // Update schema definition
            addDefinitionForRelatedFacetKeys(schema, [DefinitionName.ObjectPageCustomSectionFragment], facetSections, [
                'ID',
                'label',
                'key'
            ]);
        }
    });
}

/**
 * Add Object Page Header to app-specific schema
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {Definition} appSchema Schema of the object page
 * @param {SapUiAppPageV4} v4Page - current page in manifest
 */
function addHeader(
    generateParameters: GenerateAppSchemaParameters,
    appSchema: Definition,
    v4Page: v4.SapUiAppPageV4
): void {
    const entityType = generateParameters.entityType;
    addCommonHeaderSchema(appSchema, entityType);

    // Add header actions
    addHeaderActions(
        appSchema,
        entityType,
        generateParameters.entitySet,
        generateParameters.serviceAVT,
        addHeaderActionCallBackV4
    );

    // Add custom header actions
    addHeaderFooterCustomActionsOP(appSchema, v4Page, DefinitionName.CustomHeaderActionOP, generateParameters.logger);

    // Add header sections
    if (entityType) {
        appSchema.definitions[DefinitionName.HeaderSections][SchemaTag.annotationPath] =
            `/${entityType.fullyQualifiedName}/@${UIAnnotationTerms.HeaderFacets}`;
    }
    addHeaderSections(generateParameters, appSchema, entityType, v4Page);
}

/**
 * Generates an app specific schema out of the generic schema.
 * Generic types are replaced by information from the app specific annotations.
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {object} genericSchema  - generic JSON schema of an object page
 * @param {string} contextPath - contextPath of the given page
 * @param {object} genericMacrosSchema - generic JSON schema of custom page
 *
 * @returns the app specific JSON schema
 */
export function generateObjectPageSchemaV4(
    generateParameters: GenerateAppSchemaParameters,
    genericSchema: object,
    contextPath: string,
    genericMacrosSchema?: object
): object {
    const appSchema: Definition = JSON.parse(JSON.stringify(genericSchema));

    // Change reference to generated sections
    appSchema.properties.sections = {
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.Sections}`
    };
    if (
        !generateParameters.manifest[ManifestSection.ui5].routing ||
        !generateParameters.manifest[ManifestSection.ui5].routing.targets
    ) {
        log(generateParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOROUTING'),
            location: {
                path: MANIFESTPATH
            }
        });
        return appSchema;
    }

    //initialize sections
    const sections = (appSchema.definitions[DefinitionName.Sections] = {
        type: 'object',
        properties: {},
        additionalProperties: false,
        description: DefinitionName.Sections
    } as Definition);
    sections[SchemaTag.isViewNode] = true;
    if (generateParameters.entityType) {
        const pages = generateParameters.manifest[ManifestSection.ui5].routing
            .targets as unknown as v4.SapUiAppPageV4[];
        if (!pages) {
            log(generateParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOPAGES', { appId: generateParameters.manifest['sap.app']['id'] }),
                location: {
                    path: MANIFESTPATH,
                    range: [ManifestSection.generic]
                }
            });
            return appSchema;
        }
        const v4Page = findPageV4(pages, v4.FE_TEMPLATE_V4_OBJECT_PAGE, generateParameters.entitySet, contextPath);
        if (!v4Page) {
            return appSchema;
        }

        // Add Header
        addHeader(generateParameters, appSchema, v4Page);

        // Add sections
        sections[SchemaTag.isViewNode] = true;
        if (generateParameters.entityType) {
            sections[SchemaTag.annotationPath] =
                `/${generateParameters.entityType.fullyQualifiedName}/@${UIAnnotationTerms.Facets}`;
        }
        addSections(generateParameters, appSchema, v4Page);

        // Custom Section facets
        addRelatedFacetKeysType(generateParameters, appSchema, pages, contextPath);

        // Custom extension fragments
        if (isFeatureSupported(generateParameters.featureToggles, Features.BuildingBlocks) && genericMacrosSchema) {
            appSchema['definitions'][DefinitionName.ObjectPageCustomSectionFragment]['properties']['controls'] = {
                type: 'object'
            };
            try {
                resolveFragments(appSchema, genericMacrosSchema, generateParameters, v4Page.id);
            } catch (error) {
                log(generateParameters.logger, {
                    severity: LogSeverity.Error,
                    message: error
                });
            }
        }

        // Add Footer (actions only)
        addFooterActions(appSchema, generateParameters, FioriElementsVersion.v4, addFooterActionCallBackV4);
        addHeaderFooterCustomActionsOP(
            appSchema,
            v4Page,
            DefinitionName.CustomFooterActionOP,
            generateParameters.logger
        );
    }
    // Delete generic definitions from schema
    delete appSchema.definitions[DefinitionName.CommonHeaderFacetSettings];
    delete appSchema.definitions[DefinitionName.GenericColumnsOP];
    delete appSchema.definitions[DefinitionName.GenericSections];
    delete appSchema.definitions[DefinitionName.GenericSubSections];
    delete appSchema.definitions[DefinitionName.ObjectPageChart];
    delete appSchema.definitions[DefinitionName.ObjectPageSectionAddress];
    delete appSchema.definitions[DefinitionName.ObjectPageSectionChart];
    delete appSchema.definitions[DefinitionName.ObjectPageSectionContact];
    delete appSchema.definitions[DefinitionName.ObjectPageSectionDataPoint];
    delete appSchema.definitions[DefinitionName.ObjectPageSectionForm];
    delete appSchema.definitions[DefinitionName.ObjectPageSectionTableV4];
    delete appSchema.definitions[DefinitionName.ObjectPageSectionPresentationVariant];
    delete appSchema.definitions[DefinitionName.ObjectPageSubSections];
    delete appSchema.definitions[DefinitionName.ObjectPageTable];
    delete appSchema.definitions[DefinitionName.ObjectPageToolBar];
    delete appSchema.definitions[DefinitionName.ObjectPageToolBarAction];
    delete appSchema.definitions[DefinitionName.ObjectPageToolBarActions];
    delete appSchema.definitions[DefinitionName.QuickVariantSelectionV4OP];
    updatePropertyIndices(appSchema, ROOT_PROPERTIES_ORDER);
    return appSchema;
}
