import type {
    ExtensionLogger,
    Application,
    CustomPageConfig,
    Files,
    Manifest,
    PagesBase,
    v2,
    v4,
    PageConfig
} from '@sap/ux-specification-types';
import {
    DirName,
    FileName,
    FioriElementsVersion,
    OdataVersion,
    ViewTypes,
    PageTypeV4,
    SchemaType
} from '@sap/ux-specification-types';
import { getSchemaFilePath } from './utils';

/**
 * Splits and returns the name of a custom page
 * @param {v4.Page|v2.Page} page - custom page
 * @returns {string} custom page name
 */
function getCustomPageName(page: v2.Page | v4.Page): string {
    const targetName = (page as v4.Page).view.name?.split('.');
    return targetName[targetName.length - 1];
}

/**
 * Returns the view type of a custom page
 * @param {v4.Page|v2.Page} page - custom page
 * @param {Application} app - application
 * @returns {string} view type
 */
function getViewType(page: v2.Page | v4.Page, app: Application): ViewTypes {
    const viewType = (page as v4.Page)?.view?.viewType || (app.settings as v4.AppSettings)?.viewType;
    return viewType || ViewTypes.XML;
}

/**
 * Returns file extension name for the page view type of a custom page
 * @param {v4.Page|v2.Page} page - custom page
 * @param {Application} app - application
 * @returns {string} File extension name of page view type
 */
function getViewTypeExtension(page: v2.Page | v4.Page, app: Application): string {
    return getViewType(page, app).toLowerCase();
}

/**
 * Returns the view path and file name of the custom page
 * @param {v4.Page|v2.Page} page - custom page
 * @param {Application} app - application
 * @param {string} viewName - name of view
 * @returns {string} view path
 */
function getViewPath(
    page: v4.Page | v2.Page,
    app: Application,
    viewPath: string[]
): { viewPath: string[]; fileName: string } {
    const splitViewPath = (splitPath: string): string[] => {
        return splitPath.split(`${app.id}.`)[1]?.split('.');
    };
    const appSettings = app.settings as v4.AppSettings;
    const appPath = (page as v4.Page).view?.path || appSettings?.path;
    const pageName = (page as v4.Page).view?.name;
    if (pageName?.includes(app.id)) {
        viewPath = splitViewPath(pageName);
    } else if (appPath?.includes(app.id)) {
        viewPath = [...splitViewPath(appPath), ...pageName.split('.')];
    }
    const [pathParts, fileName] = [viewPath.slice(0, -1), ...viewPath.slice(-1)];
    return { viewPath: pathParts, fileName };
}

/**
 * Returns the page name and path of a custom page
 * @param {v4.Page|v2.Page} page - custom page
 * @param {string} appId - Application id
 * @returns page name
 */
function getCustomPagePath(page: v2.Page | v4.Page, app: Application): string {
    const viewName = page.entitySet || getCustomPageName(page);
    const { viewPath, fileName } = getViewPath(page, app, [viewName]);
    const viewType = getViewTypeExtension(page, app);
    // we always use '/' as separator so that snapshot tests generated from Windows do not fail on Jenkins
    return [...viewPath, `${fileName}.view.${viewType}`].join('/');
}

export interface Controllers {
    [key: string]: string;
}

export abstract class AppProvider {
    public readonly app: Application;
    public readonly logger: ExtensionLogger;
    public readonly schemas: Files;
    // Store controller names which should be handled lately
    public unhandledControllers: Array<string> = [];

    protected constructor(
        manifest: Manifest,
        schemas: Files,
        home: string,
        pages: PagesBase,
        version: FioriElementsVersion,
        settings?: v2.AppSettings | v4.AppSettings,
        logger?: ExtensionLogger
    ) {
        const schemaFile = getSchemaFilePath(SchemaType.Application);
        this.app = {
            $schema: schemaFile.path,
            id: manifest['sap.app'].id,
            pages,
            home,
            target: {
                fioriElements: version,
                odata: version === FioriElementsVersion.v2 ? OdataVersion.v2 : OdataVersion.v4
            },
            ...(settings && { settings })
        };
        this.schemas = schemas;
        this.logger = logger;
    }

    /**
     * Create config files for app (app.json) and pages (pages/{name}.json).
     * This is basically a copy of createFiles optimized to be used in FTFS:
     * it always uses posix path notation with forward slash (/)
     * @param {ConvertedMetadata} oDataServiceAVT - Complete service information, as returned by annotation vocabularies tool
     * @returns app and page files as separate objects
     */
    public createConfigFiles(): { appConfig: Files; pageConfigs: Files } {
        const pageConfigs: Files = {};
        const appConfig: Files = {};
        let page: v2.Page | v4.PageV4;
        // add schema reference to all pages
        for (const pageId in this.app.pages) {
            page = this.app.pages[pageId];
            const schemaSuffix = this.app.target.odata === OdataVersion.v4 ? pageId : page.entitySet;
            const config = this.getPageConfigFile(page, schemaSuffix);
            if (config) {
                pageConfigs[`${DirName.Pages}/${pageId}.json`] = config;
            }
        }
        // remove config before converting to JSON
        const app: Application = JSON.parse(JSON.stringify(this.app));
        for (const pageId in app.pages) {
            delete app.pages[pageId].config;
        }
        appConfig[FileName.App] = app;

        return { appConfig, pageConfigs };
    }

    /**
     * Method returns page's config object to store in file for passed page.
     * @param {v2.Page | v4.Page} page Page from 'app.json'.
     * @param {PageConfig | undefined} config Page's config object.
     * @param {pageId} id of the page or entityset in case of V2
     */
    private getPageConfigFile(page: v2.Page | v4.Page, pageId: string): PageConfig | undefined {
        let config: PageConfig | undefined;
        const isEditableXML = getViewType(page, this.app) === ViewTypes.XML && !!page.config;
        if (page.pageType === PageTypeV4.CustomPage || (page.pageType === PageTypeV4.FPMCustomPage && !isEditableXML)) {
            const { path: $schema } = getSchemaFilePath(page.pageType);
            config = { $schema };
        } else if (page.config) {
            config = page.config;
            const viewId = 'view' in page ? page.view?.id : undefined;
            const schemaFile = getSchemaFilePath(page.pageType, pageId, viewId);
            if (this.schemas[schemaFile.filename]) {
                config.$schema = schemaFile.path;
            } else {
                const genericSchemaFile = getSchemaFilePath(page.pageType);
                config.$schema = genericSchemaFile.path;
            }
        }
        if ((config && page.pageType === PageTypeV4.CustomPage) || page.pageType === PageTypeV4.FPMCustomPage) {
            this.applyCustomPageFilePath(page, config);
        }
        return config;
    }

    /**
     * Method updates "$filePath" property for custom page's config object.
     * @param {v4.Page} page Custom page from 'app.json'.
     * @param {CustomPageConfig} config Custom page's config object.
     */
    private applyCustomPageFilePath(page: v4.Page, config: CustomPageConfig): void {
        const $filePath = getCustomPagePath(page, this.app);
        config.$filePath = $filePath;
    }
}
