import type { Definition } from 'typescript-json-schema';
import type { ConvertedMetadata, EntitySet, EntityType } from '@sap-ux/vocabularies-types';
import type { Editor } from 'mem-fs-editor';
import type {
    CustomPage as FPMCustomPage,
    CustomTableColumn as FPMCustomTableColumn,
    CustomSection as FPMCustomSection,
    ObjectPage as FPMObjectPage,
    CustomAction as FPMCustomAction,
    CustomView as FPMCustomView,
    ControllerExtension as FPMControllerExtension,
    CustomSubSection as FPMCustomSubSection,
    CustomHeaderSection as FPMCustomHeaderSection,
    CustomFilter as FPMCustomFilter
} from '@sap-ux/fe-fpm-writer';
import type { FioriElementsVersion, Manifest, v2, v4, File } from '.';
