import type { Manifest, v4 } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { addPatternForBindingChangeOfEnumOPV4 } from '../../../common/decoration/control';
import { getHeaderActionsPath } from '../../application';
import { CustomHeaderActionPosition } from './HeaderAction';

export class ActionAfterExecutionConfigurationObjectPageHeader
    implements v4.ActionAfterExecutionConfigurationObjectPageHeader
{
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey): string =>
                getHeaderActionsPath(pageName, manifest, sectionId, columnKey) + '/afterExecution'
        }
    })
    navigateToInstance?: boolean;

    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey): string =>
                getHeaderActionsPath(pageName, manifest, sectionId, columnKey) + '/afterExecution'
        }
    })
    enableAutoScroll?: boolean;
}

export class ObjectPageHeaderAction implements v4.ObjectPageHeaderAction {
    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        }
    })
    @validity({
        since: '1.84.0'
    })
    afterExecution?: ActionAfterExecutionConfigurationObjectPageHeader;
}

/**
 * Sync class for Position
 */

export class CustomHeaderActionPositionOP extends CustomHeaderActionPosition {}

export class CustomHeaderActionOP implements v4.CustomHeaderActionOP {
    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        }
    })
    @validity({
        since: '1.84.0'
    })
    text: string;

    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        }
    })
    @validity({
        since: '1.84.0'
    })
    position: CustomHeaderActionPositionOP;

    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        }
    })
    @validity({
        since: '1.84.0'
    })
    press: string;

    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        },
        generate: addPatternForBindingChangeOfEnumOPV4
    })
    @validity({
        since: '1.84.0'
    })
    visible?: boolean;

    @syncRule({
        manifest: {
            path: getHeaderActionsPath
        },
        generate: addPatternForBindingChangeOfEnumOPV4
    })
    @validity({
        since: '1.84.0'
    })
    enabled?: boolean;
}
