import {
    addLineItemsType,
    addSelectionFields,
    addTableColumnExtensions,
    addEntityPropertiesToEnum,
    addStandardHeaderActions
} from './utils';
import { findListReportPageV2 } from '../import';
import {
    DEFINITION_LINK_PREFIX,
    addFieldsType,
    addEnumForSingleTabVariant,
    addEnumForMultiTabVariant,
    addEnumForVariantEntitySet,
    addEnumForFilterBarAnnotationPath,
    addEnumFieldGroupAnnotationPath,
    updatePropertyIndices,
    createAnnotationPath
} from '../../common';
import i18next from 'i18next';
import { ManifestSection, DefinitionName, LogSeverity, MANIFESTPATH, SchemaTag } from '@sap/ux-specification-types';
import type { v2 } from '@sap/ux-specification-types';
import { log } from '../../../extensionLogger';
import {
    ANALYTICALTABLE,
    GRIDTABLE,
    INLINEDELETETABLE,
    MULTISELECTTABLE,
    TREETABLE,
    LINEITEMTABLETYPES
} from '../types';
import type { ConvertedMetadata, EntitySet, EntityType } from '@sap-ux/vocabularies-types';
import type { Definition } from 'typescript-json-schema';
import type { GenerateAppSchemaParameters } from '@sap/ux-specification-types';

const ROOT_PROPERTIES_ORDER: Array<keyof v2.ListReportConfigV2> = ['filterBar', 'table', 'footer'];

/**
 * Adds the enum types for QuickVariant and QuickVariantX, based on the entity type annotations
 * @param {EntityType} entityType
 * @param {EntitySet} entitySet - current entity set of the page or view
 * @param {Definition} appSchema - app specific JSON schema
 * @param {ConvertedMetadata} oDataServiceAVT - combined service metadata, as returned by annotation vocabularies tools
 */
export function addEnumForVariantPaths(
    entityType: EntityType,
    entitySet: EntitySet,
    appSchema: Definition,
    oDataServiceAVT: ConvertedMetadata
): void {
    addEnumForSingleTabVariant(entityType, appSchema, 'QuickVariant');
    addEnumForMultiTabVariant(appSchema, 'QuickVariantX', entitySet);
    addEnumForVariantEntitySet(oDataServiceAVT, appSchema, 'QuickVariantX');
}

/**
 * Generates an app specific schema for the FE V2 Listreport from the generic schema.
 * Generic types are replaced by information from the app specific annotations.
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {object} genericSchema - generic JSON schema of a list report
 * @returns appSchema - the application specific JSON schema
 */
export function generateListReportSchemaV2(
    generateParameters: GenerateAppSchemaParameters,
    genericSchema: Definition
): object {
    const appSchema = JSON.parse(JSON.stringify(genericSchema)) as Definition;

    appSchema.properties.table['anyOf']['0']['$ref'] = DEFINITION_LINK_PREFIX + MULTISELECTTABLE;
    appSchema.properties.table['anyOf']['1']['$ref'] = DEFINITION_LINK_PREFIX + INLINEDELETETABLE;
    appSchema.properties.table['anyOf']['2']['$ref'] = DEFINITION_LINK_PREFIX + TREETABLE;
    appSchema.properties.table['anyOf']['3']['$ref'] = DEFINITION_LINK_PREFIX + ANALYTICALTABLE;
    appSchema.properties.table['anyOf']['4']['$ref'] = DEFINITION_LINK_PREFIX + GRIDTABLE;

    const responsiveTableMultiSelect = JSON.parse(
        JSON.stringify(genericSchema.definitions.ResponsiveTableWithMultiSelect)
    ) as Definition;
    const responsiveTableInlineDelete = JSON.parse(
        JSON.stringify(genericSchema.definitions.ResponsiveTableWithInlineDelete)
    );
    const treeTableDefinition = JSON.parse(JSON.stringify(genericSchema.definitions.TreeTable));
    const analyticalTableDefinition = JSON.parse(JSON.stringify(genericSchema.definitions.AnalyticalTable));
    const gridTableDefinition = JSON.parse(JSON.stringify(genericSchema.definitions.GridTable));

    const lrLineItems = {
        $ref: DEFINITION_LINK_PREFIX + DefinitionName.LineItems
    };
    responsiveTableMultiSelect.properties.columns = lrLineItems;
    responsiveTableInlineDelete.properties.columns = lrLineItems;
    treeTableDefinition.properties.columns = lrLineItems;
    analyticalTableDefinition.properties.columns = lrLineItems;
    gridTableDefinition.properties.columns = lrLineItems;

    const lrToolBar = DEFINITION_LINK_PREFIX + DefinitionName.ToolBarLR;
    appSchema.definitions[MULTISELECTTABLE] = responsiveTableMultiSelect;
    appSchema.definitions[INLINEDELETETABLE] = responsiveTableInlineDelete;
    appSchema.definitions[TREETABLE] = treeTableDefinition;
    appSchema.definitions[ANALYTICALTABLE] = analyticalTableDefinition;
    appSchema.definitions[GRIDTABLE] = gridTableDefinition;

    LINEITEMTABLETYPES.forEach((tableType) => {
        (appSchema.definitions[tableType] as Definition).properties.toolBar['$ref'] = lrToolBar;
    });

    const entityType = generateParameters.entitySet?.entityType;
    if (!entityType) {
        if (generateParameters.entitySet?.name) {
            log(generateParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOENTITYTYPE4SET', { entitySetName: generateParameters.entitySet?.name })
            });
        }
    } else {
        // handle standard header actions
        addStandardHeaderActions(appSchema);
        //handle line items
        const lineItemAnnotation = entityType?.annotations?.UI?.LineItem;
        if (lineItemAnnotation) {
            LINEITEMTABLETYPES.forEach((tableType) => {
                appSchema.definitions[tableType][SchemaTag.annotationPath] = createAnnotationPath(
                    entityType?.fullyQualifiedName,
                    lineItemAnnotation.term,
                    lineItemAnnotation.qualifier
                );
            });
        }
        addLineItemsType(appSchema, lineItemAnnotation, entityType);
        //handle filter fields
        const selectionFieldAnnotation = entityType?.annotations?.UI?.SelectionFields;
        addSelectionFields(appSchema, selectionFieldAnnotation, entityType);

        //add fields for createWithParameterDialog
        addFieldsType(appSchema, entityType);

        // add entityProperties to a enum, in this case MultiEdit -> ignoredFields
        (appSchema.definitions.MultiEdit as Definition).properties.ignoredFields['items']['enum'] = [];
        addEntityPropertiesToEnum(
            (appSchema.definitions.MultiEdit as Definition).properties.ignoredFields['items']['enum'],
            entityType
        );

        const pageKeys = [];
        const v2Page = findListReportPageV2(
            generateParameters.manifest[ManifestSection.generic].pages as v2.SapUiAppPagesV2,
            pageKeys
        );
        if (!v2Page) {
            log(generateParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOLR'),
                location: {
                    path: MANIFESTPATH,
                    range: [ManifestSection.generic]
                }
            });
            return appSchema;
        }
        addTableColumnExtensions(
            appSchema,
            entityType,
            v2Page,
            generateParameters.manifest,
            generateParameters.fragments
        );

        addEnumForVariantPaths(entityType, generateParameters.entitySet, appSchema, generateParameters.serviceAVT);
        addEnumForFilterBarAnnotationPath(entityType, appSchema, DefinitionName.ListReportFilterBar);
        addEnumFieldGroupAnnotationPath(entityType, appSchema, DefinitionName.MultiEditV2);

        //Delete generic definitions
        delete appSchema.definitions.ResponsiveTableWithMultiSelect;
        delete appSchema.definitions.ResponsiveTableWithInlineDelete;
        delete appSchema.definitions.TreeTable;
        delete appSchema.definitions.AnalyticalTable;
        delete appSchema.definitions.GridTable;
        delete appSchema.definitions.GenericColumns;
        delete appSchema.definitions.TableColumnAction;
        delete appSchema.definitions.ToolBar;
        delete appSchema.definitions.Actions;
        delete appSchema.definitions.GenericFooter;
        delete appSchema.definitions.FieldPath;
        delete appSchema.definitions.Share;
    }
    updatePropertyIndices(appSchema, ROOT_PROPERTIES_ORDER);
    return appSchema;
}
