const ROOT_PROPERTIES_ORDER: string[] = ['header', 'sections', 'footer'];

/**
 * Adds flexChange properties for sub(sections)
 * @param appSchema - app schema in general
 * @param schemaSection - current section
 * @param property - property that will be enhanced
 * @param sectionType - type of section
 */
function addFlexChangeForSection(
    appSchema: Definition,
    schemaSection: Definition,
    property: string,
    sectionType: SectionTypeV2
) {
    schemaSection.properties[property][SchemaTag.artifactType] = ArtifactType.FlexChange;
    let controlType: ControlType;
    switch (sectionType) {
        case SectionTypeV2.Section:
        case SectionTypeV2.SectionForm:
        case SectionTypeV2.SectionChart:
        case SectionTypeV2.SectionTable:
            controlType = ControlType.Section;
            break;
        case SectionTypeV2.SubSection:
            controlType = ControlType.SubSection;
            break;
        case SectionTypeV2.SubSectionForm:
            controlType = ControlType.Group;
            break;
        case SectionTypeV2.SubSectionChart:
            controlType = ControlType.SmartChart;
            break;
        case SectionTypeV2.SubSectionTable:
            controlType = ControlType.SmartTable;
            break;
        case SectionTypeV2.HeaderSection:
        case SectionTypeV2.HeaderSectionAddress:
        case SectionTypeV2.HeaderSectionChart:
        case SectionTypeV2.HeaderSectionDataPoint:
        case SectionTypeV2.HeaderSectionForm:
            controlType = ControlType.ObjectPageHeaderSection;
            break;
    }
    if (!controlType) {
        return;
    }
    addPatternForBindingChangeOfEnumOP(appSchema, schemaSection, property);
    schemaSection.properties[property][SchemaTag.controlType] = controlType;
}

/**
 * Callback for adding a single action to headerActions of V2
 * @param {EntityType} entityType - current entity type
 * @param {object} appSchema Schema of the app
 * @param actionAnnotation - annotation as present in AVT ConvertedMetadata
 * @param headerActions - list of header actions, to be enhanced
 */
function addHeaderActionCallBackV2(
    entityType,
    appSchema: Definition,
    identification: any,
    headerActions: {
        type: string;
        properties: {};
        description: string;
        additionalProperties: boolean;
        isViewNode: boolean;
    },
    oDataServiceAVT: ConvertedMetadata
): any {
    identification?.forEach((item) => {
        if (!item['Determining'] && item.$Type === UIAnnotationTypes.DataFieldForAction) {
            const { actionName, actionId, namespace } = getActionNameAndId(item, oDataServiceAVT);
            //actionId is used as key in V2, as this is the right part for building the full stable ID
            if (!actionId) {
                return;
            }
            const description = item.Label || actionName;
            const actionDefinitionKey = `ObjectPageHeaderAction<${prepareRef(actionId)}>`;
            appSchema.definitions[actionDefinitionKey] = JSON.parse(
                JSON.stringify(appSchema.definitions['ObjectPageHeaderAction'])
            );
            const convertedIdentification = item.fullyQualifiedName.replace(
                `@${UIAnnotationTerms.Identification}`,
                `/@${UIAnnotationTerms.Identification}`
            );
            appSchema.definitions[actionDefinitionKey][SchemaTag.annotationPath] = `/${convertedIdentification}`;
            (appSchema.definitions[actionDefinitionKey] as Definition)[SchemaTag.actionType] = ActionType.Annotation;
            (appSchema.definitions[actionDefinitionKey] as Definition).description = description;
            appSchema.definitions[actionDefinitionKey][SchemaTag.keys] = [
                { name: SchemaKeyName.action, value: actionName }
            ];
            if (namespace) {
                appSchema.definitions[actionDefinitionKey][SchemaTag.target] = namespace;
            }

            headerActions.properties[actionId] = {
                $ref: `${DEFINITION_LINK_PREFIX}${actionDefinitionKey}`
            };
        }
    });
}

/**
 * Callback for adding a single action to footerActions of V2
 * @param {EntityType} entityType - current entity type
 * @param {Definition} appSchema Schema of the app
 * @param {Definition} footerActions - list of footer actions, to be enhanced
 * @param {String} identification - converted UI.Identification term
 */
function addFooterActionCallBackV2(
    entityType,
    appSchema: Definition,
    footerActions: {
        type: string;
        properties: {};
        description: string;
        additionalProperties: boolean;
        isViewNode: boolean;
    },
    identification
): any {
    identification.forEach((item) => {
        if (item['Determining'] && item.$Type === UIAnnotationTypes.DataFieldForAction) {
            const actionId = getStableIdPartFromDataField(item).split('::').splice(1).join('::');
            const description = getDataFieldDescription(item, entityType);
            const actionDefinitionKey = prepareRef(`${DefinitionName.ObjectPageFooterAction}<${actionId}>`);
            const convertedIdentification = item.fullyQualifiedName.replace(
                `@${UIAnnotationTerms.Identification}`,
                `/@${UIAnnotationTerms.Identification}`
            );
            appSchema.definitions[actionDefinitionKey] = {
                ...JSON.parse(JSON.stringify(appSchema.definitions[DefinitionName.ObjectPageFooterAction])),
                description,
                annotationPath: `/${convertedIdentification}`
            };

            footerActions.properties[actionId] = {
                $ref: `${DEFINITION_LINK_PREFIX}${actionDefinitionKey}`
            };
        }
    });
}

/**
 * Method ensures that definitions for custom columns are created for passed entitySet.
 * @param {object} appSchema App schema in general
 * @param {object} manifest manifest.json of the app
 * @param {string} entitySetName Entity set name.
 * @param {string} entityTypeName Entity type name.
 * @param {ConvertedMetadata} oDataServiceAVT Complete service information, as returned by annotation vocabularies tool
 * @param {FacetConfig} facet information
 * @param {string} facetKey - key of the facet, as listed in FacetConfigs
 * @param {FileData[]} [fragments] Array with XML fragments.
 * @return {string} Name of custom columns definition.
 */
function ensureCustomColumnDefinitionExists(
    appSchema: Definition,
    manifest: object,
    entitySetName: string,
    entityTypeName: string,
    oDataServiceAVT: ConvertedMetadata,
    facet: FacetConfig,
    facetKey: string,
    fragments?: FileData[]
): string {
    let suffix, customColumnsDefinitionName;
    if (facet.ID && facet.Label) {
        suffix = facet.ID + facet.Label.replace(/\s+/g, '');
    } else if (facet.ID) {
        suffix = facet.ID;
    } else if (facet.Label) {
        suffix = facet.Label.replace(/\s+/g, '');
    }
    if (suffix) {
        customColumnsDefinitionName = `${DefinitionName.CustomColumns}<${entitySetName}><${facetKey}><${suffix}>`;
    } else {
        customColumnsDefinitionName = `${DefinitionName.CustomColumns}<${entitySetName}><${facetKey}>`;
    }

    if (appSchema.definitions[customColumnsDefinitionName]) {
        // Definition is already created - we do not need to create it again
        return customColumnsDefinitionName;
    }
    // Create type specific 'TableCustomColumn' definition - use entity set as type.
    const customColumnDefinitionName = `${DefinitionName.CustomColumn}<${entitySetName}>`;
    const customColumnDefinition = JSON.parse(JSON.stringify(appSchema.definitions['TableCustomColumn']));
    appSchema.definitions[customColumnDefinitionName] = customColumnDefinition;
    // Update definitions for type specific custom columns
    const pageKeys = [];
    const entityType = oDataServiceAVT?.entityTypes.find((et) => et.name === entityTypeName);
    const v2Page = findObjectPageV2(manifest[ManifestSection.generic].pages, entitySetName, pageKeys);
    addTableColumnExtensions(
        appSchema,
        entityType,
        v2Page,
        manifest,
        fragments,
        customColumnsDefinitionName,
        customColumnDefinitionName,
        facet.ID
    );

    return customColumnsDefinitionName;
}

/**
 * Method fills new app schema section with properties.
 * @param {Definition} appSchema App schema in general
 * @param {object} appSchemaSection app schema section to be filled
 * @param {SectionTypeV2} sectionType (sub)section type
 * @param {SectionTypeV2} mainSectionType main section type, used to fill app schema section
 */
function addAppSchemaDefinition(
    appSchema: Definition,
    appSchemaSection: Definition,
    sectionType: SectionTypeV2,
    mainSectionType: SectionTypeV2
): void {
    appSchemaSection.properties = JSON.parse(
        JSON.stringify((appSchema.definitions[mainSectionType] as Definition).properties)
    );
    if (appSchemaSection.properties.visible) {
        addFlexChangeForSection(appSchema, appSchemaSection, 'visible', sectionType);
    }
}

/**
 * Method adds reference to section property key.
 * @param {object} sections - schema of current sections definition
 * @param {string} key section key which will contain the reference
 * @param {string} definitionKey key which the section should point to
 */
function addSectionReference(sections: Definition, key: string, definitionKey: string) {
    sections.properties[key] = { $ref: `${DEFINITION_LINK_PREFIX}${definitionKey}` };
}

/**
 * Reuse function for all table types, copies and adapts the generic definition
 * @param tableType - Table type
 * @param facetId - facet ID, as derived for the given section
 * @param facet - facet or section information
 * @param appSchema - app-specific schema, to be updated
 */
function addTableDefinition(tableType: string, facetId: string, facet: FacetConfig, appSchema: Definition): void {
    const schemaId = prepareRef(`${tableType}<${facetId}>`);
    appSchema.definitions[schemaId] = JSON.parse(JSON.stringify(appSchema.definitions[tableType]));
    appSchema.definitions[schemaId]['properties']['columns']['$ref'] = DEFINITION_LINK_PREFIX + facetId;
    (appSchema.definitions[schemaId] as Definition).properties.toolBar['$ref'] =
        `${DEFINITION_LINK_PREFIX}ObjectPageToolBar<${facetId}>`;
    appSchema.definitions[schemaId][SchemaTag.annotationPath] = facet.annotationPath;
    if (facet.ID !== undefined) {
        (appSchema.definitions[schemaId] as Definition).title = FacetTitlePrefix + facet.ID;
        appSchema.definitions[schemaId][SchemaTag.keys] = [{ name: `ID`, value: facet.ID }];
    }
    (appSchema.definitions[schemaId] as Definition).description = DefinitionName.Table;

    //adjust quickVariantSelection
    (appSchema.definitions[schemaId] as Definition).properties['quickVariantSelection']['$ref'] =
        `${DEFINITION_LINK_PREFIX}QuickVariantSelectionOP<${facetId}>`;
    appSchema.definitions[`QuickVariantSelectionOP<${facetId}>`] = JSON.parse(
        JSON.stringify(appSchema.definitions['QuickVariantSelectionOP'])
    );
    appSchema.definitions[`QuickVariant<${facetId}>`] = JSON.parse(
        JSON.stringify(appSchema.definitions['QuickVariant'])
    );
    (
        (appSchema.definitions[`QuickVariantSelectionOP<${facetId}>`] as Definition).properties.variants as Definition
    ).patternProperties['^[0-9]+$']['$ref'] = `${DEFINITION_LINK_PREFIX}QuickVariant<${facetId}>`;

    addEnumForSingleTabVariant(facet.entityType, appSchema, `QuickVariant<${facetId}>`);
}

/**
 * Adds definitions for line items in object page sections to the app schema
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {string} facetKey - key of the facet, as listed in FacetConfigs
 * @param {Definition} appSchema - app schema in general
 * @param {FacetConfig} facet - the given facet from the UI annotations
 * @param {FacetConfigs} facets - list of all facets
 * @param {SectionTypeV2} sectionType (sub)section type
 */
function handleLineItem(
    generateParameters: GenerateAppSchemaParameters,
    facetKey: string,
    appSchema: Definition,
    facet: FacetConfig,
    facets: FacetConfigs,
    sectionType?: SectionTypeV2
): void {
    if (facet.base !== FacetBase.LineItem || !facetKey) {
        //no properties
        return;
    }
    const navigationProperty = facetKey.split('::')[0];
    const schemaIdForOpSection = `${sectionType}<${facetKey}>`;

    appSchema.definitions[schemaIdForOpSection] = createDefaultSection(appSchema, facet, facetKey);
    const appSchemaSection = appSchema.definitions[schemaIdForOpSection] as Definition;
    addAppSchemaDefinition(appSchema, appSchemaSection, sectionType, SectionTypeV2.SectionTable);
    appSchemaSection.properties.table = JSON.parse(
        JSON.stringify(appSchema.definitions[SectionTypeV2.SectionTable]['properties'][PropertyName.table])
    );
    (appSchemaSection.properties.table as Definition).anyOf = [
        {
            $ref: `${DEFINITION_LINK_PREFIX}ObjectPageResponsiveTableWithMultiSelect<${facetKey}>`
        },
        {
            $ref: `${DEFINITION_LINK_PREFIX}ObjectPageResponsiveTableWithInlineDelete<${facetKey}>`
        },
        {
            $ref: `${DEFINITION_LINK_PREFIX}ObjectPageAnalyticalTable<${facetKey}>`
        },
        {
            $ref: `${DEFINITION_LINK_PREFIX}ObjectPageGridTable<${facetKey}>`
        },
        {
            $ref: `${DEFINITION_LINK_PREFIX}ObjectPageTreeTable<${facetKey}>`
        }
    ];
    appSchemaSection[SchemaTag.annotationPath] = facet.annotationPath;
    addSectionTitleAndDescription(facet, appSchemaSection, facetKey);

    addTableDefinition('ObjectPageResponsiveTableWithMultiSelect', facetKey, facet, appSchema);
    addTableDefinition('ObjectPageResponsiveTableWithInlineDelete', facetKey, facet, appSchema);
    addTableDefinition('ObjectPageGridTable', facetKey, facet, appSchema);
    addTableDefinition('ObjectPageTreeTable', facetKey, facet, appSchema);
    addTableDefinition('ObjectPageAnalyticalTable', facetKey, facet, appSchema);

    let customColumnDefinitionKey;
    // Find target entity
    const targetEntitySet = determineEntitySetOfEntityType(facet.entityType, generateParameters.serviceAVT);
    if (targetEntitySet) {
        // Make sure that custom columns definitions are created for passed entity
        customColumnDefinitionKey = ensureCustomColumnDefinitionExists(
            appSchema,
            generateParameters.manifest,
            targetEntitySet.name,
            facet.entityType.name,
            generateParameters.serviceAVT,
            facet,
            facetKey,
            generateParameters.fragments
        );
    }
    for (const facetItem in facets) {
        if (facets[facetItem].base === FacetBase.LineItem && facetItem.includes(navigationProperty)) {
            addLineItemsType(
                appSchema,
                facets[facetItem].target as AnnotationTerm<LineItem>,
                facets[facetItem].entityType,
                facetItem,
                customColumnDefinitionKey
            );
        }
    }
}

/**
 * Adds definitions for subsections in object page sections to the app schema
 * @param {Definition} appSchema - app schema in general
 * @param {string} definitionKey - key of app schema definition to be filled
 * @param {FacetConfig} facet facet or section information
 * @param {string} facetId - key of the facet, as listed in FacetConfigs
 * @param {SectionTypeV2} sectionType (sub)section type
 */
function handleSubSection(
    appSchema: Definition,
    definitionKey: string,
    facet: FacetConfig,
    facetId: string,
    sectionType: SectionTypeV2,
    isHeaderFacet = false
): void {
    appSchema.definitions[definitionKey] = createDefaultSection(appSchema, facet, facetId);
    const appSchemaSection = appSchema.definitions[definitionKey] as Definition;
    const sectionDefinition = isHeaderFacet ? SectionTypeV2.HeaderSection : SectionTypeV2.SubSection;
    addAppSchemaDefinition(appSchema, appSchemaSection, sectionType, sectionDefinition);
    appSchemaSection.properties.subsections = {
        type: 'object',
        description: 'Subsections',
        properties: {},
        additionalProperties: false
    };
    appSchemaSection.properties.subsections[SchemaTag.isViewNode] = true;
}

/**
 * Method fills new chart section with properties.
 * @param {Definition} appSchema App schema in general
 * @param {string} definitionKey - key of app schema definition to be filled
 * @param {FacetConfig} facet facet or section information
 * @param {string} facetKey - facet key.
 * @param {SectionTypeV2} sectionType (sub)section type
 */
function handleSectionChart(
    appSchema: Definition,
    definitionKey: string,
    facet: FacetConfig,
    facetKey: string,
    sectionType: SectionTypeV2
) {
    appSchema.definitions[definitionKey] = createDefaultSection(appSchema, facet, facetKey);
    const appSchemaSection = appSchema.definitions[definitionKey] as Definition;
    addAppSchemaDefinition(appSchema, appSchemaSection, sectionType, SectionTypeV2.SectionChart);
}
/**
 * Method fills new header section with properties.
 * @param {Definition} appSchema App schema in general
 * @param {string} definitionKey - key of app schema definition to be filled
 * @param {FacetConfig} facet facet or section information
 * @param {string} facetKey - facet key.
 * @param {SectionTypeV2} sectionType (sub)section type
 */
function handleHeaderFacet(
    appSchema: Definition,
    definitionKey: string,
    facet: FacetConfig,
    facetKey: string,
    sectionType: SectionTypeV2,
    facetDefinition?: SectionTypeV2
) {
    appSchema.definitions[definitionKey] = createDefaultSection(appSchema, facet, facetKey);
    const appSchemaSection = appSchema.definitions[definitionKey] as Definition;
    addAppSchemaDefinition(
        appSchema,
        appSchemaSection,
        sectionType,
        facetDefinition || SectionTypeV2.CommonHeaderFacetSettings
    );
    appSchemaSection[SchemaTag.annotationPath] = facet.annotationPath;
    appSchemaSection[SchemaTag.isViewNode] = true;
    appSchemaSection[SchemaTag.dataType] = determineDataTypeOfTarget(facet?.target);
}

function addDataPointHeaderFacetDataType(appSchema: Definition, definitionKey: string, facet: FacetConfig) {
    const appSchemaSection = appSchema.definitions[definitionKey] as Definition;
    const dataType = (appSchemaSection[SchemaTag.dataType] = determineDataTypeOfTarget(facet?.target));
    if (dataType === DataType.Rating) {
        appSchemaSection.title = `${appSchemaSection.title}::RatingIndicatorVBox`;
    } else if (dataType === DataType.Progress) {
        appSchemaSection.title = `${appSchemaSection.title}::ProgressIndicatorVBox`;
    } else {
        appSchemaSection.title = `${appSchemaSection.title}::DataPoint`;
    }
}

/**
 * Method fills new form section in app schema with properties.
 * @param {Definition} appSchema App schema in general
 * @param {string} definitionKey - key of app schema definition to be filled
 * @param {FacetConfig} facet facet or section information
 * @param {string} facetKey - facet key.
 * @param {SectionTypeV2} sectionType (sub)section type
 * @return {object} returns filled form section in app schema.
 */
function handleFormSection(
    appSchema: Definition,
    definitionKey: string,
    facet: FacetConfig,
    facetKey: string,
    sectionType: SectionTypeV2
) {
    appSchema.definitions[definitionKey] = createDefaultSection(appSchema, facet, facetKey);
    const appSchemaSection = appSchema.definitions[definitionKey] as Definition;
    addAppSchemaDefinition(appSchema, appSchemaSection, sectionType, SectionTypeV2.SectionForm);
    appSchemaSection[SchemaTag.annotationPath] = facet.annotationPath;
    appSchemaSection[SchemaTag.isViewNode] = true;
    return appSchemaSection;
}

/**
 * Mapping of facet type to (sub)section type to enable correct further handling
 * @param {FacetBase} facetType - facet type to be mapped
 * @param {boolean} isSubSection - if section is subsection of section
 * @return {SectionTypeV2} sectionType (sub)section type
 */
function getSectionType(facetType: FacetBase, isSubSection: boolean, isHeaderFacet: boolean) {
    const headerSectionMap = new Map<FacetBase, SectionTypeV2>([
        [FacetBase.CollectionFacet, SectionTypeV2.HeaderSection],
        [FacetBase.Address, SectionTypeV2.HeaderSectionAddress],
        [FacetBase.Chart, SectionTypeV2.HeaderSectionChart],
        [FacetBase.DataPoint, SectionTypeV2.HeaderSectionDataPoint],
        [FacetBase.Form, SectionTypeV2.HeaderSectionForm],
        [FacetBase.Identification, SectionTypeV2.HeaderSectionForm]
    ]);
    const sectionTypeMapping = new Map<FacetBase, SectionTypeV2>([
        [FacetBase.CollectionFacet, SectionTypeV2.Section],
        [FacetBase.LineItem, SectionTypeV2.SectionTable],
        [FacetBase.Chart, SectionTypeV2.SectionChart],
        [FacetBase.Address, SectionTypeV2.SectionAddress],
        [FacetBase.Contact, SectionTypeV2.SectionContact],
        [FacetBase.Form, SectionTypeV2.SectionForm],
        [FacetBase.Identification, SectionTypeV2.SectionForm]
    ]);
    const subSectionTypeMapping = new Map<FacetBase, SectionTypeV2>([
        [FacetBase.CollectionFacet, SectionTypeV2.SubSection],
        [FacetBase.LineItem, SectionTypeV2.SubSectionTable],
        [FacetBase.Chart, SectionTypeV2.SubSectionChart],
        [FacetBase.Address, SectionTypeV2.SubSectionAddress],
        [FacetBase.Contact, SectionTypeV2.SubSectionContact],
        [FacetBase.Form, SectionTypeV2.SubSectionForm],
        [FacetBase.Identification, SectionTypeV2.SubSectionForm]
    ]);
    if (isHeaderFacet) {
        return headerSectionMap.get(facetType);
    } else if (isSubSection) {
        return subSectionTypeMapping.get(facetType);
    } else {
        return sectionTypeMapping.get(facetType);
    }
}

/**
 * Method returns reference key, special handling of (sub)sections
 * @param {FacetConfig} facet facet or section information
 * @param {string} facetKey - facet key.
 * @param {SectionTypeV2} sectionType (sub)section type
 * @return {string} reference key
 */
function getReferenceKey(facet, facetKey, sectionType): string {
    let referenceKey = facetKey;
    if (sectionType === (SectionTypeV2.Section || SectionTypeV2.SubSection)) {
        referenceKey = referenceKey.replace(`@${UIAnnotationTerms.Facets}`, facet.base);
    }
    return `${referenceKey}`;
}

/**
 * Creates a section definition in app schema.
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {FacetConfigs} facets - list of all facets.
 * @param {string} facetKey - facet key.
 * @param {object} sections - schema of current sections definition.
 * @param {Definition} appSchema - app specific schema that potentially gets enhanced.
 * @param {boolean} isSubSection - if section is subsection of section
 */
function addSection(
    generateParameters: GenerateAppSchemaParameters,
    facets: FacetConfigs,
    facetKey: string,
    sections: Definition,
    appSchema: Definition,
    isSubSection = false,
    isHeaderFacet = false
): void {
    const facet: FacetConfig = facets[facetKey];
    const sectionType = getSectionType(facet.base, isSubSection, isHeaderFacet);
    const referenceKey = getReferenceKey(facet, facetKey, sectionType);
    const definitionKey = `${sectionType}<${referenceKey}>`;
    addSectionReference(sections, referenceKey, definitionKey);
    switch (sectionType) {
        case SectionTypeV2.Section:
        case SectionTypeV2.SubSection:
        case SectionTypeV2.HeaderSection: {
            //handle collection facets
            isSubSection = isHeaderFacet ? false : true;
            handleSubSection(appSchema, definitionKey, facet, facetKey, sectionType, isHeaderFacet);
            const subSections = (appSchema.definitions[definitionKey] as Definition).properties
                .subsections as Definition;
            for (const key in facet.facets) {
                addSection(generateParameters, facet.facets, key, subSections, appSchema, isSubSection, isHeaderFacet);
            }
            break;
        }
        case SectionTypeV2.SectionTable:
        case SectionTypeV2.SubSectionTable: {
            handleLineItem(generateParameters, facetKey, appSchema, facet, facets, sectionType);
            //add fields for createWithParameterDialog
            const floorplanSuffix = 'OP';
            addFieldsType(appSchema, facet.entityType, floorplanSuffix);
            break;
        }
        case SectionTypeV2.SectionChart:
        case SectionTypeV2.SubSectionChart: {
            //handle reference facet (if comprising chart)
            handleSectionChart(appSchema, definitionKey, facet, facetKey, sectionType);
            break;
        }
        case SectionTypeV2.SectionForm:
        case SectionTypeV2.SubSectionForm: {
            const appSchemaSection = handleFormSection(appSchema, definitionKey, facet, facetKey, sectionType);
            handleForm(SectionType.Section, facet, appSchemaSection, appSchema, FioriElementsVersion.v2, facetKey);
            break;
        }
        case SectionTypeV2.SectionAddress:
        case SectionTypeV2.SectionContact: {
            handleAddressContact(facet, facetKey, sections, appSchema, SectionType.Section, undefined, 'V2');
            break;
        }
        case SectionTypeV2.SubSectionAddress:
        case SectionTypeV2.SubSectionContact: {
            handleAddressContact(
                facet,
                facetKey,
                sections,
                appSchema,
                SectionType.Section,
                SectionType.SubSection,
                'V2'
            );
            break;
        }
        case SectionTypeV2.HeaderSectionForm:
        case SectionTypeV2.HeaderSectionChart:
        case SectionTypeV2.HeaderSectionAddress: {
            handleHeaderFacet(appSchema, definitionKey, facet, facetKey, sectionType);
            break;
        }
        case SectionTypeV2.HeaderSectionDataPoint: {
            handleHeaderFacet(appSchema, definitionKey, facet, facetKey, sectionType);
            addDataPointHeaderFacetDataType(appSchema, definitionKey, facet);
            break;
        }
        default: {
            // Other facets -> only default properties of ObjectPageSection
            sections.properties[facetKey] = createDefaultSection(appSchema, facet, facetKey);
        }
    }
}

/**
 * Helper function to add facets to the app schema
 * @param facetsExists flag if facets exists and should be added
 * @param facets collection of facets
 * @param generateParameters list of API input parameters
 * @param sections sections in config to be extended
 * @param appSchema app speci
 * @param isHeaderFacet flag if facet is located in header section
 * @returns void
 */
function addSectionsFromFacets(
    facetsExists: boolean,
    facets: FacetConfigs,
    sections: Definition,
    generateParameters: GenerateAppSchemaParameters,
    appSchema: Definition,
    isHeaderFacet = false
) {
    if (!facetsExists) {
        return;
    }
    for (const facetKey in facets) {
        addSection(generateParameters, facets, facetKey, sections, appSchema, false, isHeaderFacet);
    }
}

/**
 * Adds the sections to the app schema
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {Definition} appSchema - app specific schema that potentially gets enhanced
 * @param {SapUiAppPagesV2} pages - list of (manifest) pages
 */
function addSections(
    generateParameters: GenerateAppSchemaParameters,
    appSchema: Definition,
    pages: v2.SapUiAppPagesV2
): void {
    const sections = appSchema.definitions[DefinitionName.Sections] as Definition;
    sections[SchemaTag.isViewNode] = true;
    if (generateParameters.entitySet?.entityType) {
        sections[SchemaTag.annotationPath] =
            `/${generateParameters.entitySet.entityType.fullyQualifiedName}/@${UIAnnotationTerms.Facets}`;
    }
    const headerSections = appSchema.definitions[DefinitionName.HeaderSections] as Definition;
    headerSections[SchemaTag.isViewNode] = true;
    if (!headerSections.properties) {
        headerSections.properties = {};
    }
    headerSections.additionalProperties = false;
    if (generateParameters.entitySet?.entityType) {
        headerSections[SchemaTag.annotationPath] =
            `/${generateParameters.entitySet.entityType.fullyQualifiedName}/@${UIAnnotationTerms.HeaderFacets}`;
    }

    if (!pages) {
        return;
    }
    // Get facet annotations
    const facets: FacetConfigs = getObjectPageFacets(
        generateParameters.entitySet?.entityType,
        generateParameters.serviceAVT,
        FioriElementsVersion.v2,
        generateParameters.logger
    );
    const headerFacets: FacetConfigs = getObjectPageHeaderFacets(
        generateParameters.entitySet?.entityType,
        generateParameters.serviceAVT,
        FioriElementsVersion.v2,
        generateParameters.logger
    );
    const generateFacets = facets && !!Object.keys(facets).length;
    const generateHeaderFacets = headerFacets && !!Object.keys(headerFacets).length;
    if (!generateFacets && !generateHeaderFacets) {
        return;
    }

    // Loop on all pages, look for the right entity set
    for (const key in pages) {
        const element = pages[key];
        if (generateParameters.entitySet?.name?.includes(element.entitySet)) {
            // Loop on all facets (of the given entitySet)
            addSectionsFromFacets(generateFacets, facets, sections, generateParameters, appSchema);
            addSectionsFromFacets(
                generateHeaderFacets,
                headerFacets,
                headerSections,
                generateParameters,
                appSchema,
                true
            );
        } else {
            addSections(generateParameters, appSchema, element.pages);
        }
    }

    // Add custom sections
    const customSections = appSchema.definitions['CustomSections'] as Definition;
    for (const name in customSections.properties) {
        sections.properties[name] = customSections.properties[name];
    }
}

/**
 * Add Object Page Header to app-specific schema
 *
 * @param {object} appSchema Schema of the app
 * @param {EntitySet} entitySet - current entity set
 * @param {EntityType} entityType - current entity type
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 */
function addHeader(
    appSchema: object,
    entitySet: EntitySet,
    entityType: EntityType,
    oDataServiceAVT: ConvertedMetadata
): void {
    addCommonHeaderSchema(appSchema, entityType);

    //Add header actions
    addHeaderActions(appSchema, entityType, entitySet, oDataServiceAVT, addHeaderActionCallBackV2, true);
}

/**
 * Adds the related facets keys 'enum' to the app schema
 * @param schema - app specific schema that potentially gets enhanced
 * @param {EntityType} entityType - current entity type
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @param {ExtensionLogger} logger - Logger class for logging messages
 */
function addRelatedFacetKeysType(
    schema: object,
    entityType: EntityType,
    oDataServiceAVT: ConvertedMetadata,
    logger?: ExtensionLogger
): void {
    // Find sections from annotation
    // And create new definition in schema as enum with description
    const facetSections = getObjectPageFacetSection(entityType, oDataServiceAVT, logger, undefined, true);
    if (facetSections) {
        addDefinitionForRelatedFacetKeys(
            schema,
            ['ObjectPageCustomSectionFragment', 'ObjectPageCustomSectionView'],
            facetSections,
            ['ID', 'key']
        );
    }
}

/**
 * Generates an app specific schema out of the generic schema.
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param genericSchema - generic JSON schema of an object page
 *
 * @returns the app specific JSON schema
 */
export function generateObjectPageSchemaV2(
    generateParameters: GenerateAppSchemaParameters,
    genericSchema: object
): object {
    // Initialize i18next
    initI18n();

    const appSchema = JSON.parse(JSON.stringify(genericSchema)) as Definition;

    // Custom sections as part of sections
    const additionalSections = {};
    for (const name in appSchema.definitions['CustomSections']['properties']) {
        additionalSections[name] = (appSchema.definitions['CustomSections'] as Definition).properties[name];
    }

    // Change reference to generated sections
    appSchema.properties[PropertyName.sections] = {
        $ref: DEFINITION_LINK_PREFIX + 'Sections'
    };

    const entityType = generateParameters.entitySet?.entityType;
    generateParameters.entityType = entityType;
    const pages = generateParameters.manifest[ManifestSection.generic].pages;
    if (!pages) {
        log(generateParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOPAGES', { appId: generateParameters.manifest['sap.app']['id'] }),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.generic]
            }
        });
        return appSchema;
    }

    // Add header
    addHeader(appSchema, generateParameters.entitySet, entityType, generateParameters.serviceAVT);

    // Add sections
    appSchema.definitions[DefinitionName.Sections] = {
        type: 'object',
        properties: {},
        description: 'Sections',
        additionalProperties: false
    };
    addSections(generateParameters, appSchema, pages as v2.SapUiAppPagesV2);

    // Custom section facets
    addRelatedFacetKeysType(appSchema, entityType, generateParameters.serviceAVT, generateParameters.logger);

    if (!appSchema.definitions.Sections) {
        if (appSchema.definitions.RelatedFacetKeys && appSchema.definitions.RelatedFacetKeys['oneOf'].length) {
            // If related facets exists - then we need sections definition
            appSchema.definitions.Sections = {
                type: 'object',
                properties: additionalSections,
                additionalProperties: false
            };
            appSchema.definitions.Sections[SchemaTag.isViewNode] = true;
        } else {
            delete appSchema.properties[PropertyName.sections];
        }
    }
    addFooterActions(
        appSchema,
        generateParameters,
        FioriElementsVersion.v2,
        addFooterActionCallBackV2,
        pages as v2.SapUiAppPagesV2
    );

    // Eliminate generic definitions
    delete appSchema.definitions['GenericSections'];
    delete appSchema.definitions['GenericSubSections'];
    delete appSchema.definitions['ObjectPageSectionTableV2'];
    delete appSchema.definitions['GenericColumns'];
    delete appSchema.definitions['TableColumnAction'];
    delete appSchema.definitions['CommonHeaderFacetSettings'];
    delete appSchema.definitions['ObjectPageHeaderSection'];
    // Currently not used
    delete appSchema.definitions['ObjectPageCustomHeaderSectionFragment'];
    delete appSchema.definitions['ObjectPageSectionFormV2'];
    delete appSchema.definitions['ObjectPageSectionTableV2'];
    delete appSchema.definitions['ObjectPageSubSectionV2'];
    delete appSchema.definitions['ObjectPageResponsiveTableWithMultiSelect'];
    delete appSchema.definitions['ObjectPageResponsiveTableWithInlineDelete'];
    delete appSchema.definitions['ObjectPageAnalyticalTable'];
    delete appSchema.definitions['ObjectPageTreeTable'];
    delete appSchema.definitions['ObjectPageGridTable'];
    delete appSchema.definitions['ObjectPageToolBar'];
    delete appSchema.definitions['ObjectPageToolBarActions'];
    delete appSchema.definitions['ObjectPageForm'];
    delete appSchema.definitions['ObjectPageFormFields'];
    delete appSchema.definitions['ObjectPageFormActions'];
    delete appSchema.definitions['FieldPathOP'];
    updatePropertyIndices(appSchema, ROOT_PROPERTIES_ORDER);
    return appSchema;
}
