import type { Manifest, v4 } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getActionsPathForOP } from '../../application';
import { CustomFormActionOP } from './ObjectPageFormAction';

export class ActionAfterExecutionConfigurationForm implements v4.ActionAfterExecutionConfiguration {
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey): string =>
                getActionsPathForOP(pageName, manifest, sectionId, columnKey) + '/afterExecution'
        }
    })
    navigateToInstance?: boolean;

    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey): string =>
                getActionsPathForOP(pageName, manifest, sectionId, columnKey) + '/afterExecution'
        }
    })
    enableAutoScroll?: boolean;
}

export class FormAction implements v4.FormAction {
    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    afterExecution?: ActionAfterExecutionConfigurationForm;
}

export class ObjectPageFormActions implements v4.ObjectPageFormActions {
    [key: string]: FormAction | CustomFormActionOP;
}

export class ObjectPageForm implements v4.ObjectPageForm {
    actions: ObjectPageFormActions;
}
