import { syncRule, childId, validity } from '../common/decoration/decorators';
import type { v4, ViewTypes, Target, Manifest, Application } from '@sap/ux-specification-types';
import { UIAnnotationTerms } from '@sap-ux/vocabularies-types/vocabularies/UI';
import { QUICKVARPATH } from '@sap/ux-specification-types';
import { controllerExtensions } from './sync-rules/controllerExtensions';

export const getSettingsPathForPage = (pageName: string[]): string => {
    return `sap.ui5/routing/targets/${pageName[0]}/options/settings`;
};

export const getSettingsPathForLRViewsPaths = (pageName: string[]): string => {
    return getSettingsPathForPage(pageName) + `/views/paths`;
};

export const getSettingsPathForHeaderOP = (pageName: string[]): string => {
    return getSettingsPathForPage(pageName) + `/content/header`;
};

export const getSettingsPathForFooterOP = (pageName: string[]): string => {
    return getSettingsPathForPage(pageName) + `/content/footer`;
};

export const getControlConfigurationPath = (pageName: string[]): string => {
    return getSettingsPathForPage(pageName) + '/controlConfiguration';
};

export const getHeaderFacetSettingPath = (pageName: string[], _manifest: Manifest, sectionId: string): string => {
    return getControlConfigurationPath(pageName) + `/@${UIAnnotationTerms.HeaderFacets}/facets/${sectionId}`;
};

export const getSelectionFieldsPath = (pageName: string[]): string => {
    return getControlConfigurationPath(pageName) + `/@${UIAnnotationTerms.SelectionFields}`;
};

/**
 * Common function that calculates the path in manifest for custom filter field
 * @param pageName - page name in manifest
 * @param _manifest - manifest (not used)
 * @param _sectionId - section id (not used)
 * @param filterFieldId - Custom filter field id
 * @returns - path as string
 */
export const getCustomFilterFieldFieldPath = (
    pageName: string[],
    _manifest: Manifest,
    _sectionId: string,
    filterFieldId
): string => {
    return (
        getControlConfigurationPath(pageName) + `/@${UIAnnotationTerms.SelectionFields}/filterFields/${filterFieldId}`
    );
};

export const getFilterFieldPath = (
    pageName: string[],
    _manifest: Manifest,
    _targetAnnotationEncoded: string,
    fieldId: string
): string => {
    return getSelectionFieldsPath(pageName) + `/filterFields/${fieldId}`;
};

/**
 * Common function that calculates the path in manifest for table settings of a list report or ALP table view
 * @param pageName - page name in manifest
 * @param manifest - manifest (not used)
 * @param targetAnnotationEncoded - encoded target annotation (not used)
 * @param viewKey - view key (not used)
 * @param targetAnnotation - target annotation, i.e. the line item (with or without qualifier)
 * @returns - path as string
 */
export const getTableSettingsPathForView = (
    pageName: string[],
    _manifest: Manifest,
    _targetAnnotationEncoded: string,
    _viewKey: string,
    targetAnnotation: string
): string => {
    const annotationEncoded = targetAnnotation?.replace(/\//gi, '??');
    return annotationEncoded
        ? getControlConfigurationPath(pageName) + `/${annotationEncoded}/tableSettings`
        : undefined;
};

export const getTableSettingsPathForLR = (
    pageName: string[],
    manifest: Manifest,
    targetAnnotationEncoded: string,
    columnKey: string,
    targetAnnotation: string
): string => {
    if (targetAnnotation) {
        return getTableSettingsPathForView(pageName, manifest, targetAnnotationEncoded, columnKey, targetAnnotation);
    } else {
        return getControlConfigurationPath(pageName) + `/@${UIAnnotationTerms.LineItem}/tableSettings`;
    }
};

export const getQuickVariantPath = (
    pageName: string[],
    manifest: Manifest,
    targetAnnotationEncoded: string,
    columnKey: string,
    targetAnnotation: string
): string => {
    return (
        getTableSettingsPathForLR(pageName, manifest, targetAnnotationEncoded, columnKey, targetAnnotation) +
        QUICKVARPATH
    );
};

