export const getCustomSectionPath = (
    pageName: string[],
    _manifest: Manifest,
    sectionId: string,
    pathPart?: string
): string => {
    let path = getSettingsPathForPage(pageName) + `/content/body/sections/${sectionId}`;
    if (pathPart) {
        path += '/' + pathPart;
    }
    return path;
};

export const getObjectPageCustomHeaderSectionPath = (
    pageName: string[],
    _manifest: Manifest,
    sectionId: string,
    pathPart?: string
): string => {
    let path = getSettingsPathForPage(pageName) + `/content/header/facets/${sectionId}`;
    if (pathPart) {
        path += '/' + pathPart;
    }
    return path;
};

export const getCustomSubSectionPath = (
    pageName: string[],
    manifest: Manifest,
    sectionId: string,
    pathPart?: string,
    targetAnnotation?: string,
    configObject?: object,
    originalIds?: string[]
): string => {
    let parent = sectionId;

    if (originalIds) {
        if (originalIds[0] === 'sections' && originalIds[1]) {
            parent = originalIds[1];
        }
        if (originalIds[2] === 'subsections' && originalIds[3]) {
            pathPart = `subSections/${originalIds[3]}`;
        }
    }
    let path = getSettingsPathForPage(pageName) + `/content/body/sections/${parent}`;
    if (pathPart) {
        path += '/' + pathPart;
    }
    return path;
};

export class ApplicationV4 implements Application {
    $schema?: string;
    id?: string;
    pages?: v4.Pages;
    home?: string;
    target?: Target;
    @childId('settings')
    settings?: AppSettings;
}

export class AppSettings implements v4.AppSettings {
    @syncRule({
        manifest: {
            path: (): string => 'sap.app'
        }
    })
    title?: string;

    @syncRule({
        manifest: {
            path: (): string => 'sap.app'
        }
    })
    description?: string;

    @syncRule({
        manifest: {
            path: (): string => 'sap.ui5/routing/config'
        }
    })
    @validity({
        since: '1.78.0'
    })
    flexibleColumnLayout?: v4.FlexibleColumnLayoutV4;

    @syncRule({
        manifest: {
            path: (): string => 'sap.ui5'
        }
    })
    @validity({
        since: '1.56.0'
    })
    flexEnabled?: boolean;

    @syncRule({
        manifest: {
            path: (): string => 'sap.ui5/routing/config'
        }
    })
    viewType?: ViewTypes;

    @syncRule({
        manifest: {
            path: (): string => 'sap.ui5/routing/config'
        }
    })
    path?: string;

    @syncRule(controllerExtensions)
    controllerExtensions?: v4.ControllerExtensions;
}
