import type {
    FileData,
    ExtensionLogger,
    ImportListReportV2Parameters,
    ImportALPV2Parameters,
    ImportObjectPageV2Parameters,
    ImportAllConfigsParameters
} from '@sap/ux-specification-types';
import {
    LogSeverity,
    MANIFESTPATH,
    FioriElementsVersion,
    PageTypeV2,
    ManifestSection,
    v2,
    SchemaType
} from '@sap/ux-specification-types';
import { AppProvider } from '../../../common/appProvider';
import { transferSettingsOfObject } from '../common';
import { createListReportConfig } from '../pages/listReport';
import { createAnalyticalListPageConfig } from '../pages/analyticalListPage';
import { createObjectPageConfigV2 } from '../pages/objectPage';
import { AppSettings } from '../../application';
import { getSchemaFilePath, generatePageId, getPageTypeV2 } from '../../../common';
import i18next from 'i18next';
import { initI18n } from '../../../../i18n/i18n';
import { log } from '../../../../extensionLogger';

/**
 * Method which generates id for passed page
 * @param page - Generic Page object.
 * @param pageName - Page name in manifest. Used if entitySet is empty
 * @returns {string} Generated page id
 */
function generateId(page: v2.SapUiAppPageV2, pageName: string): string {
    if (page.component?.name) {
        return generatePageId(getPageTypeV2(page.component?.name), page.entitySet, undefined, pageName);
    } else {
        return pageName;
    }
}

function getPageConfigsAndNavigations(
    page: v2.Page,
    v2Page: v2.SapUiAppPageV2,
    flex: string[],
    manifest: any,
    schemas: any,
    fragments: FileData[],
    pages: { [key: string]: v2.Page }
) {
    page.navigation = {};
    // eslint-disable-next-line @typescript-eslint/no-use-before-define
    const targets: { [key: string]: v2.Page } = getPagesAndConfigs(v2Page.pages, flex, manifest, schemas, fragments);
    for (const pageId in targets) {
        const target = targets[pageId];
        pages[pageId] = target;
    }
    for (const v2ChildPageName in v2Page.pages) {
        const id = generateId(v2Page.pages[v2ChildPageName], v2ChildPageName);
        if (page.entitySet) {
            page.navigation[id] = targets[id].navigationProperty
                ? `${page.entitySet}.${targets[id].navigationProperty}`
                : page.entitySet;
        } else {
            page.navigation[id] = targets[id].navigationProperty ? targets[id].navigationProperty : id;
        }
    }
}

/**
 * Sets up page definitions, including the configurations for them
 * @param {SapUiAppPagesV2} v2Pages - lisr of all pages
 * @param flex - list of flexibility changes
 * @param manifest - manifest.json of the app
 * @param schemas - list of all app-specific schemas
 * @param fragments - list of fragments
 * @param {ExtensionLogger} logger - logger for messages
 * @returns an index table of page definitions
 */
function getPagesAndConfigs(
    v2Pages: v2.SapUiAppPagesV2,
    flex: string[],
    manifest,
    schemas,
    fragments?: FileData[],
    logger?: ExtensionLogger
): { [key: string]: v2.Page } {
    const pages: { [key: string]: v2.Page } = {};
    for (const v2PageName in v2Pages) {
        const v2Page = v2Pages[v2PageName];
        const id = generateId(v2Page, v2PageName);
        if (!id) {
            continue;
        }
        const page: v2.Page = {
            pageType: getPageTypeV2(v2Page.component?.name),
            entitySet: v2Page.entitySet,
            ...(v2Page.navigationProperty && { navigationProperty: v2Page.navigationProperty }),
            ...(v2Page.defaultLayoutType && { defaultLayoutType: v2Page.defaultLayoutType })
        };
        pages[id] = page;

        // Get the JSON schema for the page. If no page specific schema exists, fall back top the generic schema for the page type
        const schemaKey: string =
            Object.keys(schemas).find((element) => element.includes(id)) ||
            Object.keys(schemas).find((element) => element.includes(id.slice(0, -1))) ||
            Object.keys(schemas).find((element) => element.includes(page.pageType));

        // convert settings to config
        if (schemaKey) {
            if (page.pageType === PageTypeV2.ListReport) {
                const importParameters: ImportListReportV2Parameters = {
                    manifest,
                    flex,
                    jsonSchema: schemas[schemaKey],
                    fragments,
                    logger
                };
                page.config = createListReportConfig(importParameters);
            } else if (page.pageType === PageTypeV2.AnalyticalListPage) {
                const importParameters: ImportALPV2Parameters = {
                    manifest,
                    flex,
                    jsonSchema: schemas[schemaKey],
                    fragments,
                    logger
                };
                page.config = createAnalyticalListPageConfig(importParameters);
            } else {
                const importParameters: ImportObjectPageV2Parameters = {
                    manifest,
                    flex,
                    jsonSchema: schemas[schemaKey],
                    entitySet: page.entitySet,
                    fragments,
                    logger
                };
                page.config = createObjectPageConfigV2(importParameters);
            }
        }
        if (v2Page.pages) {
            getPageConfigsAndNavigations(page, v2Page, flex, manifest, schemas, fragments, pages);
        }
    }
    return pages;
}

/**
 * The V2AppProvider collects all the app information for a Fiori Elements V2 app
 */
export class V2AppProvider extends AppProvider {
    public constructor(importParameters: ImportAllConfigsParameters) {
        initI18n();
        let v2Pages: v2.SapUiAppPagesV2;
        if (!importParameters.manifest[ManifestSection.generic]) {
            log(importParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOFE'),
                location: {
                    path: MANIFESTPATH,
                    range: [ManifestSection.generic]
                }
            });
        } else {
            v2Pages = importParameters.manifest[ManifestSection.generic].pages as v2.SapUiAppPagesV2;
        }
        if (!v2Pages) {
            log(importParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOPAGES', { appId: importParameters.manifest['sap.app']['id'] }),
                location: {
                    path: MANIFESTPATH,
                    range: [ManifestSection.generic]
                }
            });
        }
        const pageKey = v2Pages && Object.keys(v2Pages)[0];
        const pageId = v2Pages && generateId(v2Pages[pageKey], pageKey);
        // Import settings
        const appSettings = new AppSettings();
        const schemaFile = getSchemaFilePath(SchemaType.Application);
        const appSchema = importParameters.appSchemas[schemaFile.filename];
        transferSettingsOfObject(
            appSettings,
            importParameters.manifest,
            appSchema['definitions']['AppSettings'],
            undefined
        );
        // Call base
        super(
            importParameters.manifest,
            importParameters.appSchemas,
            pageId,
            getPagesAndConfigs(
                v2Pages,
                importParameters.flexChanges,
                importParameters.manifest,
                importParameters.appSchemas,
                importParameters.fragments,
                importParameters.logger
            ),
            FioriElementsVersion.v2,
            appSettings
        );
    }
}

/**
 * Collects all pages of a V2 app from the manifest, by traversing the V2 page hierarchy.
 * @param v2Pages - entry point = complex page definition in manifest
 * @param componentName - Component name
 * @returns index table of pages
 */
export function getPages(v2Pages: v2.SapUiAppPagesV2, componentName: string, logger?: ExtensionLogger): v2.Pages {
    const pages: v2.Pages = {};
    for (const v2PageName in v2Pages) {
        const v2Page = v2Pages[v2PageName];
        if (!v2Page.component?.name) {
            log(logger, {
                severity: LogSeverity.Error,
                message: i18next.t('COMPONENTNOTDEFINED', { pageName: v2PageName }),
                location: {
                    path: MANIFESTPATH
                }
            });
        }
        if (v2Page.component?.name === componentName) {
            const id = generateId(v2Page, v2PageName);
            pages[id] = v2Page;
        }
        if (componentName === v2.FE_TEMPLATE_V2_OBJECT_PAGE && v2Page.pages) {
            const subPages = getPages(v2Page.pages, componentName, logger);
            for (const subPageId in subPages) {
                pages[subPageId] = subPages[subPageId];
            }
        }
    }
    return pages;
}
