const ROOT_PROPERTIES_ORDER: Array<keyof v4.ListReportConfigV4> = ['header', 'filterBar', 'chart', 'table'];

/**
 * Adds the selection fields to the app schema, as properties of the FilterBar
 * @param {EntityType} entityType - the entity type as part of the AVT ConvertedMetadata
 * @param {Definition} appSchema - the app specific schema that shall get enhanced
 * @param {AnnotationTerm<SelectionFields>} selectionFieldsAnnotation - the UI.SelectionField annotation
 * @param {Definition} [selectionFieldsDefinition] - Schema definitions of selection fields
 */
export function addSelectionFields(
    entityType: EntityType,
    appSchema: Definition,
    selectionFieldsAnnotation: AnnotationTerm<SelectionFields>,
    selectionFieldsDefinition?: Definition
): Definition {
    if (!selectionFieldsDefinition) {
        selectionFieldsDefinition = appSchema.definitions[DefinitionName.SelectionFields] as Definition;
        selectionFieldsDefinition.additionalProperties = false;
        selectionFieldsDefinition.properties = {};
    }
    if (entityType) {
        selectionFieldsDefinition[SchemaTag.annotationPath] = createAnnotationPath(
            entityType.fullyQualifiedName,
            UIAnnotationTerms.SelectionFields
        );
    }
    let i = 0;
    selectionFieldsAnnotation?.forEach((selectionField) => {
        let description = selectionField.value;
        const fieldDefinition = 'SelectionField::' + prepareRef(selectionField.value);
        if (selectionField.type === 'PropertyPath') {
            description = getLabelForPropertyPath(selectionField, entityType);
        }
        selectionFieldsDefinition.properties[fieldDefinition] = {
            $ref: DEFINITION_LINK_PREFIX + fieldDefinition
        };
        selectionFieldsDefinition.properties[fieldDefinition][SchemaTag.propertyIndex] = i;
        const dataType = determineDataType(selectionField);
        appSchema.definitions[fieldDefinition] = {
            type: 'object',
            properties: {},
            description,
            additionalProperties: false,
            ...(dataType && { dataType })
        };
        appSchema.definitions[fieldDefinition][SchemaTag.isViewNode] = true;
        appSchema.definitions[fieldDefinition][SchemaTag.keys] = [
            { name: SchemaKeyName.value, value: `${selectionField.value}` }
        ];
        if (selectionField.$target?.fullyQualifiedName.indexOf('@') > -1) {
            appSchema.definitions[fieldDefinition][SchemaTag.target] = selectionField.$target.fullyQualifiedName
                .split('@')[0]
                .split('/')[0];
        } else if (selectionField.$target?.fullyQualifiedName) {
            appSchema.definitions[fieldDefinition][SchemaTag.target] =
                selectionField.$target.fullyQualifiedName.split('/')[0];
        }

        appSchema.definitions[fieldDefinition][SchemaTag.annotationType] = selectionField?.type;
        appSchema.definitions[fieldDefinition][SchemaTag.annotationPath] = `${
            selectionFieldsDefinition[SchemaTag.annotationPath]
        }/${i}`;
        i++;
    });
    return selectionFieldsDefinition;
}

/**
 * Returns visual filters (if defined) from manifest page
 * @param {SapUiAppPageV4} v4Page - actual page in the manifest
 * @returns
 */
function getVisualFilters(v4Page: v4.SapUiAppPageV4): {} | undefined {
    const selectionFields =
        v4Page.options.settings?.controlConfiguration?.[`@${UIAnnotationTerms.SelectionFields}`]?.['filterFields'];
    if (selectionFields) {
        const visualFilters = {};
        Object.keys(selectionFields).forEach((field) => {
            if (!selectionFields[field].template) {
                visualFilters[field] = selectionFields[field];
            }
        });
        return Object.keys(visualFilters).length > 0 ? visualFilters : undefined;
    }
}

/**
 * Collects indexes of selection fields and entityProperties to sort visual filters
 * @param {AnnotationTerm<SelectionFields>} selectionFieldsAnnotation - the UI.SelectionField annotation
 * @param {EntityType} entityType - the entity type as part of the AVT ConvertedMetadata
 * @returns
 */
function getPropertiesIndex(selectionFields: AnnotationTerm<SelectionFields>, entityType: EntityType): string[] {
    const properties: string[] = [];
    selectionFields?.forEach((selectionField) => properties.push(selectionField.value));
    if (entityType) {
        entityType.entityProperties.forEach((property) =>
            !properties.includes(property.name) ? properties.push(property.name) : undefined
        );
    }
    return properties;
}

/**
 * Adds visual filters to the app schema, as properties of the FilterBar
 * @param {EntityType} entityType - the entity type as part of the AVT ConvertedMetadata
 * @param {Definition} appSchema - the app specific schema that shall get enhanced
 * @param visualFiltersManifest - the UI.SelectionFields under controlConfiguration
 * @param {AnnotationTerm<SelectionFields>} selectionFieldsAnnotation - the UI.SelectionField annotation
 */
function addVisualFilters(
    entityType: EntityType,
    appSchema: Definition,
    visualFiltersManifest: any,
    selectionFields: AnnotationTerm<SelectionFields>
) {
    const propertiesIndex = getPropertiesIndex(selectionFields, entityType);
    const visualFilters = appSchema.definitions[DefinitionName.VisualFilters] as Definition;
    visualFilters.properties = {};
    for (const fieldName in visualFiltersManifest) {
        const filterField = visualFiltersManifest[fieldName];
        // No custom filter field support in visual filters
        if (!filterField.template) {
            const visualFiltersField = (visualFilters.properties[fieldName] = {
                $ref: getFacetDefinitionLink(DefinitionName.VisualFilter, fieldName)
            });
            const propertyIndex = propertiesIndex.indexOf(fieldName);
            if (propertyIndex !== -1) {
                visualFiltersField[SchemaTag.propertyIndex] = propertyIndex;
            }
            const filterDefinition = parseSchemaDefinition(DefinitionName.VisualFilter, fieldName, appSchema);
            if (filterField.visualFilter?.valueList) {
                filterDefinition[SchemaTag.annotationPath] = `${fieldName}/@${filterField.visualFilter.valueList}`;
            }
            const annotationLabel = entityType?.entityProperties
                ?.find((property) => property.name === fieldName)
                ?.annotations?.Common?.Label?.toString();
            filterDefinition.description = annotationLabel || fieldName;
            filterDefinition[SchemaTag.keys] = [{ name: SchemaKeyName.value, value: fieldName }];
        }
    }
}

/**
 * Method updates schema for custom filter fields:
 * 1. Enables creation of custom filter fields;
 * 2. Populates custom filter fields from manifest to schema;
 * @param {Definition} appSchema - the app specific schema that shall get enhanced
 * @param {Definition} selectionFieldsDefinition - schema of selection fields which should contain custom filter fields
 * @param {SapUiAppPageV4} v4Page - actual page in the manifest
 */
function applyCustomFilterFields(
    appSchema: Definition,
    selectionFieldsDefinition: Definition,
    v4Page: v4.SapUiAppPageV4
): void {
    selectionFieldsDefinition.additionalProperties = {
        $ref: `#/definitions/${DefinitionName.CustomFilterField}`
    };
    addCustomFilterFieldDefinition(appSchema, selectionFieldsDefinition, v4Page);
    const customSectionDefinition = appSchema.definitions[DefinitionName.CustomFilterFieldPosition];
    if (typeof customSectionDefinition === 'object') {
        addDescriptiveEnumDefinition(
            appSchema,
            selectionFieldsDefinition,
            {
                schema: customSectionDefinition,
                property: 'anchor'
            },
            {
                resolveDefinition: true,
                keyProperties: [SchemaKeyName.value, SchemaKeyName.key],
                descriptionAsFallback: true
            }
        );
    }
}

/**
 * Adds selection fields or compact/visual filters to filter bar
 * @param {EntityType} entityType - actual entity type (AVT information)
 * @param {Definition} appSchema - app-specific JSON schema
 * @param {SapUiAppPageV4} v4Page - actual page in the manifest
 * @param {string} alias - the given alias or namespace of the OData annotations
 */
function addFilterBar(entityType: EntityType, appSchema: Definition, v4Page: v4.SapUiAppPageV4, alias: string) {
    const visualFilters = getVisualFilters(v4Page);
    const selectionFields = alias && entityType?.annotations?.[alias]?.SelectionFields;
    let selectionFieldsDefinition;
    // Hide visualFilters/selectionFields depending on existing visual filters
    if (visualFilters) {
        appSchema.definitions[DefinitionName.SelectionFields][SchemaTag.hidden] = true;
        appSchema.definitions[DefinitionName.SelectionFields]['properties'] = {};
        selectionFieldsDefinition = appSchema.definitions[DefinitionName.CompactFilters];
        selectionFieldsDefinition.additionalProperties = false;
        selectionFieldsDefinition.properties = {};
        addVisualFilters(entityType, appSchema, visualFilters, selectionFields);
    } else {
        const filterBar = appSchema.definitions[DefinitionName.FilterBar] as Definition;
        filterBar.properties['initialLayout'][SchemaTag.hidden] = true;
        filterBar.properties['layout'][SchemaTag.hidden] = true;
        appSchema.definitions[DefinitionName.CompactFilters][SchemaTag.hidden] = true;
        appSchema.definitions[DefinitionName.VisualFilters][SchemaTag.hidden] = true;
    }
    selectionFieldsDefinition = addSelectionFields(entityType, appSchema, selectionFields, selectionFieldsDefinition);
    // Apply custom filter fields
    applyCustomFilterFields(appSchema, selectionFieldsDefinition, v4Page);
}

function defineActions(
    appSchema: Definition,
    schemaIdForActions: string,
    annotations,
    targetAnnotation: string,
    entityType: EntityType
) {
    appSchema.definitions[schemaIdForActions] = JSON.parse(JSON.stringify(appSchema.definitions['Actions']));
    const actions = appSchema.definitions[schemaIdForActions] as Definition;
    actions.properties = {};
    actions.additionalProperties = false;
    const chartAnnotation = annotations[targetAnnotation?.split(`${UIVOCABULARY}.`)[1]];
    if (chartAnnotation && chartAnnotation.Actions) {
        let i = 0;
        chartAnnotation.Actions.forEach((actionRecord) => {
            const actionDefinition = prepareRef(`${DefinitionName.ChartToolBarAction}<${actionRecord.Action}>`);
            const description = getDataFieldDescription(actionRecord as DataFieldAbstractTypes, entityType);
            const prefix = actionRecord.$Type.split(`${UIVOCABULARY}.`)[1];
            const action = (actions.properties[`${prefix}::${actionRecord.Action}`] = {
                $ref: DEFINITION_LINK_PREFIX + actionDefinition,
                description
            });
            //Add tags
            action[SchemaTag.propertyIndex] = i;
            action[SchemaTag.annotationType] = actionRecord.$Type;
            action[SchemaTag.annotationPath] = `/${actionRecord.fullyQualifiedName}`;
            //as ChartToolBarAction is an empty object, copy ViewToolBarAction and remove properties
            appSchema.definitions[actionDefinition] = JSON.parse(
                JSON.stringify(appSchema.definitions[DefinitionName.ViewToolBarAction])
            );
            (appSchema.definitions[actionDefinition] as Definition).properties = {};
            appSchema.definitions[actionDefinition][SchemaTag.annotationPath] = actionRecord.fullyQualifiedName;
            i++;
        });
    }
}

/**
 * Finds the right visualization for a given view
 * @param allVisualizations - list of visualizations for the given (S)PV
 * @param namespace - namespace for the UI annotations
 * @param viewKey - view key: primary, secondary, or anything else; may also be undefined. The first visualization is taken in this case.
 * @param visualizationType - allows to find the right visualization if there are many.
 * @returns the value of the first fitting visualization
 */
function findTheRightVisualization(
    allVisualizations: Array<object>,
    namespace: string,
    viewKey?: string,
    visualizationType?: Visualization
): { visualization?: string; targetAnnotation: string } {
    let visualization: string | undefined, visualizationObject: object, targetAnnotation: string;
    if (allVisualizations) {
        if (viewKey === 'primary') {
            visualizationObject = allVisualizations.find((vis) => {
                return vis['value'].indexOf('Chart') > -1;
            });
        } else if (viewKey === 'secondary') {
            visualizationObject = allVisualizations.find((vis) => {
                return vis['value'].indexOf(FacetBase.LineItem) > -1;
            });
        } else {
            if (visualizationType) {
                visualizationObject = allVisualizations.find((v) => v['value'].indexOf(visualizationType) > -1);
            } else {
                visualizationObject = allVisualizations[0];
            }
        }
        visualization =
            typeof visualizationObject?.['value'] === 'string'
                ? replaceNamespaces(visualizationObject['value'])
                : undefined;
        if (visualizationObject?.['$target']?.fullyQualifiedName) {
            targetAnnotation = `/${namespace}/${visualizationObject['$target'].fullyQualifiedName.split(namespace)[1]}`;
        } else if (visualization) {
            targetAnnotation = `/${namespace}/` + visualization.replace(`UI`, UIVOCABULARY);
        }
    }
    return { visualization, targetAnnotation };
}

/**
 * An annotationPath must be analyzed, to find the right target annotation and visualization
 *
 */
function analyzeChartAnnotation(
    namespace: string,
    annotationPath: string,
    annotationAVT: object,
    viewKey: string
): { visualization: string; targetAnnotation: string; chartTitle: string } {
    const visualization = '@UI.Chart';
    const targetAnnotationFullName = `/${namespace}/@${annotationPath}`;
    let chartTitle: string;

    if (annotationAVT['Title']) {
        chartTitle = annotationAVT['Title'];
    } else {
        chartTitle = `Chart View (${viewKey})`;
    }
    return { visualization, targetAnnotation: targetAnnotationFullName, chartTitle };
}

/**
 * An annotationPath must be analyzed, to find the right target annotation and visualization
 * @param annotationPath - given annotationPath
 * @param uIAnnotations - annotations of UI namespace of the given entityType
 * @param serviceAVT Combined service metadata, as returned by annotation vocabularies tools.
 * @param viewKey - allows to distinguish the different view types (LR or ALP)
 * @param visualizationType - allows to find the right visualization if there are many.
 * @param logger - extension logger.
 * @returns { visualization: string describing the visualization of the annotation,
 *            targetAnnotation: the target annotation behind the visualization
 *            namespace: namespace of the annotation,
 *            title: title to be shown, derived from the annotation }
 */
export function analyzeViewAnnotation(
    annotationPath: string,
    uIAnnotations: object,
    serviceAVT: ConvertedMetadata,
    viewKey?: string,
    visualizationType?: Visualization,
    logger?: ExtensionLogger
): { visualization: string; targetAnnotation: string; namespace: string; title: string } {
    let namespace: string = undefined,
        title: string;
    const UILINEITEM = '@UI.LineItem';
    const annotationInManifest = annotationPath?.split(`${UIVOCABULARY}.`)[1];
    const annotationAVT = annotationInManifest && uIAnnotations[annotationInManifest];
    if (!annotationAVT) {
        return { visualization: undefined, targetAnnotation: undefined, namespace, title: `View (${viewKey})` };
    }
    title = annotationAVT.Text;
    namespace = annotationAVT.fullyQualifiedName.split(UIVOCABULARYALPHADOT)[0];

    if (annotationInManifest.startsWith('PresentationVariant')) {
        const { visualization, targetAnnotation } = findTheRightVisualization(
            annotationAVT.Visualizations,
            namespace,
            viewKey,
            visualizationType
        );
        title = title || `View (${viewKey})`;
        return { visualization, targetAnnotation, namespace, title };
    } else if (annotationInManifest.startsWith('SelectionPresentationVariant')) {
        let { visualization, targetAnnotation } = findTheRightVisualization(
            annotationAVT.PresentationVariant?.Visualizations,
            namespace,
            viewKey,
            visualizationType
        );
        if (!visualization) {
            log(logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOVISUALIZATIONS')
            });
            visualization = UILINEITEM; // default visualization
            targetAnnotation = `/${namespace}/` + visualization.replace(`UI`, UIVOCABULARY); // default targetAnnotation
        }
        title = title || `View (${viewKey})`;
        return { visualization, targetAnnotation, namespace, title };
    } else if (annotationInManifest.startsWith('SelectionVariant')) {
        const visualization = UILINEITEM;
        const targetAnnotation = `/${namespace}/@${UIAnnotationTerms.LineItem}`;
        title = title || `Table View (${viewKey})`;
        return { visualization, targetAnnotation, namespace, title };
    } else if (annotationInManifest.startsWith('LineItem')) {
        let visualization = UILINEITEM;
        if (annotationAVT.qualifier) {
            visualization += `#${annotationAVT.qualifier}`;
        }
        const targetAnnotation = `/${namespace}/@${annotationPath}`;
        title = title || `Table View (${viewKey})`;
        return { visualization, targetAnnotation, namespace, title };
    } else if (annotationInManifest.startsWith('Chart')) {
        const { visualization, targetAnnotation, chartTitle } = analyzeChartAnnotation(
            namespace,
            annotationPath,
            annotationAVT,
            viewKey
        );
        return { visualization, targetAnnotation, namespace, title: chartTitle };
    }
    return { visualization: undefined, targetAnnotation: undefined, namespace, title: `View (${viewKey})` };
}

/**
 * Adds the ALP chart view to the app-specific schema
 * @param {Definition} appSchema - app-specific JSON schema
 * @param targetAnnotation - target annotation of the chart view, as determined by analyzeViewAnnotation
 * @param annotations - UI annotations, as determined by AVT
 */
function addALPChartView(appSchema: Definition, chartAnnotationPath: string, targetAnnotation: string, annotations) {
    const chartView = appSchema.definitions[DefinitionName.ALPChartView] as Definition;
    appSchema.definitions[DefinitionName.ALPChart] = JSON.parse(JSON.stringify(chartView));
    delete appSchema.definitions[DefinitionName.ALPChart][SchemaTag.isViewNode];
    if (targetAnnotation) {
        appSchema.definitions[DefinitionName.ALPChart][SchemaTag.annotationPath] = targetAnnotation;
    }
    (appSchema.definitions[DefinitionName.ALPChart] as Definition).properties.annotationPath[SchemaTag.hidden] = true;

    chartView.properties.annotationPath[SchemaTag.hidden] = true;
    chartView.additionalProperties = false;
    chartView.description = `Chart (key: primary)`;
    chartView.title = `Chart`;
    chartView.$ref = `${DEFINITION_LINK_PREFIX}${DefinitionName.ALPChart}`;
    if (chartAnnotationPath) {
        const viewAnnotation = chartAnnotationPath.split(UIVOCABULARYDOT)[1];
        const pathParts = annotations[viewAnnotation].fullyQualifiedName.split('@');
        chartView[SchemaTag.annotationPath] = `/${pathParts[0]}/@${pathParts[1]}`;
    }
    chartView[SchemaTag.artifactType] = ArtifactType.Manifest;
    chartView[SchemaTag.key] = 'primary';
    chartView[SchemaTag.keys] = [{ name: SchemaKeyName.key, value: 'primary' }];
}

/**
 * Adds the ALP views to the app-specific schema
 * @param {Definition} appSchema - app-specific JSON schema
 * @param {Array<Object>} viewsPaths - views' paths' section in the manifest
 * @param {EntityType} entityType - actual entity type of the page's entitySet
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param templateAnnotation - defaultTemplateAnnotationPath as registered in manifest
 * @param {SapUiAppPageV4} v4Page - actual page in the manifest
 */
function addALPViews(
    appSchema: Definition,
    viewsPaths: Array<Object>,
    entityType: EntityType,
    generateParameters: GenerateAppSchemaParameters,
    templateAnnotation: string,
    v4Page: v4.SapUiAppPageV4,
    tableDefinitionName: string
) {
    const annotations = entityType?.annotations.UI;
    //1. Chart View
    const chartAnnotationPath = viewsPaths?.[0]?.['primary']?.[0]?.[SchemaTag.annotationPath];
    const { targetAnnotation } = analyzeViewAnnotation(
        chartAnnotationPath,
        annotations,
        generateParameters.serviceAVT,
        'primary',
        undefined,
        generateParameters.logger
    );
    addALPChartView(appSchema, chartAnnotationPath, targetAnnotation, annotations);
    // Copy toolbar
    const schemaIdForActions = `Actions<ALPChart>`;
    (appSchema.definitions[DefinitionName.ViewChartToolBar] as Definition).properties.actions['$ref'] =
        DEFINITION_LINK_PREFIX + schemaIdForActions;

    defineActions(appSchema, schemaIdForActions, annotations, targetAnnotation, entityType);

    //2. Table View
    const { visualization: tableVisualization, targetAnnotation: target } = analyzeViewAnnotation(
        viewsPaths?.[0]?.['secondary']?.[0]?.[SchemaTag.annotationPath],
        annotations,
        generateParameters.serviceAVT,
        'secondary',
        undefined,
        generateParameters.logger
    );
    const tableDefinition = appSchema.definitions[tableDefinitionName] as Definition;
    if (tableVisualization || templateAnnotation) {
        tableDefinition.properties.toolBar['$ref'] = `${DEFINITION_LINK_PREFIX}${DefinitionName.ToolBarLR}`;
        // Switch from generic column to specific definition
        tableDefinition.properties['columns'] = {
            $ref: DEFINITION_LINK_PREFIX + DefinitionName.LineItems
        };
        tableDefinition.properties.annotationPath[SchemaTag.hidden] = true;
    } else {
        tableDefinition.properties = {};
    }
    const tableView = appSchema.definitions[DefinitionName.ALPTableView] as Definition;
    appSchema.definitions[DefinitionName.ALPTable] = JSON.parse(JSON.stringify(tableView));
    delete appSchema.definitions[DefinitionName.ALPTable][SchemaTag.isViewNode];
    tableView.additionalProperties = false;
    tableView.description = `Table (key: secondary)`;
    tableView.title = `Table`;
    if (tableVisualization) {
        delete tableView.properties;
        tableView.$ref = `${DEFINITION_LINK_PREFIX}${DefinitionName.ALPTable}`;
    } else if (!templateAnnotation) {
        tableView.properties = {};
    }
    tableView[SchemaTag.artifactType] = ArtifactType.Manifest;

    let annotationTerm: string, viewAnnotation: string;
    if (tableVisualization && tableVisualization.indexOf(FacetBase.LineItem) > -1) {
        annotationTerm = tableVisualization.split('.')[1];
        viewAnnotation = viewsPaths[0]['secondary'][0].annotationPath.split(UIVOCABULARYDOT)[1];
    } else if (templateAnnotation) {
        annotationTerm = viewAnnotation = templateAnnotation.split(UIVOCABULARYDOT)[1];
    }
    if (annotationTerm) {
        const dataForLineItem: LineItemDataForSchema = {
            appSchema,
            lineItemAnnotation: entityType?.annotations.UI[annotationTerm] as AnnotationTerm<LineItem>,
            entityType,
            oDataServiceAVT: generateParameters.serviceAVT,
            lineItemId: DefinitionName.LineItems
        };
        addLineItemsTypeToSchema(dataForLineItem, DefinitionName.ViewTableColumn);
        if (target) {
            appSchema.definitions[DefinitionName.ALPTable][SchemaTag.annotationPath] = target;
        }
        const pathParts = annotations[viewAnnotation].fullyQualifiedName.split('@');
        tableView[SchemaTag.annotationPath] = `/${pathParts[0]}/@${pathParts[1]}`;
        tableView[SchemaTag.key] = 'secondary';
        tableView[SchemaTag.keys] = [{ name: SchemaKeyName.key, value: 'secondary' }];
    }
    addCustomColumnDefinition(appSchema, v4Page, generateParameters.logger);
    // Table toolbar actions with custom actions
    const schemaIdForToolbarActions = `${DefinitionName.Actions}<${prepareRef(DefinitionName.LineItems)}>`;
    addCustomActionDefinition(appSchema, v4Page, generateParameters.logger, undefined, schemaIdForToolbarActions);
    addEnumForActionAnchor(appSchema, schemaIdForToolbarActions, DefinitionName.CustomActionPosition);
}

type ViewsParameters = {
    viewKey: string;
    appSchema: Definition;
    targetAnnotation: string;
    navPropName: string;
    targetEntityType: EntityType;
    title: string;
    entitySet?: string;
};

/**
 * Modifies the app schema, adds the right line chart settings
 * @param inputParameters - list of input parameters as collected by the calling instance
 * @param annotations - UI annotations of the given entityType
 * @returns the ID for the chart view in schema
 */
function addViewChartDefinitions(inputParameters: ViewsParameters, annotations): string {
    const idForChart = `${DefinitionName.LRChartView}<${inputParameters.viewKey}>`;
    inputParameters.appSchema.definitions[idForChart] = JSON.parse(
        JSON.stringify(inputParameters.appSchema.definitions[DefinitionName.LRChartView])
    );
    delete inputParameters.appSchema.definitions[idForChart][SchemaTag.isViewNode];
    inputParameters.appSchema.definitions[idForChart][SchemaTag.annotationPath] = inputParameters.targetAnnotation;
    if (inputParameters.navPropName) {
        inputParameters.appSchema.definitions[idForChart]['navigationProperty'] = inputParameters.navPropName;
    }
    //Copy toolbar
    const idForToolbar = `${DefinitionName.ViewChartToolBar}<${inputParameters.viewKey}>`;
    inputParameters.appSchema.definitions[idForToolbar] = JSON.parse(
        JSON.stringify(inputParameters.appSchema.definitions[DefinitionName.ViewChartToolBar])
    );
    inputParameters.appSchema.definitions[idForChart]['properties'].toolBar.$ref =
        `${DEFINITION_LINK_PREFIX}${idForToolbar}`;
    //Copy actions
    const schemaIdForActions = `${DefinitionName.Actions}<${inputParameters.viewKey}>`;
    defineActions(
        inputParameters.appSchema,
        schemaIdForActions,
        annotations,
        inputParameters.targetAnnotation,
        inputParameters.targetEntityType
    );
    inputParameters.appSchema.definitions[idForToolbar]['properties'][PropertyName.actions]['$ref'] =
        `${DEFINITION_LINK_PREFIX}${schemaIdForActions}`;

    return idForChart;
}

/**
 * Adds the definition of a chart view to the app schema
 * @param inputParameters - list of input parameters as collected by the calling instance
 * @param UIAnnotations - AVT UI annotations of the given entity type
 * @param appSchema - app schema
 * @param viewsDefinition - the views definition in app schema (MultiTableModeV4)
 * @param viewKey - view key
 * @param title - view title
 */
function addViewChart(
    inputParameters: ViewsParameters,
    UIAnnotations,
    appSchema: Definition,
    viewsDefinition: Definition,
    viewKey: string,
    title: string
) {
    const idForChart = addViewChartDefinitions(inputParameters, UIAnnotations);
    (appSchema.definitions[idForChart] as Definition).properties.annotationPath[SchemaTag.hidden] = true;
    (appSchema.definitions[idForChart] as Definition).properties.index[SchemaTag.hidden] = true;
    (appSchema.definitions[idForChart] as Definition).properties.entitySet[SchemaTag.hidden] = true;
    const titleOnHover = `Chart View (key: ${viewKey})`;
    viewsDefinition.properties[viewKey] = {
        $ref: `${DEFINITION_LINK_PREFIX}${idForChart}`,
        description: title ? title : titleOnHover,
        title: titleOnHover
    };
}

/**
 * Modifies the app schema, adds the right line item settings as derived from the visualization
 * @param inputParameters - list of input parameters as collected by the calling instance
 * @param viewsDefinition - the views definition in app schema (MultiTableModeV4)
 * @param visualization - visualization from the given (S)PV
 * @param templateAnnotation - defaultTemplateAnnotationPath as registered in manifest
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 */
function addViewLineItems(
    inputParameters: ViewsParameters,
    viewsDefinition: Definition,
    visualization: string,
    templateAnnotation: string,
    generateParameters: GenerateAppSchemaParameters
): void {
    const tableId = `${DefinitionName.LRTableView}<${inputParameters.viewKey}>`;
    const viewId = `${DefinitionName.LineItemsOfView}::${inputParameters.viewKey}`;
    const lrTableView = parseSchemaDefinition(
        DefinitionName.LRTableView,
        inputParameters.viewKey,
        inputParameters.appSchema
    );
    delete lrTableView[SchemaTag.isViewNode];
    lrTableView[SchemaTag.annotationPath] = inputParameters.targetAnnotation;
    if (inputParameters.navPropName) {
        lrTableView['navigationProperty'] = inputParameters.navPropName;
    }
    if (inputParameters.entitySet) {
        lrTableView['entitySet'] = inputParameters.entitySet;
    }
    //Copy toolbar
    addDefinitionRef(lrTableView.properties.toolBar as Definition, DefinitionName.ToolBar, viewId);
    const title = `Table View (key: ${inputParameters.viewKey})`;
    viewsDefinition.properties[inputParameters.viewKey] = {
        $ref: `${DEFINITION_LINK_PREFIX}${tableId}`,
        description: inputParameters.title || title,
        title
    };
    addDefinitionRef(lrTableView.properties.columns as Definition, viewId);
    lrTableView.properties.annotationPath[SchemaTag.hidden] = true;
    lrTableView.properties.index[SchemaTag.hidden] = true;
    lrTableView.properties.entitySet[SchemaTag.hidden] = true;
    const annotationTerm = visualization?.split('.')[1] || templateAnnotation.split(UIVOCABULARYDOT)[1];
    const dataForLineItem: LineItemDataForSchema = {
        appSchema: inputParameters.appSchema,
        lineItemAnnotation: inputParameters.targetEntityType?.annotations.UI[
            annotationTerm
        ] as AnnotationTerm<LineItem>,
        entityType: inputParameters.targetEntityType,
        oDataServiceAVT: generateParameters.serviceAVT,
        lineItemId: viewId
    };
    addLineItemsTypeToSchema(
        dataForLineItem,
        DefinitionName.ViewTableColumn,
        DefinitionName.ViewCustomColumn,
        DefinitionName.ViewCustomAction
    );
}

/**
 * Adds the schema tags to a given view
 * @param viewsDefinition - the views definition in app schema (MultiTableModeV4)
 * @param viewKey - key property of the view
 * @param namespace - target namespace for annotations
 * @param view - the given view
 */
function addTagsToView(viewsDefinition: Definition, viewKey: string, namespace: string, view: Object) {
    viewsDefinition.properties[viewKey][SchemaTag.isViewNode] = true;
    viewsDefinition.properties[viewKey][SchemaTag.annotationPath] = namespace
        ? `/${namespace}/@${view[SchemaTag.annotationPath]}`
        : undefined;
    viewsDefinition.properties[viewKey][SchemaTag.artifactType] = ArtifactType.Manifest;
    viewsDefinition.properties[viewKey][SchemaTag.key] = viewKey;
    viewsDefinition.properties[viewKey][SchemaTag.keys] = [{ name: SchemaKeyName.key, value: viewKey }];
}

/**
 * Adjusts the entity type if a different entity set is maintained for a view
 * @param {EntitySet} entitySet - actual entity set (AVT information)
 * @param view - the current view in manifest
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {EntityType} entityType - actual entity type of the page's entitySet
 * @returns the target entity type
 */
function getTargetEntityType(
    entitySet: EntitySet,
    view: Object,
    generateParameters: GenerateAppSchemaParameters,
    entityType: EntityType
): EntityType {
    let targetEntityType = entityType;
    let viewEntitySet = entitySet;
    if (view['entitySet'] && view['entitySet'] !== entitySet.name) {
        viewEntitySet = generateParameters.serviceAVT?.entitySets.find((es) => {
            return es.name === view['entitySet'];
        });
        targetEntityType = viewEntitySet.entityType;
    }
    return targetEntityType;
}

/**
 * Adds view custom extensions - custom column and custom action definitions
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {object} commonInputParameters - list of input parameters as collected by the calling instance
 * @param {string} visualization - visualization of the annotation
 * @param {SapUiAppPageV4} v4Page - the given page in manifest
 */
function addViewCustomExtensions(
    commonInputParameters: ViewsParameters,
    generateParameters: GenerateAppSchemaParameters,
    visualization: string,
    v4Page: v4.SapUiAppPageV4
) {
    const sectionId = `${DefinitionName.LineItemsOfView}::${commonInputParameters.viewKey}`;
    const sectionIdPrefix = commonInputParameters.entitySet ? `/${commonInputParameters.entitySet}/` : '';
    const sectionIdInManifest = `${sectionIdPrefix}@${UIAnnotationTerms.LineItem}${
        visualization.split(Visualization.LineItem)[1]
    }`;
    const viewColumnDefinition = parseSchemaDefinition(
        DefinitionName.ViewCustomColumn,
        sectionId,
        commonInputParameters.appSchema
    );
    addDefinitionRef(viewColumnDefinition.properties.position as Definition, DefinitionName.ViewPosition, sectionId);
    parseSchemaDefinition(DefinitionName.ViewPosition, sectionId, commonInputParameters.appSchema);
    addCustomColumnDefinition(
        commonInputParameters.appSchema,
        v4Page,
        generateParameters.logger,
        `${DefinitionName.ViewCustomColumn}<${sectionId}>`,
        sectionId,
        sectionIdInManifest
    );
    addFragmentEnumForAnchor(
        commonInputParameters.appSchema,
        sectionId,
        v4Page,
        `${DefinitionName.ViewPosition}<${sectionId}>` as DefinitionName,
        sectionIdInManifest
    );
    // Remove generic position
    if (commonInputParameters.appSchema.definitions.Position) {
        delete commonInputParameters.appSchema.definitions.Position;
        delete commonInputParameters.appSchema.definitions.TableCustomColumn;
    }
    // Table toolbar actions with custom actions
    const schemaIdForToolbarActions = `${DefinitionName.Actions}<${sectionId}>`;
    const customActionDefinitionKey = `${DefinitionName.ViewCustomAction}<${sectionId}>`;
    addCustomActionDefinition(
        commonInputParameters.appSchema,
        v4Page,
        generateParameters.logger,
        customActionDefinitionKey,
        schemaIdForToolbarActions,
        sectionIdInManifest
    );
    // Anchor selection
    parseSchemaDefinition(DefinitionName.ViewCustomActionPosition, sectionId, commonInputParameters.appSchema);
    const customActionPositionDefinitionKey = `${DefinitionName.ViewCustomActionPosition}<${sectionId}>`;
    const customActionDefinition = commonInputParameters.appSchema.definitions[customActionDefinitionKey] as Definition;
    (customActionDefinition.properties.position as Definition).$ref =
        `${DEFINITION_LINK_PREFIX}${customActionPositionDefinitionKey}`;
    addEnumForActionAnchor(
        commonInputParameters.appSchema,
        schemaIdForToolbarActions,
        customActionPositionDefinitionKey
    );
}

/**
 * Adds definition parts to a LR View (LineItem/Chart)
 * @param {string} templateAnnotation - defaultTemplateAnnotationPath as registered in manifest
 * @param {string} visualization - visualization of the annotation
 * @param viewsDefinition - the views definition in app schema (MultiTableModeV4)
 * @param {object} commonInputParameters - list of input parameters as collected by the calling instance
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {SapUiAppPageV4} v4Page - the given page in manifest
 * @param uiAnnotations - UI annotations of the given entityType
 * @paran view - the given view
 */
function addLRViewParts(
    visualization: string,
    viewsDefinition: Definition,
    commonInputParameters: ViewsParameters,
    generateParameters: GenerateAppSchemaParameters,
    v4Page: v4.SapUiAppPageV4,
    uiAnnotations: object,
    view: object
) {
    if (visualization?.indexOf(Visualization.LineItem) > -1) {
        //Add Line item view
        addViewLineItems(
            commonInputParameters,
            viewsDefinition,
            visualization,
            commonInputParameters.targetAnnotation,
            generateParameters
        );
        addViewCustomExtensions(commonInputParameters, generateParameters, visualization, v4Page);
    } else if (visualization?.indexOf('Chart') > -1) {
        //Add Chart view
        addViewChart(
            commonInputParameters,
            uiAnnotations,
            commonInputParameters.appSchema,
            viewsDefinition,
            commonInputParameters.viewKey,
            commonInputParameters.title
        );
    } else if (view['template']) {
        const titleOnHover = view['label'] || `Custom View (key: ${commonInputParameters.viewKey})`;
        viewsDefinition.properties[commonInputParameters.viewKey] = {
            description: commonInputParameters.title || titleOnHover,
            title: titleOnHover,
            // Default view - use table view reference as default
            $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.TableViewExtension}`
        };
    } else {
        const titleOnHover = `View (key: ${commonInputParameters.viewKey})`;
        viewsDefinition.properties[commonInputParameters.viewKey] = {
            description: commonInputParameters.title || titleOnHover,
            title: titleOnHover,
            // Default view - use table view reference as default
            $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.LRTableView}`
        };
    }
}

/**
 * Add list report views to the app-specific schema
 * @param {Definition} appSchema - app-specific JSON schema
 * @param {Array<Object>} viewsPaths - views' paths' section in the manifest
 * @param {EntityType} entityType - actual entity type of the page's entitySet
 * @param {EntitySet} entitySet - actual entity set (AVT information)
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {string} templateAnnotation - defaultTemplateAnnotationPath as registered in manifest
 * @param {SapUiAppPageV4} v4Page - the given page in manifest
 */
function addLRViews(
    appSchema: Definition,
    viewsPaths: Array<Object>,
    entityType: EntityType,
    entitySet: EntitySet,
    generateParameters: GenerateAppSchemaParameters,
    v4Page: v4.SapUiAppPageV4,
    tableDefinitionName: DefinitionName.Table | DefinitionName.ALPTableView
) {
    const tableDefinition = appSchema.definitions[tableDefinitionName] as Definition;
    tableDefinition.properties = {
        annotationPath: tableDefinition.properties.annotationPath,
        initialLoad: tableDefinition.properties.initialLoad,
        views: tableDefinition.properties.views
    };
    const viewsDefinition = appSchema.definitions.MultiTableModeV4 as Definition;
    for (const view of viewsPaths) {
        if (!view[SchemaTag.key]) {
            continue;
        }
        let navPropName: string = undefined;
        const targetEntityType = getTargetEntityType(entitySet, view, generateParameters, entityType);
        const uiAnnotations = targetEntityType?.annotations.UI;
        const { visualization, targetAnnotation, namespace, title } = analyzeViewAnnotation(
            view?.[SchemaTag.annotationPath],
            uiAnnotations,
            generateParameters.serviceAVT,
            view[SchemaTag.key],
            undefined,
            generateParameters.logger
        );
        if (view['entitySet']) {
            navPropName = entityType.navigationProperties.find((np) => {
                return np.targetTypeName === namespace;
            })?.name;
        }
        const viewKey = prepareRef(view[SchemaTag.key]);
        const commonInputParameters: ViewsParameters = {
            viewKey,
            appSchema,
            targetAnnotation,
            navPropName,
            targetEntityType,
            title,
            entitySet: view['entitySet']
        };
        addLRViewParts(
            visualization,
            viewsDefinition,
            commonInputParameters,
            generateParameters,
            v4Page,
            uiAnnotations,
            view
        );
        addTagsToView(viewsDefinition, viewKey, namespace, view);
    }
}

/**
 * Adapts the table definition in schema.
 * Evaluates the path depending on if defaultTemplateAnnotationPath or Selection-/PresentationVariant is present.
 * @param {SapUiAppPageV4} v4Page - the given page in manifest
 * @param {EntityType} entityType - the current entity type, as defined in AVT
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param appSchema - the app-specific JSON schema
 * @param alias - alias definition for the UI vocabulary
 */
function adaptTableDefinition(
    appSchema: Definition,
    entityType: EntityType,
    generateParameters: GenerateAppSchemaParameters,
    templateAnnotation: string,
    v4Page: v4.SapUiAppPageV4,
    alias: string,
    tableDefinitionName: DefinitionName.Table | DefinitionName.ALPTableView
) {
    //here: tableDefinitionName = DefinitionName.Table
    const tableDefinition = appSchema.definitions[tableDefinitionName] as Definition;
    const { visualization, targetAnnotation, namespace } = analyzeViewAnnotation(
        templateAnnotation,
        entityType?.annotations.UI,
        generateParameters.serviceAVT,
        undefined,
        Visualization.LineItem,
        generateParameters.logger
    );
    if (visualization) {
        const annotationTerm = visualization.split('.')[1];
        const annotationTermInSchema = `${DefinitionName.LineItems}OfSPV::${prepareRef(annotationTerm)}`;
        const spvTable = (appSchema.definitions[`${tableDefinitionName}SPV`] = JSON.parse(
            JSON.stringify(appSchema.definitions[tableDefinitionName])
        ) as Definition);
        delete spvTable[SchemaTag.isViewNode];
        addDefinitionRef(spvTable.properties.toolBar as Definition, DefinitionName.ToolBar, annotationTermInSchema);
        // Switch from generic column to specific definition
        spvTable[SchemaTag.annotationPath] = targetAnnotation;
        spvTable.properties.columns = {} as Definition;
        addDefinitionRef(spvTable.properties.columns, annotationTermInSchema);

        delete tableDefinition.properties;
        addDefinitionRef(tableDefinition, `${tableDefinitionName}SPV`);
        tableDefinition[SchemaTag.annotationPath] = `/${namespace}/@${templateAnnotation}`;
        const lineItemAnnotation = entityType?.annotations.UI[annotationTerm] as AnnotationTerm<LineItem>;
        if (!lineItemAnnotation) {
            log(generateParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOTARGET', { target: annotationTerm })
            });
        }
        const dataForLineItem: LineItemDataForSchema = {
            appSchema,
            lineItemAnnotation,
            entityType,
            oDataServiceAVT: generateParameters.serviceAVT,
            lineItemId: annotationTermInSchema
        };
        addLineItemsTypeToSchema(dataForLineItem);
        const annotationTermQualifier = annotationTerm.split('#')?.[1];
        const sectionIdInManifest = annotationTermQualifier
            ? `@${UIAnnotationTerms.LineItem}#${annotationTermQualifier}`
            : `@${UIAnnotationTerms.LineItem}`;
        addCustomColumnDefinition(
            appSchema,
            v4Page,
            generateParameters.logger,
            undefined,
            annotationTermInSchema,
            sectionIdInManifest
        );
        addFragmentEnumForAnchor(appSchema, annotationTermInSchema, v4Page, undefined, sectionIdInManifest);
        // Table toolbar actions with custom actions
        const schemaIdForToolbarActions = `${DefinitionName.Actions}<${annotationTermInSchema}>`;
        const customColumnDefinitionName = getCustomExtensionDefinitionName(
            appSchema,
            annotationTermInSchema,
            DefinitionName.CustomTableAction
        );
        addCustomActionDefinition(
            appSchema,
            v4Page,
            generateParameters.logger,
            getDefinitionKey(customColumnDefinitionName),
            schemaIdForToolbarActions,
            sectionIdInManifest
        );
        addEnumForActionAnchor(appSchema, schemaIdForToolbarActions, DefinitionName.CustomActionPosition);
    } else {
        tableDefinition.properties.toolBar['$ref'] = `${DEFINITION_LINK_PREFIX}${DefinitionName.ToolBarLR}`;
        // Switch from generic column to specific definition
        addDefinitionRef(tableDefinition.properties.columns as Definition, DefinitionName.LineItems);
        const lineItemAnnotation = entityType?.annotations?.[alias]?.LineItem;
        if (!lineItemAnnotation) {
            log(generateParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOLINEITEMS')
            });
        }
        const dataForLineItem: LineItemDataForSchema = {
            appSchema,
            lineItemAnnotation,
            entityType,
            oDataServiceAVT: generateParameters.serviceAVT,
            lineItemId: DefinitionName.LineItems
        };
        addLineItemsTypeToSchema(dataForLineItem);
        addCustomColumnDefinition(appSchema, v4Page, generateParameters.logger);
        addFragmentEnumForAnchor(appSchema, DefinitionName.LineItems, v4Page);
        if (lineItemAnnotation) {
            tableDefinition[SchemaTag.annotationPath] = createAnnotationPath(
                entityType?.fullyQualifiedName,
                lineItemAnnotation.term,
                lineItemAnnotation.qualifier
            );
        }

        // Table toolbar actions with custom actions
        const schemaIdForActions = `${DefinitionName.Actions}<${prepareRef(DefinitionName.LineItems)}>`;
        addCustomActionDefinition(appSchema, v4Page, generateParameters.logger, undefined, schemaIdForActions);
        addEnumForActionAnchor(appSchema, schemaIdForActions, DefinitionName.CustomActionPosition);
    }
}

/**
 * Checks for the default annotation path to use for LR table
 * @param {SapUiAppPageV4} v4Page - actual page in the manifest
 * @param {EntityType} entityType - actual entity type (AVT information)
 * @returns {string} annotation term
 */
function getDefaultAnnotationPath(
    v4Page: v4.SapUiAppPageV4,
    entityType: EntityType,
    templateType: TemplateType
): string | undefined {
    if (v4Page.options.settings?.views?.paths && templateType === TemplateType.ListReportObjectPageV4) {
        return;
    }
    if (v4Page.options.settings?.defaultTemplateAnnotationPath) {
        return v4Page.options.settings.defaultTemplateAnnotationPath;
    }
    if (templateType === TemplateType.ListReportObjectPageV4) {
        return (
            entityType?.annotations.UI?.SelectionPresentationVariant?.term ||
            entityType?.annotations.UI?.PresentationVariant?.term
        );
    }
}

/**
 * Add views to schema (LR or ALP)
 * @param {Definition} appSchema - app-specific JSON schema
 * @param {SapUiAppPageV4} v4Page - actual page in the manifest
 * @param {EntityType} entityType - actual entity type (AVT information)
 * @param {EntitySet} entitySet - actual entity set (AVT information)
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 */
function addTableAndViews(
    appSchema: Definition,
    v4Page: v4.SapUiAppPageV4,
    generateParameters: GenerateAppSchemaParameters,
    alias: string
): void {
    const viewsPaths = v4Page.options?.settings?.views?.paths;
    const entityType = generateParameters.entityType;
    const entitySet = generateParameters.entitySet;
    const defaultAnnotation = getDefaultAnnotationPath(v4Page, entityType, generateParameters.templateType);
    const tableDefinitionName = alignSchemaWithTemplateType(appSchema, generateParameters);
    // Add filter bar
    addFilterBar(entityType, appSchema, v4Page, alias);
    if (generateParameters.templateType === TemplateType.ListReportObjectPageV4) {
        if (viewsPaths) {
            addLRViews(appSchema, viewsPaths, entityType, entitySet, generateParameters, v4Page, tableDefinitionName);
        } else {
            if (!v4Page.options?.settings?.views) {
                (appSchema.definitions.MultiTableModeV4 as Definition).properties = {};
            }
            adaptTableDefinition(
                appSchema,
                entityType,
                generateParameters,
                defaultAnnotation,
                v4Page,
                alias,
                tableDefinitionName
            );
        }
        appSchema.properties[PropertyName.chart][SchemaTag.hidden] = true;
    } else if (generateParameters.templateType === TemplateType.AnalyticalListPageV4) {
        addALPViews(
            appSchema,
            viewsPaths,
            entityType,
            generateParameters,
            defaultAnnotation,
            v4Page,
            tableDefinitionName
        );
        addFragmentEnumForAnchor(appSchema, DefinitionName.LineItems, v4Page);
    }
}

/**
 * Adds header actions to schema (LR or ALP)
 * @param {Definition} appSchema - app-specific JSON schema
 * @param {SapUiAppPageV4} v4Page - actual page in the manifest
 * @param {ExtensionLogger} logger - Logger class for logging messages
 */
function addHeaderActions(appSchema: Definition, v4Page: v4.SapUiAppPageV4, logger: ExtensionLogger): void {
    // add header action definitions
    const headerActionsDefinitionKey = DefinitionName.HeaderActions;
    const headerActionsDefinition = appSchema.definitions[headerActionsDefinitionKey] as Definition;
    headerActionsDefinition[SchemaTag.isViewNode] = true;
    headerActionsDefinition.description = 'Actions';
    headerActionsDefinition.properties = {};

    // add custom action definitions
    const customActionDefinitionKey = DefinitionName.CustomHeaderAction;
    const customActionDefinition = appSchema.definitions[customActionDefinitionKey] as Definition;
    customActionDefinition[SchemaTag.isViewNode] = true;
    customActionDefinition.description = 'Custom Action';
    addHeaderFooterCustomActionDefinition(
        appSchema,
        v4Page,
        logger,
        customActionDefinitionKey,
        headerActionsDefinitionKey
    );
    const facetKey = DefinitionName.CustomHeaderActionPosition;
    parseSchemaDefinition(facetKey, facetKey, appSchema, false);

    // Anchor selection
    (customActionDefinition.properties.position as Definition).$ref = `${DEFINITION_LINK_PREFIX}${facetKey}`;
    addEnumForActionAnchor(appSchema, headerActionsDefinitionKey, facetKey);
}

/**
 * Generates an app specific schema for the FE V4 ListReport from the generic schema.
 * Generic types are replaced by information from the app specific annotations.
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param genericSchema  - generic JSON schema of an object page
 * @param {string} templateName - allows to distinguish list report from ALP
 * @param {string} contextPath - entity set (AVT object) that is relevant for the given page
 *
 * @returns {object} application-specific schema of the list report
 */
export function generateListReportSchemaV4(
    generateParameters: GenerateAppSchemaParameters,
    genericSchema: object,
    templateName: string,
    contextPath: string
): object {
    const appSchema: Definition = JSON.parse(JSON.stringify(genericSchema));
    const entityType = generateParameters.entityType;

    const alias = generateParameters.serviceAVT && findAlias(UIVOCABULARY, generateParameters.serviceAVT);
    appSchema.properties[PropertyName.defaultTemplateAnnotationPath][SchemaTag.hidden] = true;

    //enum for annotation path as part of QuickVariantSelection
    addEnumForSingleTabVariant(entityType, appSchema, DefinitionName.AnnotationPathAsObject);

    if (generateParameters.manifest) {
        //Find page in targets
        const pages = generateParameters.manifest[ManifestSection.ui5].routing
            .targets as unknown as v4.SapUiAppPageV4[];
        if (!pages) {
            log(generateParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOPAGES', { appId: generateParameters.manifest['sap.app']['id'] }),
                location: {
                    path: MANIFESTPATH,
                    range: [ManifestSection.generic]
                }
            });
            return appSchema;
        }
        const v4Page = findPageV4(pages, templateName, generateParameters.entitySet, contextPath);
        if (!v4Page) {
            return appSchema;
        }

        addHeaderActions(appSchema, v4Page, generateParameters.logger);
        addTableAndViews(appSchema, v4Page, generateParameters, alias);
    }
    delete appSchema.definitions.GenericColumns;
    delete appSchema.definitions.Action;
    updatePropertyIndices(appSchema, ROOT_PROPERTIES_ORDER);
    return appSchema;
}
