import type { PageConfig } from '../../common';
import type { ALPChartView, ALPTableView, DefaultPathType, FilterBar, Header, Table } from '../controls';

export enum VariantManagementTypeListReport {
    None = 'None',
    Control = 'Control',
    Page = 'Page'
}

export interface ListReportConfigV4 extends PageConfig {
    /**
     * variantManagement defines how the variant management of page personalizations is controlled.
     * - None - No variant management by default.
     * - Control - Individual personalizations for each control.
     */
    variantManagement?: VariantManagementTypeListReport;
    /**
     * Header
     */
    header?: Header;
    /**
     * Filter Bar
     */
    filterBar?: FilterBar;

    /**
     * Chart
     */
    chart?: ALPChartView;

    /**
     * Table
     */
    table?: Table | ALPTableView;

    /**
     * defaultTemplateAnnotationPath must be a reference to a SelectionPresentationVariant.
     * You use the SelectionPresentationVariant to configure the default visualizations and default filter values of the main content area.
     */
    defaultTemplateAnnotationPath?: string;

    /**
     * Allows you to set the initial view mode
     *
     * - primary: This property loads the app in chart-only view.
     * - secondary: This property loads the app in table-only view.
     * - both: This property loads the app in hybrid view.
     *
     * @descriptionSrcURL https://ui5.sap.com/sdk/#/topic/2a9df06673d34f72b238549d49da8bfb
     */
    defaultPath?: DefaultPathType;
}
