import {
    addLineItemsType,
    addSelectionFields,
    addStandardHeaderActions,
    addTableColumnExtensions,
    removeElementsFromEnum
} from './utils';
import { findAnalyticalListPage } from '../import';
import { addFieldsType, DEFINITION_LINK_PREFIX, updatePropertyIndices } from '../../common';
import {
    v2,
    ManifestSection,
    DefinitionName,
    PropertyName,
    LogSeverity,
    MANIFESTPATH,
    UIVOCABULARYALPHADOT,
    SchemaTag
} from '@sap/ux-specification-types';
import { addEnumForVariantPaths } from './listReport';
import type { Definition } from 'typescript-json-schema';
import type { GenerateAppSchemaParameters } from '@sap/ux-specification-types';
import i18next from 'i18next';
import { log } from '../../../extensionLogger';
import type { Chart, LineItem } from '@sap-ux/vocabularies-types/vocabularies/UI';
import type { EntityTypeAnnotations_UI } from '@sap-ux/vocabularies-types/vocabularies/UI_Edm';

const ROOT_PROPERTIES_ORDER: Array<keyof v2.AnalyticalListPageConfigV2> = [
    'filterBar',
    'keyPerformanceIndicators',
    'chart',
    'table',
    'footer'
];

/**
 * Returns LineItem and Chart vizualizations from UI annotations based of qualifier.
 * @param {v2.SapUiAppPageV2} v2Page - actual page in the manifest
 * @param {EntityTypeAnnotations_UI} uIAnnotations - annotations of UI namespace of the given entityType
 */
function getVisualizations(
    v2Page: v2.SapUiAppPageV2,
    uIAnnotations: EntityTypeAnnotations_UI
): { lineItem?: LineItem; chart?: Chart } {
    const qualifier = v2Page?.component?.settings?.qualifier;
    const selectionPresentationVariant = uIAnnotations[`SelectionPresentationVariant#${qualifier}`];
    const presentationVariant =
        selectionPresentationVariant !== undefined ? selectionPresentationVariant['PresentationVariant'] : undefined;
    const vizualizations = presentationVariant !== undefined ? presentationVariant['Visualizations'] : [];
    let lineItem;
    let chart;
    for (const i in vizualizations) {
        const vizualization = vizualizations[i];
        if (vizualization.value.includes('@com.sap.vocabularies.UI.v1.LineItem')) {
            const lineItemName = vizualization.value.replace(UIVOCABULARYALPHADOT, '');
            lineItem = uIAnnotations[lineItemName];
        } else if (vizualization.value.includes('@com.sap.vocabularies.UI.v1.Chart')) {
            const chartName = vizualization.value.replace(UIVOCABULARYALPHADOT, '');
            chart = uIAnnotations[chartName];
        }
    }
    return {
        lineItem,
        chart
    };
}

/**
 * Adds missing annotation path to chart definition.
 * @param {Definition} appSchema - app specific JSON schema
 * @param {Chart} chartAnnotation - chart annotation data
 */
function addChart(appSchema: Definition, chartAnnotation: Chart): void {
    const chartSettings = appSchema.definitions[DefinitionName.ChartSettings] as Definition;
    const pathParts = chartAnnotation.fullyQualifiedName.split('@');
    chartSettings[SchemaTag.annotationPath] = `/${pathParts[0]}/@${pathParts[1]}`;
}

/**
 * Generates an app specific schema out of the generic schema.
 * Generic types are replaced by information from the app specific annotations.
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param genericSchema - generic JSON schema of a list report
 * @param entityTypeName - the base entity type of the given page
 */
export function generateAnalyticalListReportSchemaV2(
    generateParameters: GenerateAppSchemaParameters,
    genericSchema: object
): object {
    const appSchema = JSON.parse(JSON.stringify(genericSchema)) as Definition;

    (appSchema.properties[PropertyName.table] as Definition).anyOf[0]['$ref'] =
        DEFINITION_LINK_PREFIX + 'ALPResponsiveTable<LineItems>';
    const tableDefinitionResponsive = JSON.parse(
        JSON.stringify(appSchema.definitions.ALPResponsiveTable)
    ) as Definition;
    tableDefinitionResponsive.properties['columns']['$ref'] = `${DEFINITION_LINK_PREFIX}${DefinitionName.LineItems}`;
    tableDefinitionResponsive.properties['toolBar']['$ref'] = `${DEFINITION_LINK_PREFIX}${DefinitionName.ToolBarLR}`;
    appSchema.definitions['ALPResponsiveTable<LineItems>'] = tableDefinitionResponsive;

    appSchema.properties[PropertyName.table]['anyOf'][1]['$ref'] = `${DEFINITION_LINK_PREFIX}ALPGridTable<LineItems>`;
    const tableDefinitionGrid = JSON.parse(JSON.stringify(appSchema['definitions']['ALPGridTable'])) as Definition;
    tableDefinitionGrid.properties.columns['$ref'] = `${DEFINITION_LINK_PREFIX}${DefinitionName.LineItems}`;
    tableDefinitionGrid.properties.toolBar['$ref'] = `${DEFINITION_LINK_PREFIX}${DefinitionName.ToolBarLR}`;
    appSchema.definitions['ALPGridTable<LineItems>'] = tableDefinitionGrid;

    appSchema.properties[PropertyName.table]['anyOf'][2]['$ref'] =
        `${DEFINITION_LINK_PREFIX}ALPAnalyticalTable<LineItems>`;
    const tableDefinitionAnalytical = JSON.parse(
        JSON.stringify(appSchema.definitions['ALPAnalyticalTable'])
    ) as Definition;
    tableDefinitionAnalytical.properties.columns['$ref'] = `${DEFINITION_LINK_PREFIX}${DefinitionName.LineItems}`;
    tableDefinitionAnalytical.properties.toolBar['$ref'] = `${DEFINITION_LINK_PREFIX}${DefinitionName.ToolBarLR}`;
    appSchema.definitions['ALPAnalyticalTable<LineItems>'] = tableDefinitionAnalytical;

    // Remove Custom Column non existing extensions - TreeTableColumnsExtension does not exist in ALP
    removeElementsFromEnum(appSchema.definitions.TableColumnExtensionTypeV2['enum'], [
        v2.TableColumnExtensionTypeV2.TreeTableColumnsExtension
    ]);

    const entityType = generateParameters.entitySet?.entityType;
    if (!entityType) {
        log(generateParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOENTITYTYPE4SET', { entitySetName: generateParameters.entitySet?.name })
        });
    } else {
        // handle header actions
        addStandardHeaderActions(appSchema);
        const pageKeys = [];
        const v2Page = findAnalyticalListPage(
            generateParameters.manifest[ManifestSection.generic].pages as v2.SapUiAppPagesV2,
            pageKeys,
            generateParameters.logger
        );

        const annotations = entityType?.annotations?.UI;
        const { lineItem, chart } = getVisualizations(v2Page, annotations);
        const lineItemAnnotation = lineItem || annotations.LineItem;
        const chartAnnotation = chart || annotations.Chart;

        //handle line items
        addLineItemsType(appSchema, lineItemAnnotation, entityType);

        // handle chart item
        if (chartAnnotation) {
            addChart(appSchema, chartAnnotation);
        }

        //handle filter fields
        const selectionFieldAnnotation = entityType?.annotations?.UI?.SelectionFields;
        addSelectionFields(appSchema, selectionFieldAnnotation, entityType);

        //add fields for createWithParameterDialog
        addFieldsType(appSchema, entityType);

        if (!v2Page) {
            log(generateParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('NOLR'),
                location: {
                    path: MANIFESTPATH,
                    range: [ManifestSection.generic]
                }
            });
            return appSchema;
        }

        addTableColumnExtensions(
            appSchema,
            entityType,
            v2Page,
            generateParameters.manifest,
            generateParameters.fragments
        );
        addEnumForVariantPaths(entityType, generateParameters.entitySet, appSchema, generateParameters.serviceAVT);

        // Delete generic definitions that have been replaced
        delete appSchema.definitions.ALPGridTable;
        delete appSchema.definitions.ALPResponsiveTable;
        delete appSchema.definitions.ALPAnalyticalTable;
        delete appSchema.definitions.GenericColumns;
        delete appSchema.definitions.TableColumnAction;
        delete appSchema.definitions.ToolBar;
        delete appSchema.definitions.GenericFooter;
        delete appSchema.definitions.Actions;
        delete appSchema.definitions.FieldPath;
    }
    updatePropertyIndices(appSchema, ROOT_PROPERTIES_ORDER);
    return appSchema;
}
